import * as actionTypes from '../actions/actionTypes';

export const periodActiveReducer = (state = actionTypes.JUDGED_PERIOD_ACTIVE.MONTH, action)=> {
    switch (action.type) {
        case actionTypes.SET_JUDGED_PERIOD:
            return action.active;
        default:
            return state;
    }
};


