import {React, dialog, PureRenderMixin,$} from '../../common/Util';

const Map = React.createClass({
    mixins: [PureRenderMixin],
    componentDidMount:function(){

    },
    componentWillReceiveProps: function (nextProps) {
        //浮层
        if(!this.props.bodyDatassssssssss.equals(nextProps.bodyDatassssssssss)){
            this.props.drawFloatDiv(nextProps.bodyDatassssssssss);
        }
    },
    render: function () {
        return (
            <div style={{width:"auto"}} className="map">
                <div className="top_bar">宏观警情</div>
                <div id="allmap"></div>
                <div className="map_list"></div>
            </div>
        );
    }
});

export default Map;


