/**
 * Created by Administrator on 2016/8/2.  <input type="button" value="取消关注" className="cancel" />
 */
import {React, PureRenderMixin} from '../../common/Util';

const PeopleSingleView = React.createClass({
    mixins: [PureRenderMixin],
    render: function () {
        let infor = this.props.infor;
        let URL = location.origin + ":18080/picture/photo?cache=1&sfzh=";
        if (infor.get('sore') == "") {
            return (
                <div className="single_people hand" onClick={this.props.click} data-names={infor.get('name')} data-logid="107" data-logcontent={infor.get('name')}>
                    <img src={URL + infor.get('sfzh')} data-logid="107" data-logcontent={infor.get('name')} onError={e=> {
                            e.target.src = './../../images/t01a7c98e9c657197c1.jpg';
                        }}/>
                    <div className="People_NAME" data-logid="107" data-logcontent={infor.get('name')} title={infor.get('name')}>{infor.get('name')}</div>
                    <div className="People_ID" data-logid="107" data-logcontent={infor.get('name')} title={infor.get('sfzh')}>{infor.get('sfzh')}</div>
                    <div className="People_NUM" data-logid="107" data-logcontent={infor.get('name')}><span data-logid="107" data-logcontent={infor.get('name')}>{this.props.person}</span></div>
                </div>
            );
        }
        else if(infor.get('sore') >= 90) {
            return (
                <div className="single_people hand" onClick={this.props.click} data-names={infor.get('name')} data-logid="107" data-logcontent={infor.get('name')}>
                    <img src={URL + infor.get('sfzh')} data-logid="107" data-logcontent={infor.get('name')} onError={e=> {
                        e.target.src = '../../images/t01a7c98e9c657197c1.jpg';
                    }}/>
                    <div className="People_NAME" data-logid="107" data-logcontent={infor.get('name')} title={infor.get('name')}>{infor.get('name')}</div>
                    <div className="People_ID" data-logid="107" data-logcontent={infor.get('name')} title={infor.get('sfzh')}>{infor.get('sfzh')}</div>
                    <div className="People_NUM hot" data-logid="107" data-logcontent={infor.get('name')}><span data-logid="107" data-logcontent={infor.get('name')}>{this.props.person}</span>&nbsp;&nbsp;<span>{infor.get('sore')}</span>分</div>
                </div>
            );
        }
        else{
            return (
                <div className="single_people hand" onClick={this.props.click} data-names={infor.get('name')} data-logid="107" data-logcontent={infor.get('name')}>
                    <img src={URL + infor.get('sfzh')} data-logid="107" data-logcontent={infor.get('name')} onError={e=> {
                            e.target.src = './../../images/t01a7c98e9c657197c1.jpg';
                        }}/>
                    <div className="People_NAME" data-logid="107" data-logcontent={infor.get('name')} title={infor.get('name')}>{infor.get('name')}</div>
                    <div className="People_ID" data-logid="107" data-logcontent={infor.get('name')} title={infor.get('sfzh')}>{infor.get('sfzh')}</div>
                    <div className="People_NUM" data-logid="107" data-logcontent={infor.get('name')}><span data-logid="107" data-logcontent={infor.get('name')}>{this.props.person}</span>&nbsp;&nbsp;<span>{infor.get('sore')}</span>分</div>
                </div>
            );
        }
    }
});

export default PeopleSingleView;


