import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const fileInfo = (state = Immutable.fromJS([]), action)=> {
    switch (action.type) {
        case actionTypes.SET_PHONE_FILEINFO:
            return Immutable.fromJS(action.fileInfo);
        case actionTypes.SET_PHONE_REMOVE:
            let newState = state.get('listObj').splice(action.index,1)
            return state;
        default:
            return state;
    }
};


