import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const cooPhoto = (state = Immutable.fromJS([]), action)=> {
    switch (action.type) {
        case actionTypes.SET_PHOTO_DATA:
            return state.merge(Immutable.fromJS(action.photodata));
        default:
            return state;
    }
};


