import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const retrievalListReducer = (state = Immutable.fromJS([]), action)=> {
    switch (action.type) {
        case actionTypes.SET_Search_Result_List:
            return Immutable.fromJS(action.resultData);
        default:
            return state;
    }
};


