import {Tools, Immutable} from "../../common/Util";
import * as actionTypes from '../actions/actionTypes';

export default class Methods {
    /**
     * 多过滤处理列表数据
     */
    static getListByFilter(list,
                           rightTitleActive,
                           periodActive,
                           periodDateFilter,
                           bjhmStatisticFilter,
                           dfhmStatisticFilter,
                           xqhStatisticFilter,
                           jzhStatisticFilter,
                           bjhmAnalyseFilter,
                           chooseTypeAnalyseFilter,
                           chooseTimeAnalyseFilter,
                           chooseTimeSpanAnalyseFilter,
                           dfhmAnalyseFilter,
                           checkedToTop,
                           kssjSort,
                           thscSort,
                           merge,
                           thschSort,
                           thcshSort,
                           thcshSensitiveSort,
                           bjhmMergeAnalyseFilter) {
        let aHighlight = Immutable.fromJS([]);
        let aUnHighlight = Immutable.fromJS([]);

        if (merge) {
            return this.mergeAfterDataHandler(list, thschSort, thcshSort, thcshSensitiveSort);
        } else {
            //未合并前
            for (var i = 0, len = list.size; i < len; i++) {
                let oTicket = list.get(i);
                let sBjhm = oTicket.get('bjhm');
                let sDfhm = oTicket.get('dfhm');
                let sXqh = oTicket.get('ePHONE_xqh');
                let sJzh = oTicket.get('ePHONE_jzh');

                let bPeriodDate = false;

                if (rightTitleActive == actionTypes.JUDGED_RIGHT_TITLE_ACTIVE.STATISTICS) {
                    let bStatisticBjhm = false;
                    let bStatisticDfhm = false;
                    let bStatisticXqh = false;
                    let bStatisticJzh = false;

                    //统计面板激活时
                    if (!periodDateFilter.get('filter') && !bjhmStatisticFilter && !dfhmStatisticFilter && !xqhStatisticFilter && !jzhStatisticFilter) {
                        //全部为空时,设置其中一个filter为false即可
                        bPeriodDate = false;
                    } else {
                        //周期过滤
                        if (periodDateFilter.get('filter')) {
                            let aData = periodDateFilter.get('aData');
                            if (aData.includes(oTicket.get('id'))) {
                                bPeriodDate = true;
                            } else {
                                bPeriodDate = false;
                            }
                        } else {
                            bPeriodDate = true;
                        }

                        if (!bPeriodDate) {
                            aUnHighlight = aUnHighlight.push(oTicket.set('highlight', false));
                            continue;
                        }

                        //本地号码过滤
                        if (bjhmStatisticFilter) {
                            bStatisticBjhm = bjhmStatisticFilter == sBjhm ? true : false;
                        } else {
                            bStatisticBjhm = true;
                        }

                        if (!bStatisticBjhm) {
                            aUnHighlight = aUnHighlight.push(oTicket.set('highlight', false));
                            continue;
                        }


                        //对端号码
                        if (dfhmStatisticFilter) {
                            bStatisticDfhm = dfhmStatisticFilter == sDfhm ? true : false;
                        } else {
                            bStatisticDfhm = true;
                        }

                        if (!bStatisticDfhm) {
                            aUnHighlight = aUnHighlight.push(oTicket.set('highlight', false));
                            continue;
                        }

                        //小区号
                        if (xqhStatisticFilter) {
                            bStatisticXqh = xqhStatisticFilter == sXqh ? true : false;
                        } else {
                            bStatisticXqh = true;
                        }

                        if (!bStatisticXqh) {
                            aUnHighlight = aUnHighlight.push(oTicket.set('highlight', false));
                            continue;
                        }

                        //基站号
                        if (jzhStatisticFilter) {
                            bStatisticJzh = jzhStatisticFilter == sJzh ? true : false;
                        } else {
                            bStatisticJzh = true;
                        }

                        if (!bStatisticJzh) {
                            aUnHighlight = aUnHighlight.push(oTicket.set('highlight', false));
                            continue;
                        }
                    }

                    if (bPeriodDate && bStatisticBjhm && bStatisticDfhm && bStatisticXqh && bStatisticJzh) {
                        aHighlight = aHighlight.push(oTicket.set('highlight', true));
                    } else {
                        aUnHighlight = aUnHighlight.push(oTicket.set('highlight', false));
                    }
                }



                else {
                    let bAnalyseDfhm = false;
                    if (!periodDateFilter.get('filter') && !dfhmAnalyseFilter.get('filter')) {
                        //全部为空
                        bAnalyseDfhm = false;
                    }
                    else {
                        //周期过滤
                        if (periodDateFilter.get('filter')) {
                            let aData = periodDateFilter.get('aData');
                            if (aData.includes(oTicket.get('id'))) {
                                bPeriodDate = true;
                            } else {
                                bPeriodDate = false;
                            }
                        } else {
                            bPeriodDate = true;
                        }

                        if (!bPeriodDate) {
                            aUnHighlight = aUnHighlight.push(oTicket.set('highlight', false));
                            continue;
                        }

                        //对端号码过滤
                        let aBjhmData = dfhmAnalyseFilter.get('aData');
                        if (aBjhmData.size > 0) {
                            if (aBjhmData.includes(oTicket.get('id'))) {
                                bAnalyseDfhm = true;
                            } else {
                                bAnalyseDfhm = false;
                            }
                        } else {
                            bAnalyseDfhm = true;
                        }

                        if (!bAnalyseDfhm) {
                            aUnHighlight = aUnHighlight.push(oTicket.set('highlight', false));
                            continue;
                        }
                    }

                    if (bPeriodDate && bAnalyseDfhm) {
                        aHighlight = aHighlight.push(oTicket.set('highlight', true));
                    }
                    else {
                        aUnHighlight = aUnHighlight.push(oTicket.set('highlight', false));
                    }
                }

            }

            //高亮置顶

            //排序
            let fnComparator = (item1, item2, sort, field) => {
                if (sort == actionTypes.JUDGED_TABLE_SORT.ASC) {
                    if (field == 'thkssj') {
                        return Number(item1.get('thkssj')) - Number(item2.get('thkssj'));
                    } else {
                        return Number(item1.get('ePHONE_thsc')) - Number(item2.get('ePHONE_thsc'));
                    }
                } else {
                    if (field == 'thkssj') {
                        return Number(item2.get('thkssj')) - Number(item1.get('thkssj'));
                    } else {
                        return Number(item2.get('ePHONE_thsc')) - Number(item1.get('ePHONE_thsc'));
                    }
                }
            };
            //根据checked分隔并按指定字段排序
            let fnSortByCheckedAndField = (list, field, sort)=> {
                let oMap = aUnHighlight.groupBy(item=>item.get('checked'));
                let aChecked = oMap.get(true);
                if (aChecked) {
                    aChecked = aChecked.sort((item1, item2)=> {
                        return fnComparator(item1, item2, sort, field);
                    });
                } else {
                    aChecked = Immutable.List();
                }

                let aUnChecked = oMap.get(false);
                if (aUnChecked) {
                    aUnChecked.sort((item1, item2)=> {
                        return fnComparator(item1, item2, sort, field);
                    });
                } else {
                    aUnChecked = Immutable.List();
                }

                return aChecked.concat(aUnChecked);
            };

            //checked的时候不触发排序
            //按开始时间排序
            if (kssjSort != actionTypes.JUDGED_TABLE_SORT.DEFAULT) {
                if (aHighlight.size > 0) {
                    aHighlight = aHighlight.sort((item1, item2)=> {
                        return fnComparator(item1, item2, kssjSort, 'thkssj');
                    });
                }

                if (aUnHighlight.size > 0) {
                    // checkedToTop为false后不触发重新排序
                    aUnHighlight = aUnHighlight.sort((item1, item2)=> {
                        return fnComparator(item1, item2, kssjSort, 'thkssj');
                    });

                    if (checkedToTop) {
                        aUnHighlight = fnSortByCheckedAndField(aUnHighlight, 'thkssj', kssjSort);
                    }
                }
            }

            //按通话时长排序
            if (thscSort != actionTypes.JUDGED_TABLE_SORT.DEFAULT) {
                if (aHighlight.size > 0) {
                    aHighlight = aHighlight.sort(function (item1, item2) {
                        return fnComparator(item1, item2, thscSort, 'ePHONE_thsc');
                    });
                }

                if (aUnHighlight.size > 0) {
                    // checkedToTop为false后不触发重新排序
                    aUnHighlight = aUnHighlight.sort((item1, item2)=> {
                        return fnComparator(item1, item2, thscSort, 'ePHONE_thsc');
                    });

                    if (checkedToTop) {
                        aUnHighlight = fnSortByCheckedAndField(aUnHighlight, 'ePHONE_thsc', thscSort);
                    }
                }
            }

            if (kssjSort == actionTypes.JUDGED_TABLE_SORT.DEFAULT && thscSort == actionTypes.JUDGED_TABLE_SORT.DEFAULT) {
                if (aUnHighlight.size > 0) {
                    if (checkedToTop) {
                        aUnHighlight = fnSortByCheckedAndField(aUnHighlight, 'thkssj', thscSort);
                    }
                }
            }
        }

        return Immutable.fromJS({
            aHighlight: aHighlight,
            aUnHighlight: aUnHighlight
        });
    }

    static mergeBeforeDataHandler() {

    }

    /**
     * 合并后对列表数据的处理
     * @param list
     * @returns {Map<K, V>|*|Map<string, V>}
     */
    static mergeAfterDataHandler(list, thschSort, thcshSort, thcshSensitiveSort) {
        //合并后
        //根据本机号码分组
        let oMapData = this.getDataByField(list, 'bjhm');
        //根据对端号码分组
        let oMapByDfhm = Immutable.Map();
        oMapData.mapData.forEach((aItems, bjhm)=> {
            let aDfhm = Immutable.List();
            //本机号码后面对应一个数组结构如下：
            // 本机号码：[{field:'对端号码',count：通话次数，thsc：通话时长，ids：[数据所有id组成的List]，data:[本机号码-对端号码的实体们]}]
            aItems.groupBy(item=>item.get('dfhm')).forEach((arr, dfhm)=> {
                //总通话时长
                let nThsc = 0;
                //checked个数
                let nUnChecked = 0;
                //所有的id
                let ids = Immutable.List();
                //敏感时间段通话数据
                let aSensitive = Immutable.List();

                arr.forEach(oTicket=> {
                    let ePHONE_thsc = oTicket.get('ePHONE_thsc');
                    nThsc += Number(ePHONE_thsc);
                    ids = ids.push(oTicket.get('id'));

                    if (!oTicket.get('checked')) {
                        nUnChecked++;
                    }

                    //敏感时间段数据过滤
                    //22:00-6:00
                    let startTime = 22;
                    let endTime = 6;
                    //通话开始时间
                    var sThkssj = new Date(Number(oTicket.get('thkssj')));
                    var hour = sThkssj.getHours();

                    if (hour >= startTime && hour < 24 || hour >= 0 && hour <= endTime) {
                        aSensitive = aSensitive.push(oTicket);
                    }
                });
                aDfhm = aDfhm.push(Immutable.fromJS({
                    dfhm: dfhm,
                    count: arr.size,
                    ids: ids,
                    thsc: nThsc,
                    data: arr,
                    unChecked: nUnChecked,
                    sensitiveData: aSensitive,
                    sensitiveCount: aSensitive.size
                }));
            });

            //通话次数排序
            if (thschSort != actionTypes.JUDGED_TABLE_SORT.DEFAULT) {
                if (thschSort == actionTypes.JUDGED_TABLE_SORT.ASC) {
                    aDfhm = aDfhm.sort((item1, item2)=> {
                        return item1.get('thsc') - item2.get('thsc');
                    });
                } else {
                    aDfhm = aDfhm.sort((item1, item2)=> {
                        return item2.get('thsc') - item1.get('thsc');
                    });
                }
            }

            //通话时长排序
            if (thcshSort != actionTypes.JUDGED_TABLE_SORT.DEFAULT) {
                if (thcshSort == actionTypes.JUDGED_TABLE_SORT.ASC) {
                    aDfhm = aDfhm.sort((item1, item2)=> {
                        return item1.get('count') - item2.get('count');
                    });
                } else {
                    aDfhm = aDfhm.sort((item1, item2)=> {
                        return item2.get('count') - item1.get('count');
                    });
                }
            }

            //敏感时间段通话次数排序
            if (thcshSensitiveSort != actionTypes.JUDGED_TABLE_SORT.DEFAULT) {
                if (thcshSensitiveSort == actionTypes.JUDGED_TABLE_SORT.ASC) {
                    aDfhm = aDfhm.sort((item1, item2)=> {
                        return item1.get('sensitiveCount') - item2.get('sensitiveCount');
                    });
                } else {
                    aDfhm = aDfhm.sort((item1, item2)=> {
                        return item2.get('sensitiveCount') - item1.get('sensitiveCount');
                    });
                }
            }

            oMapByDfhm = oMapByDfhm.set(bjhm, aDfhm);
        });

        return oMapByDfhm;
    }

    /**
     * 获得所有的状态处理函数
     * @param state
     * @returns {*[]}
     */
    static getStateArg(state) {
        //列表数据
        const judgedList = state => {
            return state.judged_list;
        };

        //右侧面板激活状态
        const rightTitleActive = state=> {
            return state.judged_rightTitleActive;
        };

        //下面周期选项
        const periodActive = state=> {
            return state.judged_periodActive;
        };

        //下面周期filter选项
        const periodDateFilter = state=> {
            return state.judged_periodDate;
        };

        //右侧统计面板filter选项
        //本地号码
        const bjhmStatisticFilter = state=> {
            return state.judged_statisticBjhm;
        };

        //对端号码
        const dfhmStatisticFilter = state=> {
            return state.judged_statisticDfhm;
        };

        //小区号
        const xqhStatisticFilter = state=> {
            return state.judged_statisticXqh;
        };

        //基站号
        const jzhStatisticFilter = state=> {
            return state.judged_statisticJzh;
        };

        //右侧分析面板
        //本机号码
        const bjhmAnalyseFilter = state=> {
            return state.judged_analyseBjhm;
        };

        //合并后本机号码
        const bjhmMergeAnalyseFilter = state=> {
            return state.judged_analyseBjhmMerge;
        };

        //筛选类型
        const chooseTypeAnalyseFilter = state=> {
            return state.judged_analyseChooseType;
        };

        //筛选时间
        const chooseTimeAnalyseFilter = state=> {
            return state.judged_analyseChooseTime;
        };

        //筛选时间间隔
        const chooseTimeSpanAnalyseFilter = state=> {
            return state.judged_analyseChooseTimeSpan;
        };

        //对端号码
        const dfhmAnalyseFilter = state=> {
            return state.judged_analyseDfhm;
        };

        //选中置顶排序
        const checkedToTop = state=> {
            return state.judged_checkedToTop;
        };

        //开始时间排序
        const kssjSort = state=> {
            return state.judged_kssjSort;
        };

        //通话次数排序
        const thcshSort = state=> {
            return state.judged_thcshSort;
        };

        //敏感时间段通话次数排序
        const thcshSensitiveSort = state=> {
            return state.judged_thcshSensitiveSort;
        };

        //通话时长排序
        const thschSort = state=> {
            return state.judged_thschSort;
        };

        //通话时长排序
        const thscSort = state=> {
            return state.judged_thscSort;
        };

        //步骤
        const step = state=> {
            return state.judged_step;
        };

        //合并功能
        const merge = state => {
            return state.judged_merge;
        };

        //平行坐标图上线选择对端号码
        const dfhmByPolyline = state=> {
            return state.judged_dfhmByPolyline;
        };

        //列表显示数量
        const listShowCount = state=> {
            return state.judged_listShowCount;
        };

        return [
            judgedList,
            rightTitleActive,
            periodActive,
            periodDateFilter,
            bjhmStatisticFilter,
            dfhmStatisticFilter,
            xqhStatisticFilter,
            jzhStatisticFilter,
            bjhmAnalyseFilter,
            chooseTypeAnalyseFilter,
            chooseTimeAnalyseFilter,
            chooseTimeSpanAnalyseFilter,
            dfhmAnalyseFilter,
            checkedToTop,
            kssjSort,
            thscSort,
            merge,
            step,
            thschSort,
            thcshSort,
            thcshSensitiveSort,
            bjhmMergeAnalyseFilter,
            dfhmByPolyline,
            listShowCount
        ];
    }

    /**
     * 根据字段进行分组并得到相应的出现频次最大值
     * @param field
     * @returns {{mapData: (Seq.Keyed<G, Iterable<K, V>>|*), maxFre: number}}
     */
    static getDataByField(list, field) {
        let oDataMap = list.groupBy(oTicket=> {
            return oTicket.get(field);
        });

        let aData = Immutable.List();

        oDataMap.forEach((item, key)=> {
            aData = aData.push(Immutable.fromJS({field: key, count: item.size}));
        });

        aData = aData.sort((item1, item2)=> {
            return item2.get('count') - item1.get('count');
        });

        return {mapData: oDataMap, aData: aData, maxFre: aData.size > 0 ? aData.first().get('count') : 0};
    }
}


