import { React,dialog,PureRenderMixin,$} from '../../common/Util';


const SnapshotComponentKeywordSearch = React.createClass({
    mixins:[PureRenderMixin],
    componentDidMount: function () {

    },
    render: function () {
        return (
            <div className="keysearch cooperation_main_select_key">
                <input style={{lineHeight:"22px"}} data-logid="120" data-logcontent="协作-快照-关键字检索" name="keyword" className="searchbox-ipt" onKeyUp={this.props.keyOnChange} onChange={this.props.changes} placeholder="请输入关键字查询" type="text"/>
                <a className="searchbox-btn" href="javascript:;" data-logid="120" data-logcontent="协作-快照-关键字检索" onClick={this.props.keyOnChange}></a>
            </div>
        );
    }
});

export default SnapshotComponentKeywordSearch;



