import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const cooAuthorityReducer = (state = "", action)=> {
    switch (action.type) {
        case actionTypes.GET_AUTHORITY:
            return state = action.Authority;
        default:
            return state;
    }
};


