import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const cooNum = (state = Immutable.fromJS([]), action)=> {
    switch (action.type) {
        case actionTypes.SET_COO_NUM:
            return Immutable.fromJS(action.cooNum);
        default:
            return state;
    }
};


