import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const homeListReducer6 = (state = Immutable.fromJS([]), action)=> {
    switch (action.type) {
        case actionTypes.SET_HOME_PERSON:
            return Immutable.fromJS(action.Data_person);
        default:
            return state;
    }
};


