import * as actionTypes from '../actions/actionTypes';

export const stepReducer = (state = -1, action)=> {
    switch (action.type) {
        case actionTypes.SET_JUDGED_STEP_FORWARD:
            return state + 1;
        case actionTypes.SET_JUDGED_STEP_BACK:
            return state - 1;
        case actionTypes.SET_JUDGED_STEP_RESET:
            return -1;
        default:
            return state;
    }
};


