import {React,PureRenderMixin,Tools} from '../../common/Util';
const CaseSingleView = React.createClass({
    mixins:[PureRenderMixin],
    render: function () {
        let click = this.props.silingOnClick;
        let url = "tuning/services/core/snapshot/getThumbnail?sid=";
        let infor = this.props.infor;
        let judId = infor.get('snapshotId');
        return (
            <tr style={{height:"146px",lineHeight:"146px",textAlign:"center"}} data-ids={infor.get('judgeThemeId')} data-names={infor.get('judgeThemeName')} data-descriptions={infor.get('snapshotDescription')} className="hand" onClick={click} data-logid="105" data-logcontent={infor.get('judgeThemeName')}>
                <td style={{height:"146px",width:"30%"}} data-logid="105" data-logcontent={infor.get('judgeThemeName')} data-ids={infor.get('judgeThemeId')} data-names={infor.get('judgeThemeName')} data-descriptions={infor.get('snapshotDescription')}><div style={{height:"100%",width:"100%",float:"left",textAlign:"center",verticalAlign:"middle"}} data-ids={infor.get('judgeThemeId')} data-names={infor.get('judgeThemeName')} data-descriptions={infor.get('snapshotDescription')}><img style={{maxWidth:"80%",maxHeight:"128px"}} data-ids={infor.get('judgeThemeId')} data-names={infor.get('judgeThemeName')} data-descriptions={infor.get('snapshotDescription')} src={url+judId} /></div></td>
                <td  style={{width:"25%"}} data-logid="105" data-logcontent={infor.get('judgeThemeName')} data-ids={infor.get('judgeThemeId')} data-names={infor.get('judgeThemeName')} data-descriptions={infor.get('snapshotDescription')} title={infor.get('judgeThemeName')}>{infor.get('judgeThemeName')}</td>
                <td  style={{width:"15%"}} data-logid="105" data-logcontent={infor.get('judgeThemeName')} data-ids={infor.get('judgeThemeId')} data-names={infor.get('judgeThemeName')} data-descriptions={infor.get('snapshotDescription')} title={infor.get('consoleUserName')}>{infor.get('consoleUserName')}</td>
                <td  style={{width:"30%"}} data-logid="105" data-logcontent={infor.get('judgeThemeName')} data-ids={infor.get('judgeThemeId')} data-names={infor.get('judgeThemeName')} data-descriptions={infor.get('snapshotDescription')} title={Tools.convertDate2FormatStr(new Date(infor.get('snapshotTimeStamp')), 'yyyy-MM-dd hh:mm:ss')}>{Tools.convertDate2FormatStr(new Date(infor.get('snapshotTimeStamp')), 'yyyy-MM-dd hh:mm:ss')}</td>
            </tr>
        );
    }
});

export default CaseSingleView;


