import { React,dialog,PureRenderMixin,$} from '../../common/Util';
import SnapshotComponentSelect from './SnapshotComponentSelect';
import SnapshotComponentDaterange from './SnapshotComponentDaterange';
import SnapshotComponentKeywordSearch from './SnapshotComponentKeywordSearch';

const SnapshotSelect = React.createClass({
    mixins:[PureRenderMixin],
    componentDidMount: function () {

    },
    render: function () {
        return (
            <div className="cooperation_main_select clearfix">
                <h1 className="theme">研判主题：<span style={{color:"#707070"}}>{this.props.judgeThemeName}</span></h1>
                <SnapshotComponentKeywordSearch
                    keyOnChange={this.props.keyOnChange}
                    changes={this.props.changes}
                />
                <SnapshotComponentDaterange
                    timeOnChange={this.props.timeOnChange}
                />
                <SnapshotComponentSelect
                    themeOnChange={this.props.themeOnChange}
                />
            </div>
        );
    }
});

export default SnapshotSelect;


