import * as actionTypes from '../actions/actionTypes';

export const checkedToTopReducer = (state = false, action)=> {
    switch (action.type) {
        case actionTypes.SET_JUDGED_CHECKED_TOTOP:
            return action.status;
        default:
            return state;
    }
};


