import {React} from '../../common/Util';

class TabsControl extends React.Component{

    check_tittle_index(index){
        return index===this.props.tabValue ? "Tab_tittle active" : "Tab_tittle";
    }
    check_item_index(index){
        return index===this.props.tabValue ? "Tab_item show" : "Tab_item";
    }
    cooparationMainOffsetHeight(){
        //document.getElementsByClassName("tabChange_s")[0].style.height = document.getElementsByClassName("cooperation_main_view")[0].offsetHeight+17+"px";
        if(document.getElementsByClassName("cooperation_main_view")[0].offsetHeight<800){
            document.getElementsByClassName("tabChange_s")[0].style.height = document.body.clientHeight+"px";
        }else{
            document.getElementsByClassName("tabChange_s")[0].style.height = document.getElementsByClassName("cooperation_main_view")[0].offsetHeight+11+"px";
        }
    }
    componentDidMount(){
        scopaConfig.pages.cooperation.cooparationMainOffsetHeight=this.cooparationMainOffsetHeight;
    }
    render(){

        return(
            <div className="cooparationMain">
                {/*动态生成Tab导航*/}
                <div className="tabChange_s">
                    <ul>
                        { React.Children.map( this.props.children , (element,index) => {
                            return(
                                <li onClick={ () => { this.props.tabOnclickChange(index) } } className={ this.check_tittle_index(index) }><div className={element.props.icon}></div><span>{ element.props.name}</span></li>
                            );
                        }) }
                    </ul>
                </div>
                {/*Tab内容区域*/}
                <div className="mainContent component">
                    {React.Children.map(this.props.children,(element,index)=>{
                        return(
                            <div className={ this.check_item_index(index) }>{ element }</div>
                        );
                    })}
                </div>
            </div>
        );
    }
}

export default TabsControl;


