import {React,PureRenderMixin} from '../../common/Util';

const OnlyLinkSingleView = React.createClass({
    mixins:[PureRenderMixin],
    render: function () {
        let oinforType = this.props.oinforType;
        let oinforUrl = this.props.oinforUrl;
        return (
            <a className="hand" target="_blank" title={oinforType}  data-logid="108" data-logcontent="搜索内容" href={oinforUrl}>{oinforType}</a>
        );
    }
});

export default OnlyLinkSingleView;


