import {React} from '../../common/Util';

const NavView = React.createClass({
    render: function () {
        let active = this.props.active;
        let nav  = null;

        if (active == "graph") {
            nav =
                <ul role="nav" className="list-unstyled list-inline modeltype">
                    <li className="active"><a href="#/judged/graph">图析模型</a></li>
                    <li><a href="#/judged/map" className="hassplit">轨迹模型</a></li>
                    <li><a href="#/judged/ticket">话单模型</a></li>
                </ul>;
        } else if (active == "map") {
            nav =
                <ul role="nav" className="list-unstyled list-inline modeltype">
                    <li><a href="#/judged/graph">图析模型</a></li>
                    <li className="active"><a href="#/judged/map">轨迹模型</a></li>
                    <li><a href="#/judged/ticket">话单模型</a></li>
                </ul>;
        } else {
            nav =
                <ul role="nav" className="list-unstyled list-inline modeltype">
                    <li><a href="#/judged/graph" className="hassplit">图析模型</a></li>
                    <li><a href="#/judged/map">轨迹模型</a></li>
                    <li className="active"><a href="#/judged/ticket">话单模型</a></li>
                </ul>;
        }

        return (
            <div className="breadnav">{nav}</div>
        );
    }
});

export default NavView;


