import { React,dialog,PureRenderMixin } from '../../common/Util';

const CooperationMainTar = React.createClass({
    mixins:[PureRenderMixin],
    componentDidMount: function () {

    },
    render: function () {
        let followOnclick = this.props.followOnclick;
        return (
            <div className="cooperation_main_tar">
                <div className="cooperation_main_tar_common" onClick={followOnclick}><span>协同作战</span></div>
                <div className="cooperation_main_tar_alone" ><span>个人研判</span></div>
            </div>
        );
    }
});

export default CooperationMainTar;


