import {React, connect, PureRenderMixin,createSelector,dialog} from '../../common/Util';
import actionCreator from '../actions/actionCreator';

import TabsControl from './TabsControl';
import SnapshotView from './SnapshotView';
import FileInforView from './FileInforView';
import CooperationMainTar from './CooperationMainTar';
import CooperationMainSelect from './CooperationMainSelect';
import CooperationMainDataTable from './CooperationMainDataTable';
import CooperationMainPage from './CooperationMainPage';
import CooperationAlert from './CooperationAlert';

const MainCooperationView = React.createClass({
    mixins: [PureRenderMixin],
    getInitialState: function () {
        //初始化数据
        return {
            type: "1",
            keyword: "",
            startTime: "",
            endTime: "",
            userId: "ID1",
            user_id:"haiyangshi",
            judgeThemeId:"",
            judgeThemeName:"",
            pageNo: "1",
            pageSize: "4",
            count: true,
            edit:"",
            alertType:"",
            data_index:0,
            photoName:"",
            photoid:'',
            tUserId:"",
            ids:""
        }
    },
    componentDidMount: function () {
        //触发得到所有数据
        let {dispatch} = this.props;
        let {type,keyword,startTime,endTime,userId,pageNo,pageSize}=this.state;

        dispatch(actionCreator.getCooCount(userId,type,keyword,startTime,endTime,pageNo,pageSize));
        dispatch(actionCreator.getCooNum("",type,keyword,startTime,endTime,"",""));
        dispatch(actionCreator.getAllUser());
    },
    themeOnChange: function (e) {
        //协作——主题下拉列表选择
        let {dispatch} = this.props;
        let {type,keyword,startTime,endTime,userId,pageNo,pageSize} = this.state;
        type = e.target.value
        this.setState({type});
        dispatch(actionCreator.getCooCount(userId,type,keyword,startTime,endTime,pageNo,pageSize));
        dispatch(actionCreator.getCooNum("",type,keyword,startTime,endTime,"",""));

    },
    keyOnChange: function (e) {
        //协作——关键字筛选
        let {dispatch} = this.props;
        let {type,keyword,startTime,endTime,userId,pageNo,pageSize} = this.state;
        keyword = e.target.value
        this.setState({keyword});
        if(e.currentTarget.tagName === 'INPUT' && event.keyCode === 13){
            dispatch(actionCreator.getCooCount(userId,type,keyword,startTime,endTime,pageNo,pageSize));
            dispatch(actionCreator.getCooNum("",type,keyword,startTime,endTime,"",""));
        }else{
            dispatch(actionCreator.getCooCount(userId,type,keyword,startTime,endTime,pageNo,pageSize));
            dispatch(actionCreator.getCooNum("",type,keyword,startTime,endTime,"",""));
        }

    },
    timeOnChange: function (start, end) {
        //协作——时间筛选
        let {dispatch} = this.props;
        let {type,keyword,startTime,endTime,userId,pageNo,pageSize}=this.state;
        startTime = start.format('YYYY-MM-DD HH:MM:SS');
        endTime = end.format('YYYY-MM-DD HH:MM:SS');
        this.setState({startTime});
        this.setState({endTime});
        dispatch(actionCreator.getCooCount(userId,type,keyword,startTime,endTime,pageNo,pageSize));
        dispatch(actionCreator.getCooNum("",type,keyword,startTime,endTime,"",""));
    },
    followOnclickTrue: function () {
        //快照跳转协作
        let count = this.state.count;
        count = true;
        this.setState({count});
    },
    followOnclickFalse: function (e) {
        //协作跳转快照
        let count = this.state.count;
        count = false;
        this.setState({count});

        let judgeThemeId = this.state.judgeThemeId;

        judgeThemeId = e.target.dataset.ids;
        this.setState({judgeThemeId});

        //调用相关资料接口
        let {dispatch} = this.props;
        dispatch(actionCreator.getPhoneFileInfo( String(judgeThemeId)));


        let judgeThemeName = this.state.judgeThemeName;
        judgeThemeName = e.target.title;
        this.setState({judgeThemeName});

    },
    editOnclick: function (e) {
        //点击编辑显示弹窗
        if(e.target.id){
            let edit = this.state.edit;
            edit = e.target.id;
            this.setState({edit});
        }
        let data_index = this.state.data_index;
        data_index = e.target.dataset.indexs;
        this.setState({data_index});

        let ids = this.state.ids;
        ids = e.target.dataset.ids;
        this.setState({ids});

        let alertType = this.state.alertType;
        alertType = e.target.title;
        this.setState({alertType});
        document.getElementsByClassName("cooperation_alert")[0].style.display="block";
    },
    editFailOnclick:function(){
        //取消编辑
        document.getElementsByClassName("cooperation_alert")[0].style.display="none";
        document.getElementsByClassName("cooperation_describe")[0].value="";
        document.getElementsByClassName("cooperation_describe1")[0].value="";
    },
    editDoneOnclick:function(){
        //确认编辑
        let {dispatch} = this.props;
        let ids = this.state.ids;
        let theme = document.getElementsByClassName("cooperation_describe")[0].value.trim();
        let content = document.getElementsByClassName("cooperation_describe1")[0].value.trim();
        let index = this.state.data_index;
        dispatch(actionCreator.updataCooperation(theme,index,content));

        dispatch(actionCreator.getEditAttention(ids,theme,content));
        document.getElementsByClassName("cooperation_alert")[0].style.display="none";
        document.getElementsByClassName("cooperation_describe")[0].value="";
        document.getElementsByClassName("cooperation_describe1")[0].value="";

    },
    concernOnclick:function(e,user_id,judgeThemeId){
        //关注切换
        let {dispatch} = this.props;
        if(e.target.dataset.choise=="true"){
            if(e.target.className == ""){
                e.target.style.background="url('./../../images/quxiao.png') no-repeat";
                e.target.className="quxiao";
                dispatch(actionCreator.getReomveAttention(user_id,judgeThemeId));
            }else if(e.target.className == "guanzhu"){
                e.target.style.background="url('./../../images/quxiao.png') no-repeat";
                e.target.className="quxiao";
                dispatch(actionCreator.getReomveAttention(user_id,judgeThemeId));
            }else{
                e.target.style.background="url('./../../images/guanzhu.png') no-repeat 0px 2px";
                e.target.className="guanzhu";
                dispatch(actionCreator.getAddAttention(user_id,judgeThemeId));
            }
        }else{
            if(e.target.className == ""){
                e.target.style.background="url('./../../images/guanzhu.png') no-repeat 0px 2px";
                e.target.className="guanzhu";
                dispatch(actionCreator.getAddAttention(user_id,judgeThemeId));
            }else if(e.target.className == "guanzhu"){
                e.target.style.background="url('./../../images/quxiao.png') no-repeat";
                e.target.className="quxiao";
                dispatch(actionCreator.getReomveAttention(user_id,judgeThemeId));
            }else{
                e.target.style.background="url('./../../images/guanzhu.png') no-repeat 0px 2px";
                e.target.className="guanzhu";
                dispatch(actionCreator.getAddAttention(user_id,judgeThemeId));
            }
        }
    },
    giveOnclick:function(e){
        //推送弹窗
        let photoName = this.state.photoName;
        photoName = e.target.id;
        this.setState({photoName});

        let photoid = this.state.photoid;
        photoid = e.target.dataset.photoid;
        this.setState({photoid});

        let alertType = this.state.alertType;
        alertType = e.target.title;
        this.setState({alertType});
        document.getElementsByClassName("cooperation_alert")[0].style.display="block";
    },
    giveFailOnclick:function(e){
        //取消推送弹窗
        document.getElementsByClassName("cooperation_alert")[0].style.display="none";
        document.getElementsByClassName("cooperation_describe")[0].value="";
    },
    gitUserId:function(e){
        //获取快照id
        let tUserId = this.state.tUserId;
        tUserId = e.target.value
        this.setState({tUserId});
    },
    giveDoneOnclick:function(e){
        //推送确定
        let {dispatch} = this.props;
        let content = document.getElementsByClassName("cooperation_describe1")[0].value;

        let photoid = this.state.photoid;
        let userId=this.state.userId;
        let tUserId=this.state.tUserId;

        dispatch(actionCreator.getTuisongAttention(userId,tUserId,photoid,content));

        document.getElementsByClassName("cooperation_alert")[0].style.display="none";
        document.getElementsByClassName("cooperation_describe")[0].value="";

    },
    phoneUserPage:function(page){
        //协作-协同作战-相关资料翻页操作
        let {dispatch} = this.props;
        let judgeThemeId = this.state.judgeThemeId;

        dispatch(actionCreator.getPhoneFileInfo(judgeThemeId,page));
    },
    onDateApply:function(start, end, label){
        //协作-协同作战-相关资料日期操作
        let {dispatch} = this.props;
        let judgeThemeId = this.state.judgeThemeId;

        dispatch(actionCreator.getPhoneFileInfo(judgeThemeId,'',start.format('YYYY-MM-DD HH:mm:ss'),end.format('YYYY-MM-DD HH:mm:ss') ));
    },
    onhandleSearch:function(event,keyword){
        //协作-协同作战-相关资料搜索操作
        let {dispatch} = this.props;
        let judgeThemeId = this.state.judgeThemeId;

        if(event.currentTarget.tagName === 'A'){
            dispatch(actionCreator.getPhoneFileInfo(judgeThemeId,'','','',keyword ));
        }
        if(event.currentTarget.tagName === 'INPUT' && event.keyCode === 13){
            dispatch(actionCreator.getPhoneFileInfo(judgeThemeId,'','','',keyword ));
        }
    },
    phoneRemove:function(num,index){
        //协作-协同作战-相关资料删除操作
        let {dispatch} = this.props;
        dialog.confirm({
            cancel: function(){

            },
            ok:function(){
                dispatch(actionCreator.getPhoneRemove(num,index));
                dispatch(actionCreator.getPhoneFileInfo('1'));
            }
        }).showModal();
    },
    pageClick:function(page){
        //快照翻页
        let {dispatch} = this.props;
        let {type,keyword,startTime,endTime,userId,pageNo,pageSize}=this.state;
        pageNo = page;
        this.setState({pageNo});
        dispatch(actionCreator.getCooCount(userId,type,keyword,startTime,endTime,pageNo,pageSize));
    },
    render: function () {
        let {cooperation_fileInfo,cooContent,cooNum,cooperation_AllUser} = this.props;
        let {userId,pageNo,count,edit,alertType,judgeThemeId,judgeThemeName,user_id,photoName}=this.state;
        if (count) {
            return (
                <div className="cooperation_main_view component">
                    <CooperationMainTar
                        followOnclick={this.followOnclickTrue}
                    />
                    <div className="cooperation_main">
                        <CooperationMainSelect
                            themeOnChange={this.themeOnChange}
                            timeOnChange={this.timeOnChange}
                            keyOnChange={this.keyOnChange}
                            cooNum={cooNum}
                            pageNo={pageNo}
                        />
                        <CooperationMainDataTable
                            pageNo={pageNo}
                            userId={userId}
                            cooContent={cooContent}
                            followOnclick={this.followOnclickFalse}
                            editOnclick={this.editOnclick}
                            concernOnclick={this.concernOnclick}
                        />
                        <CooperationMainPage
                            cooNum={cooNum}
                            pageClick={this.pageClick}
                        />
                    </div>
                    <CooperationAlert
                        edit={edit}
                        editFailOnclick={this.editFailOnclick}
                        editDoneOnclick={this.editDoneOnclick}
                    />
                </div>
            );
        } else {
            return (
                <div className="cooperation_main_view">
                    <CooperationMainTar
                        followOnclick={this.followOnclickTrue}
                    />
                    <div className="clearfix">
                        <TabsControl>
                            <div name="快照" icon="sprite-i_snapshot">
                                <SnapshotView
                                    user_id={user_id}
                                    judgeThemeName={judgeThemeName}
                                    judgeThemeId={judgeThemeId}
                                    giveOnclick={this.giveOnclick}
                                />
                            </div>
                            <div name="相关资料" icon="sprite-i_relatedInfor">
                                {/*话单数据——点击跳转至（协作二级-相关资料），通过judgeThemeId获取话单序列号，然后传给参数*/}
                                <FileInforView
                                    judgeThemeName={judgeThemeName}
                                    judgeThemeId={judgeThemeId}
                                    fileInfoAjaxResult={cooperation_fileInfo}
                                    onUserPage={this.phoneUserPage}
                                    onPhoneRemove={this.phoneRemove}
                                    onDateApply={this.onDateApply}
                                    onhandleSearch={this.onhandleSearch}
                                />
                            </div>
                        </TabsControl>
                    </div>
                    <CooperationAlert
                        giveFailOnclick={this.giveFailOnclick}
                        alertType={alertType}
                        photoName={photoName}
                        cooperation_AllUser={cooperation_AllUser}
                        giveDoneOnclick={this.giveDoneOnclick}
                        gitUserId={this.gitUserId}
                    />
                </div>
            );
        }
    }
});

const cooperation_fileInfo = state => {
    return state.cooperation_fileInfo;
};
const cooContent = state => {
    return state.cooContent;
};
const cooNum = state => {
    return state.cooNum;
};
const cooperation_AllUser = state => {
    return state.cooperation_AllUser;
};

const getCooperation = createSelector([cooperation_fileInfo,cooContent,cooNum,cooperation_AllUser], (cooperation_fileInfo,cooContent,cooNum,cooperation_AllUser)=> {
    return {
        cooperation_fileInfo:cooperation_fileInfo,
        cooContent:cooContent,
        cooNum:cooNum,
        cooperation_AllUser:cooperation_AllUser
    }
});

export default connect(getCooperation)(MainCooperationView);


