import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const homeListReducer4 = (state = Immutable.fromJS([]), action)=> {
    switch (action.type) {
        case actionTypes.SET_HOME_PEOPLE:
            return Immutable.fromJS(action.bodyDatassssssss);
        default:
            return state;
    }
};


