import { $,React,dialog,PureRenderMixin } from '../../common/Util';
import CooperationMainSingleView  from './CooperationMaindDataTableSingleView';

const CooperationMainDataTable = React.createClass({
    mixins:[PureRenderMixin],
    getInitialState: function () {
        return {
            concern:true
        }
    },
    componentDidMount: function () {

    },
    render: function () {

        let otr = [];
        let cooperationMain = this.props.cooperationMain;
        let cooContent = this.props.cooContent;
        cooContent.forEach((item1, index1)=> {
            var result;
            $.ajax({
                url:'tuning/services/core/userAttention/existUserAttention?userId='+this.props.userId+"&judgeThemeId="+item1.get("id"),
                async:false
            }).done(oData=> {
                result = String(oData.obj);
            })
            var photoNum;
            $.ajax({
                url:'tuning/services/core/snapshot/getCountByJudgeThemeId?userId='+this.props.userId+'&judgeThemeId='+item1.get("id"),
                async:false
            }).done(oData=> {
                photoNum = String(oData.obj);

            })
            var callNum;
            $.ajax({
                url:'tuning/services/core/media/getPhoneFileList?jtid='+item1.get("id"),
                async:false
            }).done(oData=> {
                callNum = String(oData.total);
            })
            otr.push(
                <CooperationMainSingleView
                    key={'single'+index1}
                    id={index1}
                    editOnclick={this.props.editOnclick}
                    followOnclick={this.props.followOnclick}
                    userId={this.props.userId}
                    keys={index1}
                    infor1={item1}
                    crdata={result}
                    photoNum={photoNum}
                    callNum={callNum}
                    concernOnclick={this.props.concernOnclick}
                    pageNo={this.props.pageNo}
                    followOnclickFile={this.props.followOnclickFile}
                />
            )
        })

        return (
            <div className="cooperation_main_dataTable">
                <table  className="table table-striped table-bordered table-hover dataTable no-footer">
                    <thead>
                    <tr>
                        <td className="column-ellipsis column"  style={{width:'8%'}} aria-label="序列">序列</td>
                        <td className="column-ellipsis column"  style={{width:'18%'}} aria-label="综合研判图" >综合研判图</td>
                        <td className="column-ellipsis column"  style={{width:'16%'}} aria-label="研判主题" >研判主题</td>
                        <td className="column-ellipsis column"  style={{width:'18%'}} aria-label="主题描述" >主题描述</td>
                        <td className="column-ellipsis column"  style={{width:'10%'}} aria-label="时间" >时间</td>
                        <td className="column-ellipsis column"  style={{width:'8%'}} aria-label="作者" >作者</td>
                        <td className="column-ellipsis column"  style={{width:'14%'}} aria-label="协作数据" >协作数据</td>
                        <td className="column-ellipsis column"  style={{width:'8%'}} aria-label="操作" >操作</td>
                    </tr>
                    </thead>
                    <tbody>
                        {otr}
                    </tbody>
                </table>
            </div>
        );
    }
});

export default CooperationMainDataTable;


