import {Tools,React,PureRenderMixin,$} from '../../common/Util';
const CooperationMainSingleView = React.createClass({
    mixins:[PureRenderMixin],
    componentDidMount: function () {

    },

    render: function () {
        let infor1 = this.props.infor1;
        let crdata = this.props.crdata;
        let userId=this.props.userId;
        let photoNum= this.props.photoNum;
        const URL = "tuning/services/core/snapshot/getLatestThumbnail?userId="+userId+"&judgeThemeId=";
        let callNum= this.props.callNum;
        let pageNo=(this.props.pageNo-1)*4;
        let thisId = infor1.get("id");
        let author = infor1.get("author");
        let time = new Date().getMilliseconds();
        if(userId==author){
                return (
                    <tr style={{height:"174px"}}>
                        <td onClick={this.props.followOnclick} data-logid="111" data-logcontent="协作跳转快照" data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>{this.props.keys+1+pageNo}</td>
                        <td onClick={this.props.followOnclick} data-logid="111" data-logcontent="协作跳转快照" data-titles={infor1.get("name")} data-descriptions={infor1.get("description")} data-ids={thisId} style={{position:"relative",cursor:"pointer",width:"380px"}} className="hand"><img className="hand" data-titles={infor1.get("name")} data-ids={thisId} data-descriptions={infor1.get("description")} style={{maxWidth:"294px",maxHeight:"134px",cursor:"pointer",margin:"20px"}} src={URL+thisId+"&t="+time} onError={e=> {e.target.src = '../../images/t01ebaa63b994ac0c94.gif'}}/><span id="MY" data-titles={infor1.get("name")} data-ids={thisId} data-descriptions={infor1.get("description")}></span></td>
                        <td onClick={this.props.followOnclick} data-logid="111" data-logcontent="协作跳转快照" data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>{infor1.get("name")}</td>
                        <td onClick={this.props.followOnclick} data-logid="111" data-logcontent="协作跳转快照" data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>{infor1.get("description")}</td>
                        <td onClick={this.props.followOnclick} data-logid="111" data-logcontent="协作跳转快照" data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>{Tools.convertDate2FormatStr(new Date(infor1.get("time")), 'yyyy-MM-dd hh:mm:ss')}</td>
                        <td onClick={this.props.followOnclick} data-logid="111" data-logcontent="协作跳转快照" data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>{infor1.get("author")}</td>
                        <td>
                            <div style={{cursor:"pointer"}} onClick={this.props.followOnclick} data-logid="111" data-logcontent="协作跳转快照" data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>快照数量：{photoNum}</div><div style={{cursor:"pointer"}} onClick={this.props.followOnclickFile} data-logid="128" data-logcontent="协作跳转相关资料" data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>话单数量：{callNum}</div>
                        </td>
                        <td><span className="hand" style={{cursor:"pointer",background:"url('./../../images/bianji.png') no-repeat"}} data-titles="0" data-ids={thisId}  data-logid="110" data-logcontent="编辑" id={infor1.get("name")} data-der={infor1.get("description")} data-indexs={this.props.keys} onClick={this.props.editOnclick} title="编辑"></span></td>
                    </tr>
                );
        }else{
            if(crdata=="false"){
                return (
                    <tr style={{height:"174px"}}>
                        <td data-logid="111" data-logcontent="协作跳转快照" onClick={this.props.followOnclick} data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>{this.props.keys+1+pageNo}</td>
                        <td data-logid="111" data-logcontent="协作跳转快照" className="hand"  onClick={this.props.followOnclick} data-ids={thisId} data-descriptions={infor1.get("description")}  data-titles={infor1.get("name")} style={{position:"relative",cursor:"pointer",width:"380px"}}><img className="hand"  data-ids={thisId}  data-titles={infor1.get("name")} data-descriptions={infor1.get("description")} style={{maxWidth:"294px",maxHeight:"134px",cursor:"pointer",margin:"20px"}} src={URL+thisId+"&t="+time} onError={e=> {e.target.src = '../../images/t01ebaa63b994ac0c94.gif'}}/></td>
                        <td data-logid="111" data-logcontent="协作跳转快照" onClick={this.props.followOnclick} data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>{infor1.get("name")}</td>
                        <td data-logid="111" data-logcontent="协作跳转快照" onClick={this.props.followOnclick} data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>{infor1.get("description")}</td>
                        <td data-logid="111" data-logcontent="协作跳转快照" onClick={this.props.followOnclick} data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>{Tools.convertDate2FormatStr(new Date(infor1.get("time")), 'yyyy-MM-dd hh:mm:ss')}</td>
                        <td data-logid="111" data-logcontent="协作跳转快照" onClick={this.props.followOnclick} data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>{infor1.get("author")}</td>
                        <td>
                            <div style={{cursor:"pointer"}}  data-logid="111" data-logcontent="协作跳转快照" onClick={this.props.followOnclick} data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>快照数量：{photoNum}</div><div style={{cursor:"pointer"}} onClick={this.props.followOnclickFile} data-ids={thisId} data-descriptions={infor1.get("description")}  data-logid="128" data-logcontent="协作跳转相关资料" data-titles={infor1.get("name")}>话单数量：{callNum}</div>
                        </td>
                        <td><span data-choise={crdata}  data-logid="127" data-logcontent="取消关注研判主题" className="hand" title="取消关注" style={{background:"url('./../../images/quxiao.png') no-repeat",cursor:"pointer"}} id={infor1.get("id")} onClick={e=>this.props.concernOnclick( e, userId,infor1.get("id"))}></span><span className="hand" style={{cursor:"pointer"}} data-ids={thisId}  data-der={infor1.get("description")} data-titles="0" id={infor1.get("name")} data-indexs={this.props.keys}  data-logid="110" data-logcontent="编辑" onClick={this.props.editOnclick} title="编辑"></span></td>
                    </tr>
                );
            }else{
                return (
                    <tr style={{height:"174px"}}>
                        <td data-logid="111" data-logcontent="协作跳转快照" onClick={this.props.followOnclick} data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>{this.props.keys+1+pageNo}</td>
                        <td data-logid="111" data-logcontent="协作跳转快照" className="hand" onClick={this.props.followOnclick} data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")} style={{position:"relative",cursor:"pointer",width:"380px"}}><img className="hand" data-ids={thisId} data-titles={infor1.get("name")} data-descriptions={infor1.get("description")} style={{maxWidth:"294px",maxHeight:"134px",cursor:"pointer",margin:"20px"}} src={URL+thisId+"&t="+time} onError={e=> {e.target.src = '../../images/t01ebaa63b994ac0c94.gif'}}/></td>
                        <td data-logid="111" data-logcontent="协作跳转快照" onClick={this.props.followOnclick} data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>{infor1.get("name")}</td>
                        <td data-logid="111" data-logcontent="协作跳转快照" onClick={this.props.followOnclick} data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>{infor1.get("description")}</td>
                        <td data-logid="111" data-logcontent="协作跳转快照" onClick={this.props.followOnclick} data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>{Tools.convertDate2FormatStr(new Date(infor1.get("time")), 'yyyy-MM-dd hh:mm:ss')}</td>
                        <td data-logid="111" data-logcontent="协作跳转快照" onClick={this.props.followOnclick} data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>{infor1.get("author")}</td>
                        <td>
                            <div data-logid="111" data-logcontent="协作跳转快照" style={{cursor:"pointer"}} onClick={this.props.followOnclick} data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>快照数量：{photoNum}</div><div style={{cursor:"pointer"}} onClick={this.props.followOnclickFile}  data-logid="128" data-logcontent="协作跳转相关资料" data-ids={thisId} data-descriptions={infor1.get("description")} data-titles={infor1.get("name")}>话单数量：{callNum}</div>
                        </td>
                        <td><span className="hand"  data-logid="126" data-logcontent="关注研判主题" title="关注" data-choise={crdata} style={{background:"url('./../../images/guanzhu.png') no-repeat 0px 2px",cursor:"pointer"}} id={infor1.get("id")} onClick={e=>this.props.concernOnclick( e, userId,infor1.get("id"))}></span><span className="hand" data-ids={thisId} style={{cursor:"pointer"}} data-titles="0" data-der={infor1.get("description")} id={infor1.get("name")} data-indexs={this.props.keys}  data-logid="110" data-logcontent="编辑" onClick={this.props.editOnclick} title="编辑"></span></td>
                    </tr>
                );
            }
        }
    }
});

export default CooperationMainSingleView;


