import {React, ReactDOM, Immutable, PureRenderMixin} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';
import ParallelPicView from './ParallelPicView';

const AnalyseParallelView = React.createClass({
    mixins: [PureRenderMixin],
    componentWillReceiveProps: function (nextProps) {
        let list1 = nextProps.list.get(nextProps.bjhmMergeAnalyseFilter);
        let list2 = this.props.list.get(this.props.bjhmMergeAnalyseFilter);
        let picDOM = document.querySelector('#parallelpic');
        list1 = list1 ? list1 : Immutable.List();
        list2 = list2 ? list2 : Immutable.List();

        if (nextProps.bjhmMergeAnalyseFilter != actionTypes.JUDGED_ANALYSE_BJHM
            && (nextProps.bjhmMergeAnalyseFilter != this.props.bjhmMergeAnalyseFilter
            || (nextProps.bjhmMergeAnalyseFilter == this.props.bjhmMergeAnalyseFilter
            && list1.size != list2.size))) {
            this.renderSvgChart(nextProps.list.get(nextProps.bjhmMergeAnalyseFilter), nextProps.changeDfhmByPolyline, picDOM);
        }

        if(nextProps.bjhmMergeAnalyseFilter == actionTypes.JUDGED_ANALYSE_BJHM){
            this.clearSvgChart(picDOM);
        }

        //切换到此状态时
        if (nextProps.bjhmMergeAnalyseFilter == actionTypes.JUDGED_ANALYSE_BJHM && this.myChart) {
            this.myChart.clear();
        }
    },
    getBjhmUI: function () {
        let aBjhm = [];
        this.props.list.forEach((aItem, bjhm)=> {
            aBjhm.push(
                <option value={bjhm} key={bjhm}>{bjhm}</option>
            );
        });

        return aBjhm;
    },
    clearSvgChart: function (picDOM) {
        ReactDOM.unmountComponentAtNode(picDOM);
    },
    renderSvgChart: function (List, changeDfhmByPolyline, picDOM) {
        //通话次数最大值
        let nMaxCount = 0;
        //通话时长最大值
        let nMaxThsc = 0;
        //敏感时间段通话次数最大值
        let nMaxSenCount = 0;

        List.forEach(item=> {
            if (item.get('count') > nMaxCount) {
                nMaxCount = item.get('count');
            }

            if (item.get('thsc') > nMaxThsc) {
                nMaxThsc = item.get('thsc');
            }

            if (item.get('sensitiveCount') > nMaxSenCount) {
                nMaxSenCount = item.get('sensitiveCount');
            }
        });

        this.clearSvgChart(picDOM);
        ReactDOM.render(<ParallelPicView
            maxCount={nMaxCount}
            maxThsc={nMaxThsc}
            maxSenCount={nMaxSenCount}
            list={List}
            changeDfhmByPolyline={changeDfhmByPolyline}
        />, picDOM);
    },
    render: function () {
        return (
            <div>
                <div className="analysetitle">平行坐标图</div>
                <div className="analysebjhmfilter">
                    <select className="form-control input-sm" value={this.props.bjhmMergeAnalyseFilter}
                            onChange={e=>this.props.changeBjhmMergeAnalyseFilter(e.currentTarget.value)} data-logid="224" data-logcontent="话单--页面右侧分析-平面坐标图本机号码">
                        <option value="ANALYSEBJHM">请选择本机号码</option>
                        {
                            this.getBjhmUI()
                        }
                    </select>
                </div>
                <div id="parallelpic"></div>
            </div>
        );
    }
});

export default AnalyseParallelView;


