import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const cooContent = (state = Immutable.fromJS([]), action)=> {
    switch (action.type) {
        case actionTypes.SET_COO_CONTENT:
            return Immutable.fromJS(action.cooContent);
        case actionTypes.UPDATA_COOPERATION:
            return state.setIn([action.index,'name'],action.text).setIn([action.index,'description'],action.content);
        default:
            return state;
    }
};


