import {React, connect, PureRenderMixin,createSelector} from '../../common/Util';
import AttrSelect2 from './AttrSelect2';

const CaseAttrView = React.createClass({
    mixins: [PureRenderMixin],
	getInitialState:function(){
		return{

		}
	},

    render: function () {

		//绑定案件属性 1级下拉属性
		let attrObject = this.props.caseObject;
        return (
        	<div>
				<AttrSelect2
					attrObject = {attrObject}
					cbChange = {this.cbChange}
					cbSelect = {this.cbSelect}
					attrData = {this.props.attrData}
				/>
			</div>
		);
    }
});

export default CaseAttrView;


