import {React, connect, PureRenderMixin,createSelector} from '../../common/Util';
import MainAttribute from './MainAttribute';

const SelectView = React.createClass({

	//清楚全部条件
	clearAllSelect(e){
		const {selectHtml,selectNodes} = this.state;
		this.setState({selectHtml:[]});
		this.setState({selectNodes:[]});
	},

    render: function () {
		var entityJson = [
			{"label":"person","label_name":"人"},
			{"label":"vehicle","label_name":"车"},
			{"label":"case","label_name":"案件"}
		];

		let options = null;
		if(entityJson){
			options = entityJson.map((op,index)=>(
				<option key={index+1}>{op.label_name}</option>
			));
		}

		let selectHtml = this.props.selectHtml;
		let selectStr = null;
		if(this.props.tgIndex ==1){
			selectStr = (
				<div className="bd">
					<div className="type_select">
					<span>
						<select onChange={this.props.attrChange} defaultValue='请选择'>
							<option key={1}>请选择</option>
							{options}
						</select>
					</span>
						<span className="describe">
						满足任意以下条件
					</span>
						<a className="clear_type" href="javascript:;" onClick={this.clearAllSelect}>清楚全部条件</a>
					</div>
					<div className="attribute">
						<ul>
							<MainAttribute
								cbChange = {this.props.cbChange}
								cbSelect = {this.props.cbSelect}
								selectNodes = {this.props.selectNodes}
								selectHtml = {this.props.selectHtml}
								personObject = {this.props.personObject}
								vehicleObject = {this.props.vehicleObject}
								caseObject = {this.props.caseObject}
								attrData = {this.props.attrData}
								selectData = {this.props.selectData}
							/>
						</ul>
					</div>
					<div className="ft">
						<a href="javascript:;" onClick={this.props.resultOnclick}>开始检索</a>
					</div>
				</div>
			);
		}else{
			selectStr = (
				<div></div>
			);
		}

		return (
			<div>{selectStr}</div>
        );
		
    }
});

export default SelectView;


