import * as actionTypes from '../actions/actionTypes';

export const rightTitleActiveReducer = (state = actionTypes.JUDGED_RIGHT_TITLE_ACTIVE.STATISTICS, action)=> {
    switch (action.type) {
        case actionTypes.SET_JUDGED_RIGHT_TITLE_ACTIVE:
            return action.active;
        default:
            return state;
    }
};


