import {dialog} from '../../common/Util';
import * as actionTypes from './actionTypes';
import JudgedService from '../service/JudgedService';

const actionCreator = {
    getAll: function (oSettings) {
        return dispatch=> {
            JudgedService.getAll(oSettings).done(oData=> {
                if (oData.statusCode == 200) {
                    let list = oData.eventData;
                    if (list.length == 0) {
                        dialog.alert('没有相关话单数据！');
                    } else {
                        dispatch(this.setAll(list, oSettings.isClear));
                    }
                }
            });
        }
    },
    setAll: function (list, isClear) {
        return {
            type: actionTypes.SET_JUDGED_ALL,
            list,
            isClear
        }
    },
    setShowListCount: function (count) {
        return {
            type: actionTypes.SET_JUDGED_LIST_SHOWCOUNT,
            count
        }
    },
    setKssjSort: function (sort) {
        return {
            type: actionTypes.SET_JUDGED_KSSJ_SORT,
            sort
        }
    },
    setThscSort: function (sort) {
        return {
            type: actionTypes.SET_JUDGED_THSC_SORT,
            sort
        }
    },
    setThcshSort: function (sort) {
        return {
            type: actionTypes.SET_JUDGED_THCSH_SORT,
            sort
        }
    },
    setThcshSensitiveSort: function (sort) {
        return {
            type: actionTypes.SET_JUDGED_THCSH_SENSITIVE_SORT,
            sort
        }
    },
    setThschSort: function (sort) {
        return {
            type: actionTypes.SET_JUDGED_THSCH_SORT,
            sort
        }
    },
    setSingleChecked: function (ids) {
        return {
            type: actionTypes.SET_JUDGED_SINGLE_CHECKED,
            ids
        }
    },
    setCheckedAll: function (checked) {
        return {
            type: actionTypes.SET_JUDGED_CHECKED_ALL,
            checked
        }
    },
    setRightTitleActive: function (active) {
        return {
            type: actionTypes.SET_JUDGED_RIGHT_TITLE_ACTIVE,
            active
        }
    },
    setStatisticBjhm: function (filter) {
        return {
            type: actionTypes.SET_JUDGED_STATISTICS_BJHM,
            filter
        }
    },
    setStatisticDfhm: function (filter) {
        return {
            type: actionTypes.SET_JUDGED_STATISTICS_DFHM,
            filter
        }
    },
    setStatisticXqh: function (filter) {
        return {
            type: actionTypes.SET_JUDGED_STATISTICS_XQH,
            filter
        }
    },
    setStatisticJzh: function (filter) {
        return {
            type: actionTypes.SET_JUDGED_STATISTICS_JZH,
            filter
        }
    },
    setAnalyseBjhm: function (filter) {
        return {
            type: actionTypes.SET_JUDGED_ANALYSE_BJHM,
            filter
        };
    },
    setAnalyseChooseType: function (filter) {
        return {
            type: actionTypes.SET_JUDGED_ANALYSE_CHOOSETYPE,
            filter
        };
    },
    setAnalyseChooseTime: function (filter) {
        return {
            type: actionTypes.SET_JUDGED_ANALYSE_CHOOSETIME,
            filter
        };
    },
    setAnalyseChooseTimeSpan: function (filter) {
        return {
            type: actionTypes.SET_JUDGED_ANALYSE_CHOOSETIMESPAN,
            filter
        };
    },
    setAnalyseDfhm: function (filter, aData) {
        return {
            type: actionTypes.SET_JUDGED_ANALYSE_DFHM,
            filter,
            aData
        };
    },
    setAnalyseMergeBjhm: function (filter) {
        return {
            type: actionTypes.SET_JUDGED_ANALYSE_MERGE_BJHM,
            filter
        };
    },
    setCheckedToTop: function (status) {
        return {
            type: actionTypes.SET_JUDGED_CHECKED_TOTOP,
            status
        }
    },
    setPeriod: function (active) {
        return {
            type: actionTypes.SET_JUDGED_PERIOD,
            active
        }
    },
    setPeriodFilter: function (filter, aData) {
        return {
            type: actionTypes.SET_JUDGED_PERIOD_DATE,
            filter,
            aData
        }
    },
    delChecked: function () {
        return {
            type: actionTypes.DEL_JUDGED_CHECKED
        }
    },
    delReverseChecked: function () {
        return {
            type: actionTypes.DEL_JUDGED_CHECKED_REVERSE
        }
    },
    setMachinePhone: function () {
        return {
            type: actionTypes.SET_JUDGED_MACHINEPHONE
        }
    },
    setStepForward: function () {
        return {
            type: actionTypes.SET_JUDGED_STEP_FORWARD
        }
    },
    setStepBack: function () {
        return {
            type: actionTypes.SET_JUDGED_STEP_BACK
        }
    },
    resetStep: function () {
        return {
            type: actionTypes.SET_JUDGED_STEP_RESET
        }
    },
    setMerge: function (status) {
        return {
            type: actionTypes.SET_JUDGED_MERGE,
            status
        };
    },
    setDfhmByPolyLine: function (aData) {
        return {
            type: actionTypes.SET_JUDGED_DFHM_POLYLINE,
            aData
        }
    }
};
export default actionCreator;


