    //详细属性点击选择
    cbChange:function(e){
        let id = e.target.value;
        let attrObject = this.state.attrObject;
        let attrData = this.state.attrData;
        if(attrObject){
            for(var i in attrObject){
                if(id == attrObject[i].property){
                    attrData = attrObject[i].enum_value.split(",");
                    this.setState({attrData});
                    console.log(JSON.stringify(attrData));
                }
            }
        }
    },

