import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const homeListReducer5 = (state = Immutable.fromJS({}), action)=> {
    switch (action.type) {
        case actionTypes.SET_HOME_MAP:
            return Immutable.fromJS(action.bodyDatassssssssss);
        default:
            return state;
    }
};


