import {React, connect, PureRenderMixin,createSelector} from '../../common/Util';

const SearchInput = React.createClass({

    render: function () {
	    let keyword = this.props.keyword;
        let key = null;
        if(keyword){
            key = keyword;
        }
        return (
            <div className="search_input mb">
                <input type="text"  defaultValue={key} onBlur={this.props.getInputTextBlur}/>
                <button onClick={this.props.resultOnclick} name="1"></button>
            </div>
        );
    }
});

export default SearchInput;


