import {React, $, PureRenderMixin} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';
let Ps = require('perfect-scrollbar');

const TicketMergeTableView = React.createClass({
    mixins: [PureRenderMixin],
    componentDidMount: function () {
        this.container = $('.tickettable').get(0);
        Ps.initialize(this.container, {
            wheelSpeed: 2,
            minScrollbarLength: 20
        });
    },
    componentWillReceiveProps: function (nextProps) {
        let newList = nextProps.dfhmByPolyline;
        let oldList = this.props.dfhmByPolyline;

        if (!newList.equals(oldList)) {
            this.container.scrollTop = 0;
        }
    },
    componentDidUpdate: function (prevProps, prevState) {
        let newList = prevProps.dfhmByPolyline;
        let oldList = this.props.dfhmByPolyline;

        if (!newList.equals(oldList)) {
            this.container.scrollTop = 0;
            Ps.update(this.container);
        }
    },
    changeThschSort: function () {
        let curSort = this.props.thschSort;
        let sort = "";

        switch (curSort) {
            case actionTypes.JUDGED_TABLE_SORT.DEFAULT:
            case actionTypes.JUDGED_TABLE_SORT.DESC:
                sort = actionTypes.JUDGED_TABLE_SORT.ASC;
                break;
            case actionTypes.JUDGED_TABLE_SORT.ASC:
                sort = actionTypes.JUDGED_TABLE_SORT.DESC;
                break;
        }

        this.props.changeThschSort(sort);
    },
    changeThcshSort: function () {
        let curSort = this.props.thcshSort;
        let sort = "";

        switch (curSort) {
            case actionTypes.JUDGED_TABLE_SORT.DEFAULT:
            case actionTypes.JUDGED_TABLE_SORT.DESC:
                sort = actionTypes.JUDGED_TABLE_SORT.ASC;
                break;
            case actionTypes.JUDGED_TABLE_SORT.ASC:
                sort = actionTypes.JUDGED_TABLE_SORT.DESC;
                break;
        }

        this.props.changeThcshSort(sort);
    },
    changeThcshSensitiveSort: function () {
        let curSort = this.props.thcshSensitiveSort;
        let sort = "";

        switch (curSort) {
            case actionTypes.JUDGED_TABLE_SORT.DEFAULT:
            case actionTypes.JUDGED_TABLE_SORT.DESC:
                sort = actionTypes.JUDGED_TABLE_SORT.ASC;
                break;
            case actionTypes.JUDGED_TABLE_SORT.ASC:
                sort = actionTypes.JUDGED_TABLE_SORT.DESC;
                break;
        }

        this.props.changeThcshSensitiveSort(sort);
    },
    getTableUI: function () {
        let aRow = [];
        let aData = this.props.dfhmByPolyline;

        let setRows = (aDfhm)=> {
            let aRowsUnLight = [];
            let aRowsLight = [];
            aDfhm.forEach((obj, index)=> {
                let dfhm = obj.get('dfhm');
                let ids = obj.get('ids');
                let count = obj.get('count');
                let sensitiveCount = obj.get('sensitiveCount');
                let thsc = obj.get('thsc');
                let data = obj.get('data');
                let checked = true;

                for (var i = 0, len = data.size; i < len; i++) {
                    checked = data.get(i).get('checked');
                    if (!checked) {
                        break;
                    }
                }

                if (ids.equals(aData)) {
                    aRowsLight.push(<div className="grouprow highlightrow" key={dfhm}>
                        <div className="pull-left"><input id={'id_' + ids.toJS().join('|')} type="checkbox"
                                                          checked={checked}
                                                          onChange={()=>this.props.setSingleChecked(ids)}/><label
                            htmlFor={'id_' + ids.toJS().join('|')}>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</label>
                        </div>
                        <div className="pull-left">{dfhm}</div>
                        <div className="pull-left">{count}</div>
                        <div className="pull-left">{thsc}</div>
                        <div className="pull-left">{sensitiveCount}</div>
                    </div>);
                } else {
                    aRowsUnLight.push(<div className="grouprow" key={dfhm}>
                        <div className="pull-left"><input id={'id_' + ids.toJS().join('|')} type="checkbox"
                                                          checked={checked}
                                                          onChange={()=>this.props.setSingleChecked(ids)}/><label
                            htmlFor={'id_' + ids.toJS().join('|')}>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</label>
                        </div>
                        <div className="pull-left">{dfhm}</div>
                        <div className="pull-left">{count}</div>
                        <div className="pull-left">{thsc}</div>
                        <div className="pull-left">{sensitiveCount}</div>
                    </div>);
                }
            });

            return aRowsLight.concat(aRowsUnLight);
        }

        this.props.list.forEach((aDfhm, bjhm)=> {
            //行高默认34
            let nRowHeight = 34;
            let height = aDfhm.size * nRowHeight;
            let bjhmStyle = {height: height, lineHeight: height + 'px'};

            aRow.push(<div className="trrow shit" key={bjhm}>
                <div style={bjhmStyle}>{bjhm}</div>
                {
                    setRows(aDfhm)
                }
            </div>);
        });

        return aRow;
    },
    render: function () {
        //全选是否选中
        let bCheckedAll = true;
        //确定全选是否选中

        for (let aDfhm of this.props.list.valueSeq()) {
            for (let dfhm of aDfhm) {
                let data = dfhm.get('data');
                let unChecked = dfhm.get('unChecked');
                if (unChecked > 0) {
                    bCheckedAll = false;
                    break;
                }
            }
        }

        let getSortClsByValue = (sortValue)=> {
            let sortCls = 'sorticon';

            if (sortValue == actionTypes.JUDGED_TABLE_SORT.ASC) {
                sortCls += " asc";
            }

            if (sortValue == actionTypes.JUDGED_TABLE_SORT.DESC) {
                sortCls += " desc";
            }

            return sortCls;
        }

        return (
            <div className="tickettable fullscreen">
                <div className="tbody">
                    <div className="trrow">
                        <div>本机号码</div>
                        <div className="grouprow">
                            <div className="pull-left">
                                <input id="selectAll" type="checkbox" checked={bCheckedAll}
                                       onChange={()=>this.props.setCheckedAll(!bCheckedAll)}/><label
                                htmlFor="selectAll">全选</label>
                            </div>
                            <div className="pull-left">对端号码</div>
                            <div className="pull-left" onClick={this.changeThcshSort}>通话次数<span
                                className={getSortClsByValue(this.props.thcshSort)}></span>
                            </div>
                            <div className="pull-left" onClick={this.changeThschSort}>通话时长(秒)<span
                                className={getSortClsByValue(this.props.thschSort)}></span>
                            </div>
                            <div className="pull-left" onClick={this.changeThcshSensitiveSort}>敏感时段通话次数<span
                                className={getSortClsByValue(this.props.thcshSensitiveSort)}></span>
                            </div>
                        </div>
                    </div>
                    {
                        this.getTableUI()
                    }
                </div>
            </div>
        );
    }
});

export default TicketMergeTableView;


