import {$, Tools, React, Immutable, PureRenderMixin} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';
import Methods from '../others/Methods';

require('bootstrap-datetime-picker');
$.fn.datetimepicker.dates['zh-CN'] = {
    days: ["星期日", "星期一", "星期二", "星期三", "星期四", "星期五", "星期六", "星期日"],
    daysShort: ["周日", "周一", "周二", "周三", "周四", "周五", "周六", "周日"],
    daysMin: ["日", "一", "二", "三", "四", "五", "六", "日"],
    months: ["一月", "二月", "三月", "四月", "五月", "六月", "七月", "八月", "九月", "十月", "十一月", "十二月"],
    monthsShort: ["一月", "二月", "三月", "四月", "五月", "六月", "七月", "八月", "九月", "十月", "十一月", "十二月"],
    today: "今天",
    suffix: [],
    meridiem: ["上午", "下午"]
};

const AnalyseStripView = React.createClass({
    mixins: [PureRenderMixin],
    componentDidMount: function () {
        $('.form_datetime').datetimepicker({
            language: 'zh-CN',
            bootcssVer: 3,
            autoclose: true,
            format: 'yyyy-mm-dd hh:ii',
            minuteStep: 5,
            todayBtn: true,
            pickerPosition: "bottom-left"
        }).on('changeDate', e=> {
            this.props.changeChooseTimeAnalyseFilter(Tools.convertDate2FormatStr(e.date, 'yyyy-MM-dd hh:mm'));
        });
    },
    getBjhmUI: function () {
        return this.props.list.groupBy(oTicket=> {
            return oTicket.get('bjhm');
        }).keySeq().sort().map(phone=> {
            return <option key={phone} value={phone}>{phone}</option>;
        });
    },
    getDfhmUI: function () {
        let sBjhm = this.props.bjhmAnalyseFilter;
        let sChooseType = this.props.chooseTypeAnalyseFilter;
        let sChooseTime = this.props.chooseTimeAnalyseFilter;
        let sChooseTimeSpan = this.props.chooseTimeSpanAnalyseFilter;

        if (sBjhm == actionTypes.JUDGED_ANALYSE_BJHM) {
            return "";
        } else {
            switch (sChooseType) {
                case actionTypes.JUDGED_ANALYSE_CHOOSETYPE.NEWPHONE:
                case actionTypes.JUDGED_ANALYSE_CHOOSETYPE.DISAPPEARPHONE:
                    let aBeforeSplit = Immutable.List();
                    let aAfterSplit = Immutable.List();
                    let oBeforeDfhm = {};
                    let oAfterDfhm = {};
                    let aNewList = Immutable.fromJS([]);
                    let nDate = Date.parse(sChooseTime);

                    //新增号码与消失号码处理的两个数组
                    this.props.list.forEach(item=> {
                        let sBjhmReal = item.get('bjhm');
                        //通话开始时间
                        let sThkssj = Number(item.get('thkssj'));

                        //根据本地号码进行过滤
                        if (sBjhm == sBjhmReal) {
                            //根据开始时间对话单进行分隔
                            if (sThkssj < nDate) {
                                aBeforeSplit = aBeforeSplit.push(item);
                            } else {
                                aAfterSplit = aAfterSplit.push(item);
                            }
                        }
                    });

                    //以dfhm分组，获得所有的dfhm
                    oBeforeDfhm = Methods.getDataByField(aBeforeSplit, 'dfhm');
                    oAfterDfhm = Methods.getDataByField(aAfterSplit, 'dfhm');

                    //新增号码，消失号码
                    let aBeforeSet = Immutable.Set(oBeforeDfhm.aData.map(item=>item.get('field')));
                    let aAfterSet = Immutable.Set(oAfterDfhm.aData.map(item=>item.get('field')));

                    aNewList = sChooseType == actionTypes.JUDGED_ANALYSE_CHOOSETYPE.NEWPHONE ? aAfterSet.subtract(aBeforeSet) : aBeforeSet.subtract(aAfterSet);

                    //对过滤后的数据进行处理
                    let oData = Methods.getDataByField(aBeforeSplit.concat(aAfterSplit), 'dfhm');
                    let oMapData = oData.mapData;
                    //记录所有对端号码及个数
                    let aLis = Immutable.List();
                    aNewList.forEach(item=> {
                        aLis = aLis.push(Immutable.fromJS({field: item, count: oMapData.get(item).size}));
                    });

                    aLis = aLis.sort((item1, item2)=> {
                        return item2.get('count') - item1.get('count');
                    });

                    let maxFre = aLis.size > 0 ? aLis.first().get('count') : 0;

                    return aLis.slice(0, 10).map(oTicket=> {
                        let field = oTicket.get('field');
                        let count = oTicket.get('count');
                        let aTicket = oMapData.get(field);
                        aTicket = aTicket.size > 0 ? aTicket.groupBy(item=>item.get('id')).keySeq() : aTicket;

                        return (
                            <li className="clearfix" key={field}>
                                <div className="pull-left propsname">{field}</div>
                                <div className="pull-left propprogress"
                                     onClick={()=>this.props.changeAnalyseDfhmFilter(field, aTicket)} data-logid="223" data-logcontent="话单--页面右侧分析-对端号码">
                                    <div className="progressbg"></div>
                                    <div
                                        className={this.props.dfhmAnalyseFilter.get('filter') == field ? 'progressvalue active' : 'progressvalue'}
                                        style={{width: Math.floor(180 * count / maxFre)}}></div>
                                    <div className="count">{count}</div>
                                </div>
                            </li>
                        );
                    });
                    break;
                case actionTypes.JUDGED_ANALYSE_CHOOSETYPE.SENSITIVETIME:
                    //敏感时间段
                    switch (sChooseTimeSpan) {
                        case actionTypes.JUDGED_ANALYSE_CHOOSETIMESPAN.ONE:
                            //20:00-22:00
                            return this.renderUIBetweenTime(20, 22, sBjhm);
                            break;
                        case actionTypes.JUDGED_ANALYSE_CHOOSETIMESPAN.TWO:
                            //22:00-24:00
                            return this.renderUIBetweenTime(22, 24, sBjhm);
                            break;
                        case actionTypes.JUDGED_ANALYSE_CHOOSETIMESPAN.THREE:
                            //00:00-06:00
                            return this.renderUIBetweenTime(0, 6, sBjhm);
                            break;
                    }
                    break;
            }
        }
    },
    renderUIBetweenTime: function (startTime, endTime, sBjhm) {
        let aNewList = this.props.list.filter(function (item) {
            //本机号码
            let sBjhmReal = item.get('bjhm');
            //通话开始时间
            var sThkssj = new Date(Number(item.get('thkssj')));
            var hour = sThkssj.getHours();

            if (endTime == 24) {
                hour = hour == 0 ? 24 : hour;
            }

            if (sBjhmReal == sBjhm && (hour >= startTime && hour < endTime)) {
                return item;
            }
        });

        //对过滤后的数据进行处理
        let oData = Methods.getDataByField(aNewList, 'dfhm');
        let maxFre = oData.maxFre;
        //截取前10条
        let aData = oData.aData.slice(0, 10);

        return aData.map(oTicket=> {
            let field = oTicket.get('field');
            let count = oTicket.get('count');
            let aTicket = oData.mapData.get(field);
            aTicket = aTicket.size > 0 ? aTicket.groupBy(item=>item.get('id')).keySeq() : aTicket;

            return (
                <li className="clearfix" key={field}>
                    <div className="pull-left propsname">{field}</div>
                    <div className="pull-left propprogress"
                         onClick={()=>this.props.changeAnalyseDfhmFilter(field, aTicket)}>
                        <div className="progressbg"></div>
                        <div
                            className={this.props.dfhmAnalyseFilter.get('filter') == field ? 'progressvalue active' : 'progressvalue'}
                            style={{width: Math.floor(180 * count / maxFre)}}></div>
                        <div className="count">{count}</div>
                    </div>
                </li>
            );
        });
    },
    render: function () {
        return (
            <div>
                <div className="analysetitle">条形统计图</div>
                <div className="analysefilter clearfix">
                    <div className="pull-left">
                        <select className="form-control input-sm" value={this.props.bjhmAnalyseFilter}
                                onChange={e=>this.props.changeBjhmAnalyseFilter(e.currentTarget.value)}
                                data-logid="219" data-logcontent="话单--页面右侧分析-本机号码">
                            <option value="ANALYSEBJHM">本机号码</option>
                            {this.getBjhmUI()}
                        </select>
                    </div>
                    <div className="pull-left">
                        <select className="form-control input-sm" value={this.props.chooseTypeAnalyseFilter}
                                onChange={e=>this.props.changeChooseTypeAnalyseFilter(e.currentTarget.value)}
                                data-logid="220" data-logcontent="话单--页面右侧分析-本机号码右侧选项">
                            <option value="NEWPHONE">新增号码</option>
                            <option value="DISAPPEARPHONE">消失号码</option>
                            <option value="SENSITIVETIME">敏感时间段</option>
                        </select>
                    </div>
                    <div
                        className={this.props.chooseTypeAnalyseFilter != actionTypes.JUDGED_ANALYSE_CHOOSETYPE.SENSITIVETIME ? 'pull-left' : 'pull-left hide'}>
                        <div className="input-group date form_datetime">
                            <input type="text" readOnly="readOnly"
                                   className="form-control input-sm input-datetimepicker"
                                   value={this.props.chooseTimeAnalyseFilter}/>
                            <span className="input-group-addon" data-logid="221" data-logcontent="话单--页面右侧分析-本机号码下侧时间选项"><i
                                className="glyphicon glyphicon-th"></i></span>
                        </div>
                    </div>
                    <div
                        className={this.props.chooseTypeAnalyseFilter == actionTypes.JUDGED_ANALYSE_CHOOSETYPE.SENSITIVETIME ? 'pull-left' : 'pull-left hide'}>
                        <select className="form-control input-sm" value={this.props.chooseTimeSpanAnalyseFilter}
                                onChange={e=>this.props.changeChooseTimeSpanAnalyseFilter(e.currentTarget.value)}
                                data-logid="222" data-logcontent="话单--页面右侧分析-本机号码下侧敏感时间选项">
                            <option value="ONE">20:00-22:00</option>
                            <option value="TWO">22:00-24:00</option>
                            <option value="THREE">00:00-06:00</option>
                        </select>
                    </div>
                </div>
                <div className="propscontent">
                    <ul className="list-unstyled listprops">
                        {this.getDfhmUI()}
                    </ul>
                </div>
            </div>
        );
    }
});

export default AnalyseStripView;


