import * as actionTypes from '../actions/actionTypes';

export const xqhStatisticReducer = (state = "", action)=> {
    switch (action.type) {
        case actionTypes.SET_JUDGED_STATISTICS_XQH:
            return state == action.filter ? "" : action.filter;
        default:
            return state;
    }
};


