import {React, connect, PureRenderMixin,createSelector,dialog,$} from '../../common/Util';
import actionCreator from '../actions/actionCreator';

import TabsControl from './TabsControl';
import SnapshotView from './SnapshotView';
import FileInforView from './FileInforView';
import CooperationMainTar from './CooperationMainTar';
import CooperationMainSelect from './CooperationMainSelect';
import CooperationMainDataTable from './CooperationMainDataTable';
import CooperationMainPage from './CooperationMainPage';
import CooperationAlert from './CooperationAlert';

const MainCooperationView = React.createClass({
    mixins: [PureRenderMixin],
    getInitialState: function () {
        //初始化数据
        return {
            type: "2",
            keyword: "",
            startTime: "",
            endTime: "",
            judgeThemeId:"",
            judgeThemeName:"",
            pageNo: "1",
            pageSize: "4",
            count: true,
            edit:"",
            alertType:"",
            data_index:0,
            photoName:"",
            photoid:'',
            tUserId:"",
            ids:"",
            der:"",
            graphDescription:"",
            timeValue:"",
            themeValue:"",
            stateTime:"",
            keyValue:"",
            tabValue:0,
            fileInfo_startTime: "",
            fileInfo_endTime: ""
        }
    },
    componentDidMount: function () {
        //触发得到所有数据
        let {dispatch} = this.props;

        dispatch(actionCreator.getAllUser());//所有用户
        dispatch(actionCreator.getAuthority());//用户id
        //2.0对外接口
        scopaConfig.pages.cooperation.getCooperationDataByPhotos = this.getCooperationDataByPhotos;
        //主题列表对外接口
        scopaConfig.pages.cooperation.getCooperationDataCounts = this.getCooperationDataCounts;
        //相关资料对外接口
        scopaConfig.pages.cooperation.getFileInfoConentData = this.getFileInfoConentData;
    },
    getFileInfoConentData:function(){
        let {dispatch} = this.props;
        let judgeThemeId = this.state.judgeThemeId;
        if(judgeThemeId != ""){
            dispatch(actionCreator.getPhoneFileInfo(judgeThemeId));
        }
    },
    themeOnChange: function (e) {
        //协作——主题下拉列表选择
        let {dispatch} = this.props;
        let userId = this.props.cooAuthorityReducer;
        let {type,keyword,startTime,endTime,pageNo,pageSize} = this.state;
        type = e.target.value
        this.setState({type});
        pageNo = 1;
        this.setState({pageNo});
        $("#cooperation_main_select_theme").val(type);

        dispatch(actionCreator.getCooCount(userId,type,keyword,startTime,endTime,pageNo,pageSize));
        dispatch(actionCreator.getCooNum(userId,type,keyword,startTime,endTime,"",""));
    },
    getCooperationDataCounts:function(){
        let {type,keyword,timeValue,pageNo,}=this.state;
        let {dispatch} = this.props;
        if(type=="2" && keyword=="" && timeValue=="" && pageNo=="1"){
            dispatch(actionCreator.getAuthority());//用户id
        }
        if(!this.state.count){
            scopaConfig.pages.cooperation.photosAbord();
        }
    },
    keyOnChange: function (e) {
        //协作——关键字筛选
        let keyword = this.state.keyword;
        //记录关键字value值
        keyword = e.target.value
        this.setState({keyword});
    },
    keyOnClick:function(event){
        let {dispatch} = this.props;
        let userId = this.props.cooAuthorityReducer;
        let {type,keyword,startTime,endTime,pageNo,pageSize} = this.state;
        pageNo = 1;
        this.setState({pageNo});
        if(event.currentTarget.tagName === 'A'){
            dispatch(actionCreator.getCooCount(userId,type,keyword,startTime,endTime,pageNo,pageSize));
            dispatch(actionCreator.getCooNum(userId,type,keyword,startTime,endTime,"",""));
        }
        if(event.currentTarget.tagName === 'INPUT' && event.keyCode === 13){
            dispatch(actionCreator.getCooCount(userId,type,keyword,startTime,endTime,pageNo,pageSize));
            dispatch(actionCreator.getCooNum(userId,type,keyword,startTime,endTime,"",""));
        }
    },
    timeOnChange: function (start, end) {
        //协作——时间筛选
        let {dispatch} = this.props;
        let userId = this.props.cooAuthorityReducer;
        let {type,keyword,startTime,endTime,pageNo,pageSize}=this.state;
        pageNo = 1;
        this.setState({pageNo});
        let timeValue = this.state.timeValue;
        if( $(event.target).hasClass('clearBtn')){
            startTime = "";
            endTime = "";
            timeValue = "";
        }else{
            startTime = start.format('YYYY-MM-DD HH:MM:SS');
            endTime = end.format('YYYY-MM-DD HH:MM:SS');

            let startTimes = start.format('YYYY-MM-DD');
            let endTimes = end.format('YYYY-MM-DD');
            timeValue = startTimes+"至"+endTimes;
        }
        this.setState({timeValue});
        this.setState({startTime});
        this.setState({endTime});

        dispatch(actionCreator.getCooCount(userId,type,keyword,startTime,endTime,pageNo,pageSize));
        dispatch(actionCreator.getCooNum(userId,type,keyword,startTime,endTime,"",""));
    },
    followOnclickTrue: function () {
        //快照跳转协作
        let {count,type,timeValue,keyword,keyValue,stateTime,themeValue} = this.state;
        count = true;
        this.setState({count});

        themeValue = type;
        stateTime = timeValue;
        keyValue = keyword;
        this.setState({themeValue});
        this.setState({stateTime});
        this.setState({keyValue});
    },
    followOnclickFalse: function (e) {
        //协作跳转快照
        this.getCooperationDataByPhotos(e.target.dataset.ids,e.target.dataset.titles,e.target.dataset.descriptions);
    },
    getCooperationDataByPhotos:function(ids,names,descriptions){
        if(ids==""){
            let count = this.state.count;
            count = true;
            this.setState({count});
        }else{
            let {dispatch} = this.props;

            let count = this.state.count;
            count = false;
            this.setState({count});

            let judgeThemeId = this.state.judgeThemeId;
            judgeThemeId = ids;
            this.setState({judgeThemeId});

            let graphDescription = this.state.graphDescription;
            graphDescription = descriptions;
            this.setState({graphDescription});

            let judgeThemeName = this.state.judgeThemeName;
            judgeThemeName = names;
            this.setState({judgeThemeName});

            let tabValue = this.state.tabValue;
            tabValue = 0;
            this.setState({tabValue});
            //调用相关资料接口
            dispatch(actionCreator.getPhoneFileInfo(String(judgeThemeId)));
        }
    },
    followOnclickFile:function(e){
        //协作跳转相关资料
        let {dispatch} = this.props;

        let count = this.state.count;
        count = false;
        this.setState({count});

        let judgeThemeId = this.state.judgeThemeId;
        judgeThemeId = e.target.dataset.ids;
        this.setState({judgeThemeId});

        let graphDescription = this.state.graphDescription;
        graphDescription = e.target.dataset.descriptions;
        this.setState({graphDescription});

        let judgeThemeName = this.state.judgeThemeName;
        judgeThemeName = e.target.dataset.titles;
        this.setState({judgeThemeName});

        let tabValue = this.state.tabValue;
        tabValue = 1;
        this.setState({tabValue});
        //调用相关资料接口
        dispatch(actionCreator.getPhoneFileInfo(String(judgeThemeId)));
    },
    tabOnclickChange:function(index){
        let tabValue = this.state.tabValue;
        tabValue = index;
        this.setState({tabValue});
    },
    editOnclick: function (e) {
        //点击编辑显示弹窗
            let edit = this.state.edit;
            edit = e.target.id;
            this.setState({edit});

            let data_index = this.state.data_index;
            data_index = e.target.dataset.indexs;
            this.setState({data_index});

            let der = this.state.der;
            der = e.target.dataset.der;
            this.setState({der});

            let ids = this.state.ids;
            ids = e.target.dataset.ids;
            this.setState({ids});

            let alertType = this.state.alertType;
            alertType = e.target.dataset.titles;
            this.setState({alertType});

            document.getElementsByClassName("cooperation_alert")[0].style.display="block";
            document.getElementsByClassName("cooperation_describe")[0].value=edit;
            document.getElementsByClassName("cooperation_describe1")[0].value=der;
    },
    editFailOnclick:function(){
        //取消编辑
        document.getElementsByClassName("cooperation_alert")[0].style.display="none";
        document.getElementsByClassName("cooperation_describe")[0].value="";
        document.getElementsByClassName("cooperation_describe1")[0].value="";
    },
    editDoneOnclick:function(){
        //确认编辑
        let {dispatch} = this.props;
        let ids = this.state.ids;
        let theme = document.getElementsByClassName("cooperation_describe")[0].value.trim();
        let content = document.getElementsByClassName("cooperation_describe1")[0].value.trim();
        let index = this.state.data_index;
        if(theme=="" || content==""){
            dialog({
                title: '编辑失败',
                content: '研判主题或主题描述不能为空'
            }).showModal();
        }
        if(content.length>200){
            dialog({
                title: '编辑失败',
                content: '主题描述不能超过200字'
            }).showModal();
        }
        if(theme!="" && content!="" && content.length<=200){
            dispatch(actionCreator.updataCooperation(theme,index,content));

            dispatch(actionCreator.getEditAttention(ids,theme,content));
            document.getElementsByClassName("cooperation_alert")[0].style.display="none";
            document.getElementsByClassName("cooperation_describe")[0].value="";
            document.getElementsByClassName("cooperation_describe1")[0].value="";
        }

    },
    concernOnclick:function(e,user_id,judgeThemeId){
        //关注切换
        let {dispatch} = this.props;
        if(e.target.dataset.choise=="true"){
            if(e.target.className == "hand"){
                e.target.style.background="url('./../../images/quxiao.png') no-repeat";
                e.target.className="quxiao hand";
                e.target.title="取消关注";
                dispatch(actionCreator.getReomveAttention(user_id,judgeThemeId));
            }else if(e.target.className == "guanzhu hand"){
                e.target.style.background="url('./../../images/quxiao.png') no-repeat";
                e.target.className="quxiao hand";
                e.target.title="取消关注";
                dispatch(actionCreator.getReomveAttention(user_id,judgeThemeId));
            }else{
                e.target.style.background="url('./../../images/guanzhu.png') no-repeat 0px 2px";
                e.target.className="guanzhu hand";
                e.target.title="关注";
                dispatch(actionCreator.getAddAttention(user_id,judgeThemeId));
            }
        }else{
            if(e.target.className == "hand"){
                e.target.style.background="url('./../../images/guanzhu.png') no-repeat 0px 2px";
                e.target.className="guanzhu hand";
                e.target.title="关注";
                dispatch(actionCreator.getAddAttention(user_id,judgeThemeId));
            }else if(e.target.className == "guanzhu hand"){
                e.target.style.background="url('./../../images/quxiao.png') no-repeat";
                e.target.className="quxiao hand";
                e.target.title="取消关注";
                dispatch(actionCreator.getReomveAttention(user_id,judgeThemeId));
            }else{
                e.target.style.background="url('./../../images/guanzhu.png') no-repeat 0px 2px";
                e.target.className="guanzhu hand";
                e.target.title="关注";
                dispatch(actionCreator.getAddAttention(user_id,judgeThemeId));
            }
        }
    },
    giveOnclick:function(e){
        //推送弹窗
        let photoName = this.state.photoName;
        photoName = e.target.id;
        this.setState({photoName});

        let photoid = this.state.photoid;
        photoid = e.target.dataset.photoid;
        this.setState({photoid});

        let alertType = this.state.alertType;
        alertType = e.target.dataset.titles;
        this.setState({alertType});
        document.getElementsByClassName("cooperation_alert")[0].style.display="block";
    },
    giveFailOnclick:function(e){
        //取消推送弹窗
        document.getElementsByClassName("cooperation_alert")[0].style.display="none";
        document.getElementsByClassName("cooperation_describe")[0].value="";
    },
    gitUserId:function(e){
        //获取快照id
        let tUserId = this.state.tUserId;
        tUserId = e.target.value
        this.setState({tUserId});
    },
    giveDoneOnclick:function(e){
        //推送确定
        let {dispatch} = this.props;
        let content = document.getElementsByClassName("cooperation_describe1")[0].value;

        let photoid = this.state.photoid;
        let userId = this.props.cooAuthorityReducer;
        let tUserId=this.state.tUserId;

        dispatch(actionCreator.getTuisongAttention(userId,tUserId,photoid,content));

        document.getElementsByClassName("cooperation_alert")[0].style.display="none";
        document.getElementsByClassName("cooperation_describe")[0].value="";

    },
    phoneUserPage:function(page){
        //协作-协同作战-相关资料翻页操作
        let {dispatch} = this.props;
        let judgeThemeId = this.state.judgeThemeId;

        dispatch(actionCreator.getPhoneFileInfo(judgeThemeId,page));
    },
    onDateApply:function(start, end, label){
        //协作-协同作战-相关资料日期操作
        let {dispatch} = this.props;
        let judgeThemeId = this.state.judgeThemeId;
        let keyword = $('[name=fileInfokeyword]').val();
        let {fileInfo_startTime,fileInfo_endTime}=this.state;

        if( $(event.target).hasClass('clearBtn')){
            fileInfo_startTime = "";
            fileInfo_endTime = "";
        }else{
            fileInfo_startTime = start.format('YYYY-MM-DD HH:MM:SS');
            fileInfo_endTime = end.format('YYYY-MM-DD HH:MM:SS');
        }

        this.setState({fileInfo_startTime});
        this.setState({fileInfo_endTime});

        dispatch(actionCreator.getPhoneFileInfo(judgeThemeId,'',fileInfo_startTime,fileInfo_endTime,keyword));
    },
    onhandleSearch:function(event,keyword){
        //协作-协同作战-相关资料搜索操作
        let {dispatch} = this.props;
        let judgeThemeId = this.state.judgeThemeId;
        let {fileInfo_startTime,fileInfo_endTime}=this.state;

        if(event.currentTarget.tagName === 'A'){
            dispatch(actionCreator.getPhoneFileInfo(judgeThemeId,'',fileInfo_startTime,fileInfo_endTime,keyword ));
        }
        if(event.currentTarget.tagName === 'INPUT' && event.keyCode === 13){
            dispatch(actionCreator.getPhoneFileInfo(judgeThemeId,'',fileInfo_startTime,fileInfo_endTime,keyword ));
        }
    },
    phoneRemove:function(id,jtid,index){
        //协作-协同作战-相关资料删除操作
        let {dispatch} = this.props;
        dialog.confirm({
            cancel: function(){

            },
            ok:function(){
                dispatch(actionCreator.getPhoneRemove(id,index));
                dispatch(actionCreator.getPhoneFileInfo(jtid));
            }
        }).showModal();

    },
    pageClick:function(page){
        //协作翻页
        let {dispatch} = this.props;
        let userId = this.props.cooAuthorityReducer;
        let {type,keyword,startTime,endTime,pageNo,pageSize}=this.state;
        pageNo = page;
        this.setState({pageNo});
        dispatch(actionCreator.getCooNum(userId,type,keyword,startTime,endTime,"",""));
        dispatch(actionCreator.getCooCount(userId,type,keyword,startTime,endTime,pageNo,pageSize));
    },
    render: function () {
        let {cooperation_fileInfo,cooContent,cooNum,cooperation_AllUser,cooAuthorityReducer} = this.props;
        let {pageNo,count,edit,der,alertType,judgeThemeId,graphDescription,judgeThemeName,photoName,type,timeValue,keyword}=this.state;

        if (count) {
            return (
                <div className="cooperation_main_view component">
                    <CooperationMainTar
                        followOnclick={this.followOnclickTrue}
                    />
                    <div className="cooperation_main">
                        <CooperationMainSelect
                            themeOnChange={this.themeOnChange}
                            timeOnChange={this.timeOnChange}
                            keyOnChange={this.keyOnChange}
                            keyOnClick={this.keyOnClick}
                            cooNum={cooNum}
                            pageNo={pageNo}
                            themeValue={type}
                            timeValue={timeValue}
                            keyword={keyword}
                            count={count}
                        />
                        <CooperationMainDataTable
                            pageNo={pageNo}
                            userId={cooAuthorityReducer}
                            cooContent={cooContent}
                            followOnclick={this.followOnclickFalse}
                            editOnclick={this.editOnclick}
                            concernOnclick={this.concernOnclick}
                            followOnclickFile={this.followOnclickFile}
                        />
                        <CooperationMainPage
                            pageNo={pageNo}
                            cooNum={cooNum}
                            pageClick={this.pageClick}
                        />
                    </div>
                    <CooperationAlert
                        edit={edit}
                        der={der}
                        editFailOnclick={this.editFailOnclick}
                        editDoneOnclick={this.editDoneOnclick}
                    />
                </div>
            );
        } else {
            return (
                <div className="cooperation_main_view">
                    <CooperationMainTar
                        followOnclick={this.followOnclickTrue}
                    />
                    <div className="clearfix">
                        <TabsControl tabValue={this.state.tabValue} tabOnclickChange={this.tabOnclickChange}>
                            <div name="快照" icon="sprite-i_snapshot">
                                <SnapshotView
                                    user_id={cooAuthorityReducer}
                                    judgeThemeName={judgeThemeName}
                                    judgeThemeId={judgeThemeId}
                                    graphDescription={graphDescription}
                                    giveOnclick={this.giveOnclick}
                                />
                            </div>
                            <div name="相关资料" icon="sprite-i_relatedInfor">
                                {/*话单数据——点击跳转至（协作二级-相关资料），通过judgeThemeId获取话单序列号，然后传给参数*/}
                                <FileInforView
                                    judgeThemeName={judgeThemeName}
                                    judgeThemeId={judgeThemeId}
                                    fileInfoAjaxResult={cooperation_fileInfo}
                                    onUserPage={this.phoneUserPage}
                                    onPhoneRemove={this.phoneRemove}
                                    onDateApply={this.onDateApply}
                                    onhandleSearch={this.onhandleSearch}
                                />
                            </div>
                        </TabsControl>
                    </div>
                    <CooperationAlert
                        giveFailOnclick={this.giveFailOnclick}
                        alertType={alertType}
                        photoName={photoName}
                        cooperation_AllUser={cooperation_AllUser}
                        giveDoneOnclick={this.giveDoneOnclick}
                        gitUserId={this.gitUserId}
                    />
                </div>
            );
        }
    }
});

const cooperation_fileInfo = state => {
    return state.cooperation_fileInfo;
};
const cooContent = state => {
    return state.cooContent;
};
const cooNum = state => {
    return state.cooNum;
};
const cooperation_AllUser = state => {
    return state.cooperation_AllUser;
};
const cooAuthorityReducer = state => {
    return state.cooAuthorityReducer;
};

const getCooperation = createSelector([cooperation_fileInfo,cooContent,cooNum,cooperation_AllUser,cooAuthorityReducer], (cooperation_fileInfo,cooContent,cooNum,cooperation_AllUser,cooAuthorityReducer)=> {
    return {
        cooperation_fileInfo:cooperation_fileInfo,
        cooContent:cooContent,
        cooNum:cooNum,
        cooperation_AllUser:cooperation_AllUser,
        cooAuthorityReducer:cooAuthorityReducer
    }
});

export default connect(getCooperation)(MainCooperationView);


