import { React,dialog,PureRenderMixin,$} from '../../common/Util';


const SnapshotComponentDaterange = React.createClass({
    mixins:[PureRenderMixin],
    componentDidMount: function () {
        $('input[name="reservations"]').daterangepicker(
            {
                "opens": "left",
                "format":'YYYY-MM-DD',
                "applyClass": 'btn-primary',
                "clearClass": 'btn-primary',
                "timePicker": true,
                "timePicker24Hour": true,
                "timePickerSeconds": true
            },
            this.props.timeOnChange
        ).on('clear.daterangepicker', function(){
            $(this).val('');
        });
    },
    render: function () {
        return (
            <div className="dateranger cooperation_main_select_time">
                <input data-logid="119" data-logcontent="协作-快照-时间检索" style={{cursor:"default"}} type="text" name="reservations" className="daterangebox-ipt" value="" placeholder="按周期模糊搜索"/>
            </div>
        );
    }
});

export default SnapshotComponentDaterange;



