import * as actionTypes from '../actions/actionTypes';

export const bjhmMergeAnalyseReducer = (state = actionTypes.JUDGED_ANALYSE_BJHM, action)=> {
    switch (action.type) {
        case actionTypes.SET_JUDGED_ANALYSE_MERGE_BJHM:
            return action.filter;
        default:
            return state;
    }
};


