import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const snapshotNum = (state = Immutable.fromJS([]), action)=> {
    switch (action.type) {
        case actionTypes.GET_SNAPSHOT_NUM:
            return Immutable.fromJS(action.snapshotNum);
        default:
            return state;
    }
};


