import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const snapshotAllUser = (state = Immutable.fromJS([]), action)=> {
    switch (action.type) {
        case actionTypes.GET_ALL_USER:
            return Immutable.fromJS(action.snapalluser);
        default:
            return state;
    }
};


