import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const homeListReducer2 = (state = Immutable.fromJS([]), action)=> {
    switch (action.type) {
        case actionTypes.SET_HOME_CASE:
            return Immutable.fromJS(action.bodyDatassss);
        default:
            return state;
    }
};


