import {React,PureRenderMixin} from '../../common/Util';
const InformationSingleView = React.createClass({
    mixins:[PureRenderMixin],
    typeOnclick:function(e){
        let type = e.target.dataset.ids;
        let groupId = e.target.dataset.team;
        let names = e.target.dataset.names
        if(names=="团伙预警"){
            window.showForewarnDetail = groupId;
        }
        if(names=="隐性重点人挖掘"){
            window.showCovertPersonId = groupId;
        }
        mining.utils.hashChange(type);
    },
    render: function () {

        let infor = this.props.infor;

        return (
            <tr data-ids={infor.get('type')} data-team={infor.get('groupId')} data-names={infor.get('name')} data-content={infor.get('content')} onClick={this.typeOnclick} data-logid="103" data-logcontent={infor.get('name')} className="hand">
                <td data-ids={infor.get('type')} data-team={infor.get('groupId')} data-names={infor.get('name')} data-logid="103" data-logcontent={infor.get('name')} title={infor.get('name')} style={{width:"24%",paddingLeft:"20px"}}>{infor.get('name')}</td>
                <td data-ids={infor.get('type')} data-team={infor.get('groupId')} data-names={infor.get('name')} data-logid="103" data-logcontent={infor.get('name')} title={infor.get('content')} style={{width:"46%"}}>{infor.get('content')}</td>
                <td data-ids={infor.get('type')} data-team={infor.get('groupId')} data-names={infor.get('name')} data-logid="103" data-logcontent={infor.get('name')} title={infor.get('time')} style={{width:"30%"}}>{infor.get('time')}</td>
            </tr>
        );
    }
});

export default InformationSingleView;


