'use strict';

import React from 'react';

const Paginator = React.createClass({
    getDefaultProps(){
        return {
          max: 10,
          current: 1
        }
    }, 

    render(){
        let current = this.props.current;
        let total = this.props.total;
        let max = this.props.max;

        let start = Math.max(Number(current) - Math.round(max/2) ,1);

        let end = Math.min((start+max)-1,total);
        if(end - start < max) {
            start = Math.max(1,end - (max)-1);
        }
        let hasLess = null;
        let previous = null;
        if(start != 1){
            hasLess = (
                <li ><a  style={{cursor:"pointer",zIndex:"0",marginRight:"0px",borderRadius:"3px"}} >...</a></li>
            );
            previous = (
                <li>
                  <a  style={{cursor:"pointer",zIndex:"0",marginRight:"0px",borderRadius:"3px"}} href="javascript:void(0)" >
                    <span style={{cursor:"pointer",zIndex:"0",marginBottom:"0px",marginRight:"0px",borderRadius:"3px"}} className="glyphicon glyphicon-chevron-left" onClick={(e)=>this.props.onPage(Number(current)-1)}></span>
                  </a>
                </li>
            );
        }
        let hasMore =null;
        let next = null;
        if(end<total){
            hasMore =  (
                <li ><a style={{cursor:"pointer",zIndex:"0",marginRight:"0px",borderRadius:"3px"}}>...</a></li>
            );
            next = (
                <li>
                    <a style={{cursor:"pointer",zIndex:"0",marginRight:"0px",borderRadius:"3px"}} href="javascript:void(0)" >
                        <span style={{zIndex:"0",marginBottom:"0px",marginRight:"0px",borderRadius:"3px"}} className="glyphicon glyphicon-chevron-right" onClick={(e)=>this.props.onPage(Number(current)+1)}></span>
                    </a>
                </li>
            );
        }
        let getLinks= (start,end)=>{
            const links = [];
            for(let i=start;i<=end;i++){
                let className = i==current?'active':'';
                links.push(<li key={i} className={className}><a style={{cursor:"pointer",zIndex:"0",marginRight:"0px",borderRadius:"3px"}} href="javascript:void(0)" onClick={(e)=>this.props.onPage(i)}>{i}</a></li>);
            }
            return links;
        };
        return(
            <div className="text-center">
                <ul className="pagination">
                    {previous}
                    {hasLess}
                    {getLinks(start,end)}
                    {hasMore}
                    {next}
                </ul>
            </div>
        )
    }
})
export  default Paginator;


