import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const cooKContent = (state = Immutable.fromJS([]), action)=> {
    switch (action.type) {
        case actionTypes.SET_K_COO_CONTENT:
            return state.merge(Immutable.fromJS(action.K_cooContent));
        default:
            return state;
    }
};


