import {React, connect, PureRenderMixin,createSelector} from '../../common/Util';
import actionCreator from '../actions/actionCreator';
import Select2 from 'react-select2';
import AttrSelect2 from './AttrSelect2';
const PersonAttrView = React.createClass({
    mixins: [PureRenderMixin],
	getInitialState:function(){
		return{

		}
	},

    render: function () {
		let attrObject = this.props.attrObject;
        return (
			<div>
				<AttrSelect2
					attrObject = {attrObject}
					cbChange = {this.props.cbChange}
					cbSelect = {this.props.cbSelect}
					attrData = {this.props.attrData}
					selectData = {this.props.selectData}
				/>
			</div>
		);
    }
});

export default PersonAttrView;


