import {$,React, connect, PureRenderMixin,createSelector} from '../../common/Util';
import actionCreator from '../actions/actionCreator';
require('moment');
require('../../plugins/daterangepicker');

const FileInfoComponentDaterange = React.createClass({
    mixins:[PureRenderMixin],
    componentDidMount: function () {
        let {dispatch} = this.props;
        let self = this;
        $('.DateComponent input[name="fileRanger"]').daterangepicker(
            {
                "opens": "left",
                "format":'YYYY-MM-DD',
                "applyClass": 'btn-primary',
                "clearClass": 'btn-primary',
                "timePicker": true,
                "timePicker24Hour": true,
                "timePickerSeconds": true
            },
            function (start, end, label) {

                self.handleDate(start, end, label)
            }
        ).on('clear.daterangepicker', function(){
            $(this).val('');
        });
    },
    handleDate:function(start, end, label){
        this.props.onDateApply(start, end, label)
    },
    render: function () {
        let {dispatch} = this.props;
        return (
            <div className="dateranger DateComponent">
                <input type="text" name="fileRanger" className="daterangebox-ipt" value=""
                       placeholder="按周期模糊搜索" data-logid="132" data-logcontent="协作-协同作战-相关资料日期操作"/>
            </div>
        );
    }
});


export default FileInfoComponentDaterange;


