import { React,dialog,PureRenderMixin } from '../../common/Util';
import Paginator from './Paginator';

const CooperationMainPage = React.createClass({
    mixins:[PureRenderMixin],
    componentDidMount: function () {

    },
    render: function () {
        let pages={
            current:this.props.pageNo,
            pageTotal:Math.ceil(this.props.cooNum / 4)
        }
        return (
            <div>
                <Paginator current={pages.current} total={pages.pageTotal} max={this.max} onPage={this.props.pageClick}></Paginator>
            </div>
        );
    }
});

export default CooperationMainPage;


