import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const periodDateReducer = (state = Immutable.fromJS({filter: "", aData: []}), action)=> {
    switch (action.type) {
        case actionTypes.SET_JUDGED_PERIOD_DATE:
            return state.get('filter') != action.filter ? state.merge({
                filter: action.filter,
                aData: action.aData
            }) : state.merge({
                filter: "",
                aData: []
            });
        default:
            return state;
    }
};


