import * as actionTypes from '../actions/actionTypes';

export const thcshSortReducer = (state = actionTypes.JUDGED_TABLE_SORT.DEFAULT, action)=> {
    switch (action.type) {
        case actionTypes.SET_JUDGED_THCSH_SORT:
            return action.sort;
        default:
            return state;
    }
};


