import {$} from '../../common/Util';
import BaseService from '../../base/ServiceBase';

export default class CooperationService extends BaseService {
    static getFileInfo(judgeThemeId,page,starttime,endtime,keyword) {
        //话单文件管理
        var pageNo = "1",
            pageSize = "20";
        if(page){
            pageNo = String(page);
        }
        if(starttime==undefined){
            starttime='';
        }
        if(endtime==undefined){
            endtime='';
        }
        if(keyword==undefined){
            keyword='';
        }
        let URL= "tuning/services/core/media/getPhoneFileList?jtid="+judgeThemeId+"&pageNo="+pageNo+"&pageSize="+pageSize+"&starttime="+starttime+"&endtime="+endtime+"&keyword="+keyword;

        return this.getWithParameter(URL);
    }
    static setPhoneRemove(id){
        let URL= "tuning/services/core/media/removeJT";
        return this.postWithParameter(URL,{id:id});
    }
    static getAddAttention(user_id,judgeThemeId){
        //给用户添加关注的研判主题
        let URL= "tuning/services/core/userAttention/insert";
        return this.postWithParameter(URL,{userId:user_id,judgeThemeId:judgeThemeId});
    }
    static getReomveAttention(user_id,judgeThemeId){
        //取消用户关注的研判主题
        let URL= "tuning/services/core/userAttention/deleteOne";
        return this.postWithParameter(URL,{userId:user_id,judgeThemeId:judgeThemeId});
    }
    static getCooCount(userId,type,keyword,startTime,endTime,pageNo,pageSize) {
        //获取研判主题数据
        let URL= "tuning/services/core/judgeTheme/getList?"
        if(userId != ""){
            URL+="userId="+userId;
        }
        if(type != ""){
            URL+="&type="+type;
        }
        if(keyword != ""){
            URL+="&keyword="+keyword;
        }
        if(startTime != ""){
            URL+="&startTime="+startTime;
        }
        if(endTime != ""){
            URL+="&endTime="+endTime;
        }if(pageNo != ""){
            URL+="&pageNo="+pageNo;
        }
        if(pageSize != ""){
            URL+="&pageSize="+pageSize;
        }

        return this.getWithParameter(URL);
    }

    static getCooNum(K_userId,K_type,K_keyword,K_startTime,K_endTime,K_pageNo,K_pageSize) {
        //获取研判主题数据数量
        let URL= "tuning/services/core/judgeTheme/getCount?"
        if(K_userId != ""){
            URL+="userId="+K_userId;
        }
        if(K_type != ""){
            URL+="&type="+K_type;
        }
        if(K_keyword != ""){
            URL+="&keyword="+K_keyword;
        }
        if(K_startTime != ""){
            URL+="&startTime="+K_startTime;
        }
        if(K_endTime != ""){
            URL+="&endTime="+K_endTime;
        }
        return this.getWithParameter(URL);
    }
    static getSnapshotList(K_userId,judgeThemeId,K_type,K_keyword,K_startTime,K_endTime,K_pageNo,K_pageSize) {
        //提取一个研判主题下的所有快照
        let URL= "tuning/services/core/snapshot/listSnap?"
        if(K_userId != ""){
            URL+="user_id="+K_userId;
        }
        if(judgeThemeId != ""){
            URL+="&judgeId="+judgeThemeId;
        }
        if(K_type != ""){
            URL+="&type="+K_type;
        }
        if(K_keyword != ""){
            URL+="&keyword="+K_keyword;
        }
        if(K_startTime != ""){
            URL+="&starttime="+K_startTime;
        }
        if(K_endTime != ""){
            URL+="&endtime="+K_endTime;
        }if(K_pageNo != ""){
            URL+="&pageNo="+K_pageNo;
        }
        if(K_pageSize != ""){
            URL+="&pageSize="+K_pageSize;
        }
        return this.getWithParameter(URL);
    }
    static getAllUser(){//获取所有用户
        return this.getWithParameter('tuning/services/console/user/allUsers');
    }

    static getDelectAttention(num){//删除快照
        let URL= "tuning/services/core/snapshot/delete";
        return this.postWithParameter(URL,{sid:num});
    }
    static getFenxiangAttention(num){//分享
        let URL= "tuning/services/core/snapshot/share";
        return this.postWithParameter(URL,{sid:num});
    }
    static getQuxiaoFenxiangAttention(num){//取消分享
        let URL="tuning/services/core/snapshot/unshare";
        return this.postWithParameter(URL,{sid:num});
    }
    static getEditAttention(id,thum,count){//编辑
        let URL="tuning/services/core/judgeTheme/update";
        return this.postWithParameter(URL,{id:id,name:thum,description:count});
    }
    static getTuisongAttention(fid,tid,sid,comment){//推送
        let URL="tuning/services/core/snapshot/push";
        return this.postWithParameter(URL,{sid:sid,fid:fid,tid:tid,comment:comment});
    }
    static getGraphData(id){//获取图析信息
        let URL="tuning/services/core/snapshot/load?sid="+id;
        return this.getWithParameter(URL);
    }
    static getAuthority(){//获取用户id
        return this.getWithParameter('tuning/services/pass/getUserInfo');
    }
}


