import * as actionTypes from '../actions/actionTypes';

export const chooseTypeAnalyseReducer = (state = actionTypes.JUDGED_ANALYSE_CHOOSETYPE.NEWPHONE, action)=> {
    switch (action.type) {
        case actionTypes.SET_JUDGED_ANALYSE_CHOOSETYPE:
            return action.filter;
        default:
            return state;
    }
};


