import {React} from '../../common/Util';
import Methods from '../others/Methods';

const StatisticsView = React.createClass({
    getBjhm: function () {
        return this.props.list.groupBy(oTicket=> oTicket.get('bjhm')).keySeq();
    },
    getBjhmUI: function () {
        return this.getBjhm().sort().map(phone=> {
            return <li className={this.props.bjhmFilter == phone ? 'active' : ''} key={phone} data-logid="215" data-logcontent="话单--页面右侧统计-本机号码"
                       onClick={()=>this.props.changeStatisticBjhmFilter(phone)}>{phone}</li>;
        });
    },
    getDfhmUI: function () {
        let oData = Methods.getDataByField(this.props.list, 'dfhm');
        let maxFre = oData.maxFre;
        //截取前5条
        let aData = oData.aData.slice(0, 5);
        return aData.map((oTicket, index)=> {
            let field = oTicket.get('field');
            let count = oTicket.get('count');
            return (
                <li className="clearfix" key={index+'-'+field}>
                    <div className="pull-left propsname">{field}</div>
                    <div className="pull-left propprogress" data-logid="216" data-logcontent="话单--页面右侧统计-对端号码"
                         onClick={()=>this.props.changeStatisticDfhmFilter(field)}>
                        <div className="progressbg"></div>
                        <div
                            className={this.props.dfhmFilter == field ? 'progressvalue active' : 'progressvalue'}
                            style={{width: Math.floor(180 * count / maxFre)}}></div>
                        <div className="count">{count}</div>
                    </div>
                </li>
            );
        });
    },
    getXqhUI: function () {
        let oData = Methods.getDataByField(this.props.list, 'ePHONE_xqh');
        let maxFre = oData.maxFre;
        //截取前5条
        let aData = oData.aData.slice(0, 5);

        return aData.map((oTicket, index)=> {
            let field = oTicket.get('field');
            let count = oTicket.get('count');

            return (
                <li className="clearfix" key={field+'|'+index}>
                    <div className="pull-left propsname">{field}</div>
                    <div className="pull-left propprogress" data-logid="217" data-logcontent="话单--页面右侧统计-逻辑小区"
                         onClick={()=>this.props.changeStatisticXqhFilter(field)}>
                        <div className="progressbg"></div>
                        <div
                            className={this.props.xqhFilter == field ? 'progressvalue active' : 'progressvalue'}
                            style={{width: Math.floor(180 * count / maxFre)}}></div>
                        <div className="count">{count}</div>
                    </div>
                </li>
            );
        });
    },
    getJzhUI: function () {
        let oData = Methods.getDataByField(this.props.list, 'ePHONE_jzh');
        let maxFre = oData.maxFre;
        //截取前5条
        let aData = oData.aData.slice(0, 5);

        return aData.map((oTicket, index)=> {
            let field = oTicket.get('field');
            let count = oTicket.get('count');

            return (
                <li className="clearfix" key={field + '_' + index}>
                    <div className="pull-left propsname">{field}</div>
                    <div className="pull-left propprogress" data-logid="218" data-logcontent="话单--页面右侧统计-基站小区"
                         onClick={()=>this.props.changeStatisticJzhFilter(field)}>
                        <div className="progressbg"></div>
                        <div
                            className={this.props.jzhFilter == field ? 'progressvalue active' : 'progressvalue'}
                            style={{width: Math.floor(180 * count / maxFre)}}></div>
                        <div className="count">{count}</div>
                    </div>
                </li>
            );
        })
    },
    triangledownBjhmClick:function(){
        if(this.refs.bjhmlist.className == "triangledown hide"){
            this.refs.bjhmlist.className = "triangledown";
        }else{
            this.refs.bjhmlist.className = "triangledown hide";
        }

    },
    triangledownSxClick:function(){
        if(this.refs.propscontent.className == "propscontent hide"){
            this.refs.propscontent.className = "propscontent";
        }else{
            this.refs.propscontent.className = "propscontent hide";
        }
    },
    render: function () {
        return (
            <div>
                <div className="statisticstitle">本机号码({this.getBjhm().size})<span className="triangledown" onClick={this.triangledownBjhmClick}></span></div>
                <ul className="list-unstyled bjhmlist" ref="bjhmlist">
                    {
                        this.getBjhmUI()
                    }
                </ul>
                <div className="statisticstitle">属性<span className="triangledown" onClick={this.triangledownSxClick}></span></div>
                <div className="propscontent" ref="propscontent">
                    <div className="title">对端号码</div>
                    <ul className="list-unstyled listprops">
                        {
                            this.getDfhmUI()
                        }
                    </ul>
                    <div className="title">逻辑小区</div>
                    <ul className="list-unstyled listprops">
                        {
                            this.getXqhUI()
                        }
                    </ul>
                    <div className="title">基站小区</div>
                    <ul className="list-unstyled listprops">
                        {
                            this.getJzhUI()
                        }
                    </ul>
                </div>
            </div>
        );
    }
});

export default StatisticsView;


