import * as actionTypes from './actionTypes';
import CooperationService from '../service/CooperationService';
import {dialog} from '../../common/Util';

const actionCreator = {
    getPhoneFileInfo: function (judgeThemeId,page,starttime,endtime,keyword) {
        return dispatch=> {
            CooperationService.getFileInfo(judgeThemeId,page,starttime,endtime,keyword).done(oData=> {
                if (oData.statusCode == 200) {
                    dispatch(this.setPhoneFileInfo(oData));
                }
            })
        }
    },
    setPhoneFileInfo: function (fileInfo) {
        return {
            type: actionTypes.SET_PHONE_FILEINFO,
            fileInfo
        }
    },
    getPhoneRemove:function(id,index){
        let self = this;
        return dispatch=>{
            CooperationService.setPhoneRemove(id,index).done(oData=>{
                if(oData.statusCode == 200){
                    dispatch({
                        type: actionTypes.SET_PHONE_REMOVE,
                        index:index
                    });
                }
            })
        }
    },
    //研判的关注与取消关注切换
    getAddAttention:function(user_id,judgeThemeId){
        return dispatch=>{
            CooperationService.getAddAttention(user_id,judgeThemeId).done(oData=>{
                if(oData.statusCode == 200){
                    console.log("添加成功")
                }else{
                    console.log("添加失败")
                }
            })
        }
    },
    getReomveAttention:function(user_id,judgeThemeId){
        return dispatch=>{
            CooperationService.getReomveAttention(user_id,judgeThemeId).done(oData=>{
                if(oData.statusCode == 200){
                    console.log("取消成功")
                }else{
                    console.log("取消失败")
                }
            })
        }
    },
    //快照删除
    getDelectAttention:function(sid,user_id,judgeThemeId,type,keyword,startTime,endTime,pageNo,pageSize){
        return dispatch=>{
            CooperationService.getDelectAttention(sid).done(oData=>{
                if(oData.statusCode == 200){
                    console.log("删除成功")
                    dispatch(this.getSnapshotList(user_id,judgeThemeId,type,keyword,startTime,endTime,pageNo,pageSize));
                }else{
                    console.log("删除失败")
                }
            })
        }
    },
    getEditAttention:function(id,thum,count){
        return dispatch=>{
            CooperationService.getEditAttention(id,thum,count).done(oData=>{
                if(oData.statusCode == 200){
                    dialog.alert('编辑成功！');
                }else{
                    dialog.alert('编辑成功！');
                }
            })
        }
    },
    //快照的分享切换
    getFenxiangAttention:function(sid){
        return dispatch=>{
            CooperationService.getFenxiangAttention(sid).done(oData=>{
                if(oData.statusCode == 200){
                    console.log("分享成功")
                }else{
                    console.log("分享失败")
                }
            })
        }
    },
    getQuxiaoFenxiangAttention:function(sid){
        return dispatch=>{
            CooperationService.getQuxiaoFenxiangAttention(sid).done(oData=>{
                if(oData.statusCode == 200){
                    console.log("取消分享成功")
                }else{
                    console.log("取消分享失败")
                }
            })
        }
    },
    //推送
    getTuisongAttention:function(sid,fid,tid,comment){
        return dispatch=>{
            CooperationService.getTuisongAttention(sid,fid,tid,comment).done(oData=>{
                if(oData.statusCode == 200){
                    dialog.alert('推送成功！');
                }else{
                    dialog.alert('推送失败！');
                }
            })
        }
    },
    //研判的编辑
    updataCooperation:function (text,index,content) {
        return {
            type:actionTypes.UPDATA_COOPERATION,
            text,
            index,
            content
        }
    },
    //快照分享的切换
    updataFenxiang:function (num,index) {
        return {
            type:actionTypes.UPDATA_FENXIANG,
            num,
            index
        }
    },
    //删除单个快照
    delectKuaizhao:function (num) {
        return {
            type:actionTypes.DELECT_KUAIZHAO,
            num
        }
    },
    //获取研判数据
    getCooCount: function (userId,type,keyword,startTime,endTime,pageNo,pageSize) {
        return dispatch=> {
            CooperationService.getCooCount(userId,type,keyword,startTime,endTime,pageNo,pageSize).done(oData=> {
                if (oData.statusCode == 200) {
                        dispatch(this.setCooCount(oData.listObj));
                }
            })
        }
    },
    setCooCount: function (cooContent) {
        return {
            type: actionTypes.SET_COO_CONTENT,
            cooContent
        }
    },
    //检测研判主题关注
   checkAttentionTo: function(checkResult){
       return {
           type: actionTypes.CHECK_ATTENTION_TO,
           checkResult
       }
   },

    //获取研判主题数量
    getCooNum: function (K_userId,K_type,K_keyword,K_startTime,K_endTime,K_pageNo,K_pageSize) {
        return dispatch=> {
            CooperationService.getCooNum(K_userId,K_type,K_keyword,K_startTime,K_endTime,K_pageNo,K_pageSize).done(oData=> {
                if (oData.statusCode == 200) {
                    dispatch(this.setCooNum(oData.obj));
                }
            })
        }
    },
    setCooNum: function (cooNum) {
        return {
            type: actionTypes.SET_COO_NUM,
            cooNum
        }
    },
    //获取快照列表数据
    getSnapshotList: function (K_userId,judgeThemeId,K_type,K_keyword,K_startTime,K_endTime,K_pageNo,K_pageSize) {
        return dispatch=> {
            CooperationService.getSnapshotList(K_userId,judgeThemeId,K_type,K_keyword,K_startTime,K_endTime,K_pageNo,K_pageSize).done(oData=> {
                if (oData.statusCode == 200) {
                    dispatch(this.setSnapshotList(oData.bodyData));
                    dispatch(this.setSnapshotNum(oData.totalCount));
                }
            })
        }
    },
    setSnapshotList: function (snapshotList) {
        return {
            type: actionTypes.GET_SNAPSHOT_LIST,
            snapshotList
        }
    },
    setSnapshotNum: function (snapshotNum) {
        return {
            type: actionTypes.GET_SNAPSHOT_NUM,
            snapshotNum
        }
    },
    //获取所有用户列表数据
    getAllUser: function () {
        return dispatch=> {
            CooperationService.getAllUser().done(oData=> {
                if (oData.statusCode == 200) {
                    dispatch(this.setAllUser(oData.listObj));
                }
            })
        }
    },
    setAllUser: function (snapalluser) {
        return {
            type: actionTypes.GET_ALL_USER,
            snapalluser
        }
    },
    //获取图析数据
    getGraphData: function (id) {
        return dispatch=> {
            CooperationService.getGraphData(id).done(oData=> {
                if (oData.statusCode == 200) {
                    dispatch(this.setGraphData(oData.obj));
                }
            })
        }
    },
    setGraphData: function (graphData) {
        return {
            type: actionTypes.GET_GRAPH_DATA,
            graphData
        }
    },
    //获取用户id
    getAuthority: function () {
        return dispatch=> {
            CooperationService.getAuthority().done(oData=> {
                if (oData.statusCode == 200) {
                    dispatch(this.setAuthority(oData.obj["user_id"]));
                    dispatch(this.getCooCount(oData.obj["user_id"],2,"","","",1,4));
                    dispatch(this.getCooNum(oData.obj["user_id"],2,"","","",1,4));
                }
            })
        }
    },
    setAuthority: function (Authority) {
        return {
            type: actionTypes.GET_AUTHORITY,
            Authority
        }
    }
};
export default actionCreator;


