import * as actionTypes from '../actions/actionTypes';

export const dfhmStatisticReducer = (state = "", action)=> {
    switch (action.type) {
        case actionTypes.SET_JUDGED_STATISTICS_DFHM:
            return state == action.filter ? "" : action.filter;
        default:
            return state;
    }
};


