import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const HomeAuthorityReducer = (state = "", action)=> {
    switch (action.type) {
        case actionTypes.GET_HOME_AUTHORITY:
            return state = action.Authoritys;
        default:
            return state;
    }
};


