import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const snapshotList = (state = Immutable.fromJS([]), action)=> {
    switch (action.type) {
        case actionTypes.GET_SNAPSHOT_LIST:
            return Immutable.fromJS(action.snapshotList);
        case actionTypes.UPDATA_FENXIANG:
            return state.setIn([action.index,'status'],action.num);
        case actionTypes.DELECT_KUAIZHAO:
            return state.splice(action.num,1);
        default:
            return state;
    }
};


