import {React, connect, PureRenderMixin,createSelector} from '../../common/Util';
import actionCreator from '../actions/actionCreator';

const FileInfoComponentKeywordSearch = React.createClass({
    mixins:[PureRenderMixin],
    componentDidMount: function () {
    },
    handleSearch:function(e){
        this.props.onhandleSearch(e,this.refs.keyword.value)
    },
    render: function () {
        let {dispatch} = this.props;
        return (
            <div className="keysearch keywordSearch">
                <input type="text" name="fileInfokeyword" ref="keyword" className="searchbox-ipt" placeholder="请输入关键字查询" onKeyUp={this.handleSearch}/>
                <a className="searchbox-btn" href="javascript:;" onClick={this.handleSearch} data-logid="129" data-logcontent="协作-协同作战-相关资料搜索操作"></a>
            </div>
        );
    }
});


export default FileInfoComponentKeywordSearch;


