import {React,PureRenderMixin} from '../../common/Util';
import OnlyLinkSingleView  from './oLinkSingleView';

const LinkSingleView = React.createClass({
    mixins:[PureRenderMixin],
    render: function () {
        let infor = this.props.infor;
        let oLink = infor.get('countent');
        let oa = [];

        oLink.forEach((item, index)=> {
            oa.push(
                <OnlyLinkSingleView
                    key   = {index}
                    oinforType = {item.get('type')}
                    oinforUrl = {item.get('URL')}
                />
            )

        })
        return (
            <div className = "table_Link">
                <span>{infor.get('title')}</span>
                <div className = "Link_bar">
                    {oa}
                </div>
            </div>
        );
    }
});

export default LinkSingleView;


