import {$, React, Tools, Immutable, PureRenderMixin, dialog} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';
let Ps = require('perfect-scrollbar');

const TicketNormalTableView = React.createClass({
    mixins: [PureRenderMixin],
    componentDidMount: function () {
        let listShowCount = this.props.listShowCount;
        let changeShowListCount = this.props.changeShowListCount;
        let container = $('.tickettable').get(0);

        this.container = container;
        Ps.initialize(this.container, {
            minScrollbarLength: 100
        });

        document.addEventListener('ps-y-reach-end', function (e) {
            changeShowListCount(listShowCount += 10);
        });

        document.addEventListener('ps-y-reach-start', function (e) {
            changeShowListCount(actionTypes.JUDGED_TABLE_SHOWCOUNT);
        });
    },
    componentWillReceiveProps: function (nextProps) {
        let newList = nextProps.list.get('aHighlight').concat(nextProps.list.get('aUnHighlight'));
        let oldList = this.props.list.get('aHighlight').concat(this.props.list.get('aUnHighlight'));

        if (newList.size != oldList.size) {
            this.container.scrollTop = 0;
            this.props.changeShowListCount(actionTypes.JUDGED_TABLE_SHOWCOUNT);
        }
    },
    componentDidUpdate: function (prevProps, prevState) {
        let newList = prevProps.list.get('aHighlight');
        let oldList = this.props.list.get('aHighlight');

        if (newList.size != oldList.size) {
            this.container.scrollTop = 0;
            this.props.changeShowListCount(30);
            Ps.update(this.container);
        }
    },
    changeKssjSort: function () {
        let curSort = this.props.kssjSort;
        let sort = "";

        switch (curSort) {
            case actionTypes.JUDGED_TABLE_SORT.DEFAULT:
            case actionTypes.JUDGED_TABLE_SORT.DESC:
                sort = actionTypes.JUDGED_TABLE_SORT.ASC;
                break;
            case actionTypes.JUDGED_TABLE_SORT.ASC:
                sort = actionTypes.JUDGED_TABLE_SORT.DESC;
                break;
        }

        this.props.changeKssjSort(sort);
    },
    changeThscSort: function () {
        let curSort = this.props.thscSort;
        let sort = "";

        switch (curSort) {
            case actionTypes.JUDGED_TABLE_SORT.DEFAULT:
            case actionTypes.JUDGED_TABLE_SORT.DESC:
                sort = actionTypes.JUDGED_TABLE_SORT.ASC;
                break;
            case actionTypes.JUDGED_TABLE_SORT.ASC:
                sort = actionTypes.JUDGED_TABLE_SORT.DESC;
                break;
        }

        this.props.changeThscSort(sort);
    },
    render: function () {
        let list = this.props.list.get('aHighlight').concat(this.props.list.get('aUnHighlight'));

        //全选是否选中
        let bCheckedAll = true;
        //确定全选是否选中
        for (let oTicket of list) {
            if (!oTicket.get('checked')) {
                bCheckedAll = false;
                break;
            }
        }

        let getSortClsByValue = (sortValue)=> {
            let sortCls = 'sorticon';

            if (sortValue == actionTypes.JUDGED_TABLE_SORT.ASC) {
                sortCls += " asc";
            }

            if (sortValue == actionTypes.JUDGED_TABLE_SORT.DESC) {
                sortCls += " desc";
            }

            return sortCls;
        }

        return (
            <div className="tickettable">
                <div className="tbody">
                    <div className="trrow">
                        <div>
                            <input id="selectAll" type="checkbox" checked={bCheckedAll}
                                   onChange={()=> {
                                       this.props.setCheckedAll(!bCheckedAll)
                                   }}/><label htmlFor="selectAll">全选</label>
                        </div>
                        <div>本机号码</div>
                        <div>类型</div>
                        <div>对端号码</div>
                        <div onClick={this.changeKssjSort}>开始时间<span
                            className={getSortClsByValue(this.props.kssjSort)}></span>
                        </div>
                        <div onClick={this.changeThscSort}>通话时长(秒)<span
                            className={getSortClsByValue(this.props.thscSort)}></span>
                        </div>
                        <div>基站小区号</div>
                        <div>逻辑小区号</div>
                    </div>
                    {
                        list.map((item, index)=> {
                            let id = item.get('id');
                            let highlight = item.get('highlight');
                            let checked = item.get('checked');
                            //行样式处理
                            let rowCls = "trrow";
                            if (highlight) {
                                rowCls += ' highlightrow';
                            }
                            if (checked) {
                                rowCls += ' checkedrow';
                            }

                            if (index >= this.props.listShowCount) {
                                rowCls += " hide";
                            }

                            //通话类型
                            let hjlx = item.get('ePHONE_hjlx');
                            let hjlxSpan = <span className={hjlx == "主叫" ? "dialing" : "called"}>{hjlx}</span>;

                            return (
                                <div className={rowCls} key={'judged_' + id + highlight + checked + index}>
                                    <div>
                                        <input id={'judged_' + id} value={id} type="checkbox"
                                               checked={item.get('checked')}
                                               onChange={()=>this.props.setSingleChecked(Immutable.fromJS([id]))}/><label
                                        htmlFor={'judged_' + id}>&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;&nbsp;</label>
                                    </div>
                                    <div>{item.get('bjhm')}</div>
                                    <div>{hjlxSpan}</div>
                                    <div>{item.get('dfhm')}</div>
                                    <div>{Tools.convertDate2FormatStr(new Date(Number(item.get('thkssj'))), 'yyyy-MM-dd hh:mm:ss')}</div>
                                    <div>{item.get('ePHONE_thsc')}</div>
                                    <div>{item.get('ePHONE_jzh')}</div>
                                    <div>{item.get('ePHONE_xqh')}</div>
                                </div>
                            );
                        })
                    }
                </div>
            </div>
        );
    }
});

export default TicketNormalTableView;


