import { React,dialog,PureRenderMixin } from '../../common/Util';

const CooperationAlert = React.createClass({
    mixins:[PureRenderMixin],
    componentDidMount: function () {

    },
    render: function () {
        let alertType = this.props.alertType;
        let cooperation_AllUser = this.props.cooperation_AllUser;
        let oPosition = [];

        if(cooperation_AllUser != undefined ){
            cooperation_AllUser.forEach((item,index)=>{
                if(item.get("name").length>=8){
                    oPosition.push(
                        <option style={{width:"100px",overflow:"hidden"}} key={index} value={item.get("user_id")}>{item.get("name").substring(0,8)}</option>
                    )
                }else{
                    oPosition.push(
                        <option style={{width:"100px",overflow:"hidden"}} key={index} value={item.get("user_id")}>{item.get("name")}</option>
                    )
                }

            });
        }

        if(alertType){
            return (
                <div className="cooperation_alert" style={{width:"100%",height:"100%",background:"rgba(0,0,0,0.7)",position:"absolute",top:"0px",left:"0px",display:"none"}} >
                    <div style={{position:"absolute",left:"50%",top:"50%",width:"420px",height:"310px",marginLeft:"-210px",marginTop:"-155px",background:"white",border:"1px solid #dddee3"}}>
                        <div style={{lineHeight:"34px",height:"34px",width:"100%",background:"#eff2f9",borderBottom:"1px solid #d2d8e4"}}><span style={{float:"left",marginLeft:"20px",color:"#55678d"}}>快照推送</span><span onClick={this.props.giveFailOnclick} style={{float:"right",marginRight:"20px",cursor:"default",width:"10px",height:"11px",display:"inline-block",background:"url('./../../images/none.png')",marginTop:"11.5px"}}>&nbsp;</span></div>
                        <div style={{width:"100%",height:"124px",marginTop:"10px"}}>
                            <img style={{width:"80%",height:"124px",margin:"0 10%"}} src="../../../images/photo.png"/>
                        </div>
                        <div style={{width:"100%",height:"auto",marginTop:"10px"}}>
                            <div style={{display:"inline-block",fontWeight:"bold",marginLeft:"20px"}}>快照名称&nbsp;&nbsp;<span style={{fontWeight:"normal"}}>{this.props.photoName}</span></div>
                            <div style={{display:"inline-block",float:"right",marginRight:"40px"}}>
                                <div style={{display:"inline-block"}}>推送至&nbsp;&nbsp;</div>
                                <div style={{display:"inline-block"}}>
                                    <select className="cooperation_describe" id="cooperation_describe" style={{width:"110px"}} onChange={this.props.gitUserId}>
                                        <option></option>
                                        {oPosition}
                                    </select>
                                </div>
                            </div>
                        </div>
                        <div style={{width:"100%",height:"auto",marginTop:"10px"}}>
                            <div style={{display:"inline-block",marginLeft:"20px",fontWeight:"bold"}}>推送原因&nbsp;&nbsp;</div>
                            <div style={{display:"inline-block",width:"70%"}}><input type="text" className="cooperation_describe1" placeholder="限200字以内" style={{width:"100%",height:"44px"}}/></div>
                        </div>
                        <div className="cooperation_choice" style={{width:"100%",height:"auto",margin:"10px 0 0 0"}}>
                            <div><span  onClick={this.props.giveDoneOnclick} style={{cursor:"pointer"}}>确定</span><span onClick={this.props.giveFailOnclick} style={{cursor:"pointer"}}>取消</span></div>
                        </div>
                    </div>
                </div>
            )
        }else{
            return (
                <div className="cooperation_alert" style={{width:"100%",height:"100%",background:"rgba(0,0,0,0.7)",position:"absolute",top:"0px",left:"0px",display:"none"}} >
                    <div style={{position:"absolute",left:"50%",top:"50%",width:"420px",height:"260px",marginLeft:"-210px",marginTop:"-155px",background:"white",border:"1px solid #dddee3"}}>
                        <div style={{lineHeight:"34px",height:"34px",width:"100%",background:"#eff2f9",borderBottom:"1px solid #d2d8e4"}}><span style={{float:"left",marginLeft:"20px",color:"#55678d"}}>编辑研判主题信息</span><span onClick={this.props.editFailOnclick} style={{float:"right",marginRight:"20px",cursor:"default",width:"10px",height:"11px",display:"inline-block",background:"url('./../../images/none.png')",marginTop:"11.5px"}}>&nbsp;</span></div>
                        <div style={{width:"100%",height:"50px",marginTop:"30px"}}>
                            <div style={{display:"inline-block",marginLeft:"20px",fontWeight:"bold"}}><span>研判主题&nbsp;&nbsp;</span></div>
                            <div style={{display:"inline-block",width:"70%"}}>
                                <input className="cooperation_describe" defaultValue={this.props.edit} type="text" style={{width:"100%"}}/>
                            </div>
                        </div>
                        <div style={{width:"100%",height:"auto",marginTop:"10px"}}>
                            <div style={{display:"inline-block",marginLeft:"20px",fontWeight:"bold"}}>主题描述&nbsp;&nbsp;</div>
                            <div style={{display:"inline-block",width:"70%"}}><input type="text"  defaultValue={this.props.der} className="cooperation_describe1" style={{width:"100%",height:"50px"}}/></div>
                        </div>
                        <div className="cooperation_choice" style={{width:"100%",height:"auto",margin:"30px 0 0 0"}}>
                            <div><span onClick={this.props.editDoneOnclick} style={{cursor:"pointer"}} >确定</span><span onClick={this.props.editFailOnclick} style={{cursor:"pointer"}}>取消</span></div>
                        </div>
                    </div>
                </div>
            );
        }

    }
});

export default CooperationAlert;



