import { React,dialog,PureRenderMixin } from '../../common/Util';
import CaseSingleView  from './CaseSingleView';

const Case = React.createClass({
    mixins:[PureRenderMixin],
    componentDidMount: function () {

    },
	typeOnclick:function(){
		scopaConfig.pages.cooperation.getCooperationDataByPhotos("");
		mining.utils.hashChange("cooperation");
	},
	silingOnClick:function(e){
		let ids = e.target.dataset.ids;
		let names = e.target.dataset.names;
		let descriptions = e.target.dataset.descriptions;
		let home_authority = this.props.home_authority;
		scopaConfig.pages.cooperation.getCooperationDataByPhotos(ids,names,descriptions);
		scopaConfig.pages.cooperation.getCooperationSnapshotByPhotos(home_authority,ids);
		mining.utils.hashChange("cooperation");
	},
    render: function () {

		let otr = [];
		let bodyDatassss = this.props.bodyDatassss;


		bodyDatassss.forEach((item, index)=> {
			otr.push(
				<CaseSingleView
					typeOnclick={this.typeOnclick}
					id={index}
					key = {index}
					infor = {item}
					silingOnClick={this.silingOnClick}
					userId = {this.props.home_authority}
				/>
			)

		})

        return (
            <div className = "Case">
            	<div className = "top_bar"><span>协同作战&nbsp;研判主题</span><a onClick={this.typeOnclick} data-logid="104" data-logcontent="协同作战 研判主题-更多" className = "arrow hand scopaicon-gengxuoxiangyou"></a></div>
            	<table className = "all_table table_Case">
            		<thead><tr style={{textAlign:"center"}}><td style={{width:"30%"}}>最新快照</td><td style={{width:"25%"}}>案件名称</td><td style={{width:"15%"}}>作者</td><td style={{width:"30%"}}>更新时间</td></tr></thead>
            		<tbody>
						{otr}
            		</tbody>
            	</table>
            </div>
        );
    }
});

export default Case;


