import * as actionTypes from '../actions/actionTypes';

export const chooseTimeSpanAnalyseReducer = (state = actionTypes.JUDGED_ANALYSE_CHOOSETIMESPAN.ONE, action)=> {
    switch (action.type) {
        case actionTypes.SET_JUDGED_ANALYSE_CHOOSETIMESPAN:
            return action.filter;
        default:
            return state;
    }
};


