import {React, connect, createSelector, Link, Tools} from '../../common/Util';
import actionCreator from '../../login/actions/actionCreator'
import HeaderView from './HeaderView';
import HomeView from '../../home/views/MainHomeView';
import ApplicationView from '../../application/views/MainApplicationView';
import RetrievalView from '../../retrieval/views/MainRetrievalView';
import JudgedView from '../../judged/views/MainJudgedView';
import ArchivesView from '../../archives/views/MainArchivesView';
import CooperationView from '../../cooperation/views/MainCooperationView';

const MainAppView = React.createClass({
    contextTypes: {
        router: React.PropTypes.object
    },
    componentDidMount: function () {
        //校验是否登录
        // this.props.dispatch(actionCreator.checkAuthority((oUser)=> {
        //     if (oUser) {
        //         dispatch(actionCreator.setUserName(oUser.name));
        //     } else {
        //         //没有权限跳转到登录页
        //         this.context.router.replace('/');
        //     }
        // }));
    },
    logout: function () {
        let {dispatch} = this.props;
        dispatch(actionCreator.logout(()=> {
            this.context.router.replace('/');
        }));
    },
    render: function () {
        let {pathname, username} = this.props;
        let chiefCls = "";
        let result = null;

        if (pathname == "/") {
            result = <div id="chief" className="login">{this.props.children}</div>;
        } else {
            chiefCls = pathname.substring(1);
            //根据active的值设置某个模块的显示样式
            let setClsName = clsName=> {
                if (chiefCls.startsWith(clsName)) {
                    return clsName;
                } else {
                    return clsName + ' hide';
                }
            };

            result =
                <div className="hasmain">
                    <HeaderView logout={this.logout} username={username}/>
                    <div id="chief">
                        <div className={setClsName('home')}><HomeView /></div>
                        <div className={setClsName('application')}><ApplicationView /></div>
                        <div className={setClsName('retrieval')}><RetrievalView /></div>
                        <div className={setClsName('judged')}><JudgedView /></div>
                        <div className={setClsName('archives')}><ArchivesView /></div>
                        <div className={setClsName('cooperation')}><CooperationView /></div>
                    </div>
                </div>;
        }

        return (<div className="containerchild">{result}</div>);
    }
});

const pathname = state => {
    return state.routing.locationBeforeTransitions.pathname;
};

const username = state=> {
    return state.login_username;
}

const appData = createSelector([pathname, username], (pathname, username)=> {
    return {
        pathname: pathname,
        username: username
    }
});

export default connect(appData)(MainAppView);


