import {$} from '../../common/Util';
import BaseService from '../../base/ServiceBase';

export default class HomeService extends BaseService {
    static getInformation(applicantId) {
        let URL="tuning/services/sjz/gongan/index/warning/list?applicantId="+applicantId+"&controlNum=0&forewarnNum=8&covertNum=8";
        //return $.get('../simulates/home_informations.json');
        return this.getWithParameter(URL);
    }
    static getInstruction(applicantId) {
        let URL = "tuning/services/sjz/gongan/index/qingbao/list?applicantId="+applicantId+"&topNum=12";
        //return $.get('../simulates/home_instructions.json');
        return this.getWithParameter(URL);
    }
    static getCase(userId) {
        let URL = "tuning/services/core/snapshot/getThemePatten?userId="+userId+"&pageNo=1&pageSize=3"
        //http://k1269.mlamp.co/tuning/services/core/snapshot/getThemePatten?userId=admin&pageNo=1&pageSize=3
        return this.getWithParameter(URL);
        //return $.get('../simulates/home_cases.json');
    }
    static getPeople(applicantId) {
        let URL = "tuning/services/sjz/gongan/index/person/list?applicantId="+applicantId+"&topNum=6"
        //return $.get('../simulates/home_people.json');
        return this.getWithParameter(URL);
    }
    static getPerson(sfzh) {
        let URL = "tuning/services/core/query/getVertexByIndex?value="+sfzh+"&index=key"
        return this.getWithParameter(URL);
    }
    static getMap() {
        let URL = 'tuning/services/sjz/gongan/alarm/districts?type=15'
        return this.getWithParameter(URL);
        //return $.get('../simulates/home_map.json');
    }
    static getMapNew() {
        let URL = 'tuning/services/sjz/gongan/alarm/places?type=15'
        return this.getWithParameter(URL);
        //return $.get('../simulates/home_map.json');
    }
    static getHomeAuthority(){
        return this.getWithParameter('tuning/services/pass/getUserInfo');
    }
}



