import { React,dialog,PureRenderMixin } from '../../common/Util';
import LinkSingleView  from './LinkSingleView';

const Links = React.createClass({
    mixins:[PureRenderMixin],
    componentDidMount: function () {

    },
    render: function () {
		let otr = [];
		let bodyDatassssss = scopaConfig.linkArr;

		bodyDatassssss.forEach((item, index)=> {
			otr.push(
				<LinkSingleView
					id    = {index}
					key   = {index}
					infor = {item}
				/>
			)

		})
        return (
            <div className = "Link">
            	<div className = "top_bar">常用网址链接</div>
				{otr}
            </div>
        );
    }
});

export default Links;


