import {React, dialog, PureRenderMixin, $} from '../../common/Util';
require('moment');
require('../../plugins/daterangepicker');

const CooperationMainSelect = React.createClass({
    mixins: [PureRenderMixin],
    componentDidMount: function () {
        let {timeOnChange} = this.props;
        $('input[name="reservationes"]').daterangepicker(
            {
                "opens": "right",
                "format":'YYYY-MM-DD',
                "applyClass": 'btn-primary',
                "clearClass": 'btn-primary',
                "timePicker": true,
                "timePicker24Hour": true,
                "timePickerSeconds": true
            },
            timeOnChange
        ).on('clear.daterangepicker', function(){
            $(this).val('');
        });

    },
    render: function () {
        let Number = 0;
        if(this.props.pageNo*4>this.props.cooNum){
            Number=this.props.cooNum;
        }else{
            Number=this.props.pageNo*4;
        }
        if(Number<10){
            Number = "0" + Number;
        }
        let cooNum = this.props.cooNum;
        if(cooNum<10){
            cooNum = "0" + cooNum;
        }

        return (
            <div className="cooperation_main_select clearfix">
                <div className="cooperation_main_select_theme">
                    <select name="select_theme hand" data-logid="116" data-logcontent="主题筛选" id="cooperation_main_select_theme" style={{cursor:"pointer"}} value={this.props.themeValue} onChange={this.props.themeOnChange}>
                        <option value="1" >全部主题</option>
                        <option value="2" >我的主题</option>
                        <option value="3" >已关注主题</option>
                    </select>
                </div>
                <div className="dateranger cooperation_main_select_time">
                    <div className="daterangebox pull-left hand"　>
                        <input type="text" name="reservationes"  data-logid="109" data-logcontent="协作-时间检索" className="daterangebox-ipt" placeholder="按周期模糊搜索" value={this.props.timeValue} style={{cursor:"default"}}/>
                    </div>
                </div>
                <div className="keysearch cooperation_main_select_key">
                    <div className="searchbox pull-left">
                        <input type="text" name="keyword" style={{lineHeight:"22px"}} data-logid="108" data-logcontent="协作-关键字检索" ref="keyword" onChange={this.props.keyOnChange} onKeyUp={this.props.keyOnClick}  className="searchbox-ipt" value={this.props.keyword} placeholder="请输入关键字查询"/>
                        <a　style={{cursor:"pointer"}} className="searchbox-btn" href="javascript:;"  data-logid="108" data-logcontent="协作-关键字检索" onClick={this.props.keyOnClick}></a>
                    </div>
                </div>
                <div className="cooperation_main_data_count">共&nbsp;<span>{cooNum}</span>&nbsp;个&nbsp;当前显示至&nbsp;<span>{Number}</span></div>
            </div>
        );
    }
});

export default CooperationMainSelect;


