import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const homeListNewMapReducer = (state = Immutable.fromJS({}), action)=> {
    switch (action.type) {
        case actionTypes.SET_HOME_NEW_MAP:
            return Immutable.fromJS(action.home_maps);
        default:
            return state;
    }
};


