import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const homeListReducer1 = (state = Immutable.fromJS([]), action)=> {
    switch (action.type) {
        case actionTypes.SET_HOME_INSTRUCTION:
            return Immutable.fromJS(action.bodyDatass);
        default:
            return state;
    }
};



