import {$} from '../../common/Util';
import ServiceBase from '../../base/ServiceBase';

export default class JudgedService extends ServiceBase {
    /**
     * 根据ids和type话单数据
     * @param oSettings
     *        ids:["13801234567_phoneNO","13901234567_phoneNO"]
     *        type:phone_event
     * @returns {*|V}
     */
    static getAll(oSettings) {
        return this.postWithParameter('tuning/services/core/archive/getEventsByTypeAndIds', oSettings);
        // return $.get('../simulates/judged_ticketData.json');
    }
}


