import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const cooCheckAttrEyesReducer = (state = Immutable.fromJS([]), action)=> {
    switch (action.type) {
        case actionTypes.CHECK_ATTENTION_TO:
            return state.merge(Immutable.fromJS(action.checkResult));
        default:
            return state;
    }
};


