import * as actionTypes from './actionTypes';
import RetrievalService from '../service/RetrievalService';

const actionCreator = {
    getSearchResultList: function (searchBody) {
        return dispatch=> {
            RetrievalService.getSearchResultList(searchBody).done(resultData=>{
                if(resultData.statusCode == 200){
                    dispatch(this.setSearchResultList(resultData,resultData))
                }
            });
        }
    },

    setSearchResultList:function(resultData){
        return{
            type:actionTypes.SET_Search_Result_List,
            resultData
        }
    }
};
export default actionCreator;




