import {React, Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

const ToolbarView = React.createClass({
    gotoGraph: function () {
        let list = this.props.list.filter(item=>item.get('checked')).map(item=>item.get('dfhm') + '_phoneNO');
        list = Immutable.Set(list).toJS();
        mining.utils.showPhoneGraph(list, 'graph');
    },
    gotoMapping: function () {
        let list = this.props.list.filter(item=>item.get('checked')).map(item=>item.get('key')).toJS();
        list = Immutable.Set(list).toJS();
        mining.utils.showPhoneGraph(list, 'map');
    },
    render: function () {
        let toTopCls = 'totop';
        if (this.props.merge) {
            toTopCls += ' hide';
        }
        if (this.props.checkedToTop) {
            toTopCls += ' active';
        }
        return (
            <div className="toolbar">
                <div className="backoff" title="后退" data-logid="200" data-logcontent="话单--页面上方工具栏里各个图标的点击操作-后退" onClick={this.props.changeStepBack}></div>
                <div className="forward" title="前进" data-logid="201" data-logcontent="话单--页面上方工具栏里各个图标的点击操作-前进" onClick={this.props.changeStepForward}></div>
                <div className={this.props.merge ? "highlight hide" : "highlight"} title="高亮选中" data-logid="202" data-logcontent="话单--页面上方工具栏里各个图标的点击操作-高亮选中"
                     onClick={this.props.setHighlightChecked}></div>
                <div className={!this.props.merge ? "combine" : "combine active"}
                     title={!this.props.merge ? "合并" : "分解"} data-logid="203" data-logcontent="话单--页面上方工具栏里各个图标的点击操作-合并分解"
                     onClick={()=>this.props.changeMerge(!this.props.merge)}></div>
                <div className={toTopCls} title="选中置顶" data-logid="204" data-logcontent="话单--页面上方工具栏里各个图标的点击操作-选中置顶"
                     onClick={this.props.setCheckedToTop}></div>
                <div className="delete" title="删除" data-logid="205" data-logcontent="话单--页面上方工具栏里各个图标的点击操作-删除" onClick={this.props.delChecked}></div>
                <div className="oppositeDelete" title="反向删除" data-logid="206" data-logcontent="话单--页面上方工具栏里各个图标的点击操作-反向删除" onClick={this.props.delReverseChecked}></div>
                <div className={this.props.merge ? 'setmachinephone hide' : 'setmachinephone'} title="设为本机" data-logid="207" data-logcontent="话单--页面上方工具栏里各个图标的点击操作-设为本机"
                     onClick={this.props.getMachinePhoneData}></div>
                <div className="rightbtns">
                    <button title="到图析" className="btn-current" data-logid="208" data-logcontent="话单--页面上方工具栏里各个图标的点击操作-到图析" onClick={this.gotoGraph}>到图析</button>
                    <button title="到轨迹" className="btn-current" data-logid="209" data-logcontent="话单--页面上方工具栏里各个图标的点击操作-到地图" onClick={this.gotoMapping}>到轨迹</button>
                </div>
            </div>
        );
    }
});

export default ToolbarView;


