import {React, connect, PureRenderMixin,createSelector} from '../../common/Util';
import actionCreator from '../actions/actionCreator';
import Select2 from 'react-select2';

const AttrSelect2 = React.createClass({
    mixins: [PureRenderMixin],
	getInitialState:function(){
		return{
			attrClickData:[]
		}
	},

	addAttrClick:function(e){
		const {attrData} = this.state;
		let attrObject = this.props.attrObject;
		let selectData = ['请选择'];
		if(attrObject){
			for(var i in attrObject){
				let selectJson = {};
				if(attrObject[i].property){
					selectJson.id=attrObject[i].property;
					selectJson.text = mining.mappingutils.getPropLabel(attrObject[i].property);
					selectData.push(selectJson);
				}
			}
		}

		let attrClickData = this.state.attrClickData;
		let attrHtml = (
			<li>
				<span className="left">
					<Select2
						data={selectData}
						onChange={this.cbChange}
					/>
				</span>
				<span className="left">
					<Select2
						data={ attrData }
						onChange={this.cbSelect}
					/>
				</span>
				<span className="left">
					<a className="click left add">+</a>
					<a className="click left down">-</a>
				</span>
			</li>
		);
	},

    render: function () {
		// let attrData = this.props.attrData;

		// let selectData = ['请选择'];
		// let attrObject = this.props.attrObject;
		// if(attrObject){
		// 	for(var i in attrObject){
		// 		let selectJson = {};
		// 		if(attrObject[i].property){
		// 			selectJson.id=attrObject[i].property;
		// 			selectJson.text = mining.mappingutils.getPropLabel(attrObject[i].property);
		// 			selectData.push(selectJson);
		// 		}
		// 	}
		// }
		let attrData = this.props.attrData;
		let selectData = this.props.selectData;
		let defaultValue = this.props.defaultValue;

        return (
        	<div>
				<li>
					<span className="left">
						<Select2
							data={selectData}
							onChange={this.props.cbChange}
							defaultValue={defaultValue}
						/>
					</span>

					<span className="left">
						<Select2
							data={ attrData }
							onChange={this.props.cbSelect}
							defaultValue={0}
						/>
					</span>
					<span className="left">
						<a className="click left add">+</a>
						<a className="click left down">-</a>
					</span>
				</li>
				{this.state.attrClickData}
			</div>
		);
    }
});

export default AttrSelect2;


