import { React,dialog,PureRenderMixin } from '../../common/Util';
import PeopleSingleView  from './PeopleSingleView';

const People = React.createClass({
    mixins:[PureRenderMixin],
    componentDidMount: function () {

    },
	typeOnclick:function(e){
		mining.utils.hashChange("control");
	},
    render: function () {
		let otr = [];
		let bodyDatassssssss = this.props.bodyDatassssssss;
		let personArr = this.props.personArr;

		bodyDatassssssss.forEach((item, index)=> {
			let person = personArr[index];
			otr.push(
				<PeopleSingleView
					person={person}
					click={this.typeOnclick}
					id={index}
					key = {index}
					infor = {item}
				/>
			)

		})
        return (
            <div className = "People">
            	<div className = "top_bar"><span>人员关注</span><a className = "arrow hand scopaicon-gengxuoxiangyou" onClick={this.typeOnclick} data-logid="106" data-logcontent="人员关注-更多"></a></div>
				{otr}
            </div>
        );
    }
});

export default People;


