import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const cooMain = (state = Immutable.fromJS([]), action)=> {
    switch (action.type) {
        case actionTypes.SET_COOPERATION_MAIN:
            return Immutable.fromJS(action.cooperationMain);
        default:
            return state;
    }
};


