import {React, $, Tools, dialog, connect, createSelector, Immutable} from "../../common/Util";
import * as actionTypes from "../actions/actionTypes";
import actionCreator from "../actions/actionCreator";
import NavView from "./NavView";
import GraphView from "./GraphView";
import MapView from "./MapView";
import TicketView from "./TicketView";
import Methods from "../others/Methods";

const MainJudgedView = React.createClass({
    contextTypes: {
        router: React.PropTypes.object
    },
    componentDidMount: function () {
        if (location.hash == '#!scopa/ticket') {
            let ids = window.sessionStorage.getItem('judgedListPhones');
            if (ids) {
                ids = JSON.parse(ids);
                //触发得到所有数据
                this.getAllByPhones(ids, 'reload');
            }
        }
        scopaConfig.pages.judged.getJudgedDataByPhones = this.getAllByPhones;
    },
    changeShowListCount: function (count) {
        this.props.dispatch(actionCreator.setShowListCount(count));
    },
    changeKssjSort: function (sort) {
        let {dispatch} = this.props;
        this.resetFilters();
        dispatch(actionCreator.setThscSort(actionTypes.JUDGED_TABLE_SORT.DEFAULT));
        dispatch(actionCreator.setKssjSort(sort));
        this.resetStep();
    },
    changeThscSort: function (sort) {
        let {dispatch} = this.props;
        this.resetFilters();
        dispatch(actionCreator.setKssjSort(actionTypes.JUDGED_TABLE_SORT.DEFAULT));
        dispatch(actionCreator.setThscSort(sort));
        this.resetStep();
    },
    changeThschSort: function (sort) {
        let {dispatch} = this.props;
        this.resetMergeTableTitleSort();
        dispatch(actionCreator.setThschSort(sort));
        this.resetStep();
    },
    changeThcshSort: function (sort) {
        let {dispatch} = this.props;
        this.resetMergeTableTitleSort();
        dispatch(actionCreator.setThcshSort(sort));
        this.resetStep();
    },
    changeThcshSensitiveSort: function (sort) {
        let {dispatch} = this.props;
        this.resetMergeTableTitleSort();
        dispatch(actionCreator.setThcshSensitiveSort(sort));
        this.resetStep();
    },
    resetMergeTableTitleSort: function () {
        this.props.dispatch(actionCreator.setThschSort(actionTypes.JUDGED_TABLE_SORT.DEFAULT));
        this.props.dispatch(actionCreator.setThcshSort(actionTypes.JUDGED_TABLE_SORT.DEFAULT));
        this.props.dispatch(actionCreator.setThcshSensitiveSort(actionTypes.JUDGED_TABLE_SORT.DEFAULT));
    },
    resetFilters: function (bSort) {
        let {dispatch} = this.props;
        console.log('bSort=', bSort);
        //重置表格排序
        if (!bSort) {
            dispatch(actionCreator.setThscSort(actionTypes.JUDGED_TABLE_SORT.DEFAULT));
            dispatch(actionCreator.setKssjSort(actionTypes.JUDGED_TABLE_SORT.DEFAULT));
        }

        //重置表格排序
        if(!bSort){
            dispatch(actionCreator.setThscSort(actionTypes.JUDGED_TABLE_SORT.DEFAULT));
            dispatch(actionCreator.setKssjSort(actionTypes.JUDGED_TABLE_SORT.DEFAULT));
        }
        this.resetMergeTableTitleSort();

        //重置列表数量
        dispatch(actionCreator.setShowListCount(actionTypes.JUDGED_TABLE_SHOWCOUNT));

        //重置checktoTop
        dispatch(actionCreator.setCheckedToTop(false));
        //重置右侧统计面板所有的filter
        dispatch(actionCreator.setStatisticBjhm(''));
        dispatch(actionCreator.setStatisticDfhm(''));
        dispatch(actionCreator.setStatisticXqh(''));
        dispatch(actionCreator.setStatisticJzh(''));
        //分析面板
        dispatch(actionCreator.setAnalyseBjhm(actionTypes.JUDGED_ANALYSE_BJHM));
        dispatch(actionCreator.setAnalyseChooseType(actionTypes.JUDGED_ANALYSE_CHOOSETYPE.NEWPHONE));
        dispatch(actionCreator.setAnalyseChooseTime(actionTypes.JUDGED_ANALYSE_CHOOSETIME));
        dispatch(actionCreator.setAnalyseChooseTimeSpan(actionTypes.JUDGED_ANALYSE_CHOOSETIMESPAN.ONE));
        dispatch(actionCreator.setAnalyseDfhm('', []));
        //统计面板
        dispatch(actionCreator.setAnalyseMergeBjhm(actionTypes.JUDGED_ANALYSE_BJHM));
        //下方日期
        dispatch(actionCreator.setPeriodFilter('', []));
    },
    changeRightTitleActive: function (activeTabName) {
        //修改右侧面板的激活状态
        let {dispatch} = this.props;
        dispatch(actionCreator.setRightTitleActive(activeTabName));

        //根据面板属性，清空兄弟面板的filter
        switch (activeTabName) {
            case actionTypes.JUDGED_RIGHT_TITLE_ACTIVE.STATISTICS:
                dispatch(actionCreator.setAnalyseBjhm(actionTypes.JUDGED_ANALYSE_BJHM));
                dispatch(actionCreator.setAnalyseChooseType(actionTypes.JUDGED_ANALYSE_CHOOSETYPE.NEWPHONE));
                dispatch(actionCreator.setAnalyseChooseTime(actionTypes.JUDGED_ANALYSE_CHOOSETIME));
                dispatch(actionCreator.setAnalyseChooseTimeSpan(actionTypes.JUDGED_ANALYSE_CHOOSETIMESPAN.ONE));
                dispatch(actionCreator.setAnalyseDfhm("", []));
                dispatch(actionCreator.setAnalyseMergeBjhm(actionTypes.JUDGED_ANALYSE_BJHM));
                break;
            case actionTypes.JUDGED_RIGHT_TITLE_ACTIVE.ANALYSE:
                dispatch(actionCreator.setStatisticBjhm(''));
                dispatch(actionCreator.setStatisticDfhm(''));
                dispatch(actionCreator.setStatisticXqh(''));
                dispatch(actionCreator.setStatisticJzh(''));
                break;
        }
        this.resetStep();
    },
    changePeriod: function (value) {
        //切换下面周期的选项
        let {dispatch}=this.props;
        //重置checkedTotop
        this.resetCheckedToTop();
        dispatch(actionCreator.setPeriod(value));
        this.resetStep();
    },
    changePeriodDateFilter: function (filter, aData) {
        //修改周期区域的选中日期
        let {dispatch}=this.props;
        //重置checkedTotop
        this.resetCheckedToTop();

        dispatch(actionCreator.setPeriodFilter(filter, aData));
        this.resetStep();
    },
    changeStatisticBjhmFilter: function (filter) {
        //修改本地号码
        let {dispatch}=this.props;
        //重置checkedTotop
        this.resetCheckedToTop();
        dispatch(actionCreator.setStatisticBjhm(filter));
        this.resetStep();
    },
    changeStatisticDfhmFilter: function (filter) {
        //修改对端号码
        let {dispatch}=this.props;
        //重置checkedTotop
        this.resetCheckedToTop();
        dispatch(actionCreator.setStatisticDfhm(filter));
        this.resetStep();
    },
    changeStatisticXqhFilter: function (filter) {
        //修改逻辑小区号
        let {dispatch}=this.props;
        //重置checkedTotop
        this.resetCheckedToTop();
        dispatch(actionCreator.setStatisticXqh(filter));
        this.resetStep();
    },
    changeStatisticJzhFilter: function (filter) {
        //修改基站小区号
        let {dispatch}=this.props;
        //重置checkedTotop
        this.resetCheckedToTop();
        dispatch(actionCreator.setStatisticJzh(filter));
        this.resetStep();
    },
    changeBjhmAnalyseFilter: function (filter) {
        //修改分析面板本机号码
        let {dispatch}=this.props;
        //重置checkedTotop
        this.resetCheckedToTop();

        dispatch(actionCreator.setAnalyseBjhm(filter));
        //清空对端号码选择条件
        dispatch(actionCreator.setAnalyseDfhm("", []));
        this.resetStep();
    },
    changeBjhmMergeAnalyseFilter: function (filter) {
        //修改分析面板本机号码
        let {dispatch}=this.props;
        dispatch(actionCreator.setAnalyseMergeBjhm(filter));
        //清空高亮选中
        dispatch(actionCreator.setDfhmByPolyLine(Immutable.List()));
        this.resetStep();
    },
    changeChooseTypeAnalyseFilter: function (filter) {
        //修改分析面板筛选条件类型
        let {dispatch}=this.props;
        //重置checkedTotop
        this.resetCheckedToTop();
        dispatch(actionCreator.setAnalyseChooseType(filter));
        //清空对端号码选择条件
        dispatch(actionCreator.setAnalyseDfhm("", []));
        this.resetStep();
    },
    changeChooseTimeAnalyseFilter: function (filter) {
        //修改分析面板时间选择
        let {dispatch}=this.props;
        //重置checkedTotop
        this.resetCheckedToTop();
        dispatch(actionCreator.setAnalyseChooseTime(filter));
        //清空对端号码选择条件
        dispatch(actionCreator.setAnalyseDfhm("", []));
        this.resetStep();
    },
    changeChooseTimeSpanAnalyseFilter: function (filter) {
        //修改分析面板时间区域选择
        let {dispatch}=this.props;
        //重置checkedTotop
        this.resetCheckedToTop();
        dispatch(actionCreator.setAnalyseChooseTimeSpan(filter));
        //清空对端号码选择条件
        dispatch(actionCreator.setAnalyseDfhm("", []));
        this.resetStep();
    },
    changeAnalyseDfhmFilter: function (filter, aData) {
        //修改分析面板对端号码
        let {dispatch}=this.props;
        //重置checkedTotop
        this.resetCheckedToTop();
        dispatch(actionCreator.setAnalyseDfhm(filter, aData));
        this.resetStep();
    },
    setSingleChecked: function (ids) {
        this.resetCheckedToTop();

        //设置单条数据checkbox的状态
        let {dispatch}=this.props;
        if (ids.size > 0) {
            dispatch(actionCreator.setSingleChecked(ids));
            this.resetStep();
        }
    },
    setCheckedAll: function (checked) {
        //设置全选
        let {dispatch}=this.props;
        dispatch(actionCreator.setCheckedAll(checked));
        this.resetStep();
    },
    changeMerge: function (status) {
        if (this.props.originalList.size != 0) {
            this.resetFilters();
            //右侧自动切换到分析面板
            this.changeRightTitleActive(actionTypes.JUDGED_RIGHT_TITLE_ACTIVE.ANALYSE);
            this.props.dispatch(actionCreator.setMerge(status));

            if (status) {
                this.changeShowListCount(actionTypes.JUDGED_TABLE_SHOWCOUNT);
            }
        }
    },
    setHighlightChecked: function () {
        let aHighlight = this.props.judgedList.get('aHighlight');
        if (aHighlight.size > 0) {
            this.setSingleChecked(aHighlight.groupBy(item=>item.get('id')).keySeq());
        }
        this.resetStep();
    },
    setCheckedToTop: function () {
        if (this.props.originalList.size != 0) {
            //选中置顶
            this.resetFilters(true);
            let {dispatch, checkedToTop}=this.props;
            dispatch(actionCreator.setCheckedToTop(!checkedToTop));
            this.resetStep();
        }
    },
    resetCheckedToTop: function () {
        let {dispatch, checkedToTop}=this.props;
        if (checkedToTop) {
            dispatch(actionCreator.setCheckedToTop(false));
        }
        this.resetStep();
    },
    delChecked: function () {
        //删除选中
        let {dispatch}=this.props;
        let that = this;
        let aMap = this.props.originalList.groupBy(item=>item.get('checked')).get(true);
        if (aMap) {
            dialog({
                title: '删除',
                content: '确定要删除吗？',
                okValue: '确定',
                ok: function () {
                    that.resetFilters();
                    dispatch(actionCreator.delChecked());
                },
                cancelValue: '取消',
                cancel: function () {
                }
            }).showModal();
        } else {
            dialog.alert('没有选择任何数据！');
        }
        this.resetStep();
    },
    delReverseChecked: function () {
        //反向删除选中
        let {dispatch}=this.props;
        let that = this;
        if (this.props.originalList.size != 0) {
            dialog({
                title: '删除',
                content: '确定要删除吗？',
                okValue: '确定',
                ok: function () {
                    that.resetFilters();
                    dispatch(actionCreator.delReverseChecked());
                },
                cancelValue: '取消',
                cancel: function () {
                }
            }).showModal();

            this.resetStep();
        }
    },
    getAllByPhones: function (phones, isClear) {
        this.resetFilters();
        if (this.props.merge) {
            this.changeMerge(false);
        }
        let aJudgedListPhones = window.sessionStorage.getItem('judgedListPhones');
        let fnFirst = ()=> {
            window.sessionStorage.setItem('judgedListPhones', JSON.stringify(phones));
            this.props.dispatch(actionCreator.getAll({
                ids: JSON.stringify(phones),
                type: "phone_event",
                isClear: isClear
            }));
        }

        //isClear=true||reload
        if (isClear) {
            fnFirst();
        } else {
            if (aJudgedListPhones) {
                aJudgedListPhones = JSON.parse(aJudgedListPhones);
                //保存未查询过的电话号码
                let aResultPhones = [];
                //检验本机号码是否已经查询过
                phones.forEach(function (phone) {
                    if (aJudgedListPhones.indexOf(phone) == -1) {
                        aResultPhones.push(phone);
                    }
                });

                if (aResultPhones.length > 0) {
                    aJudgedListPhones = aJudgedListPhones.concat(aResultPhones);
                    window.sessionStorage.setItem('judgedListPhones', JSON.stringify(aJudgedListPhones));
                    this.props.dispatch(actionCreator.getAll({
                        ids: JSON.stringify(aResultPhones),
                        type: "phone_event",
                        isClear: isClear
                    }));
                }
            } else {
                fnFirst();
            }
        }

        this.resetStep();
    },
    getMachinePhoneData: function () {
        let aCheckedDfhm = this.props.originalList.groupBy(item=>item.get('checked')).get(true);
        let aBjhm = this.props.originalList.groupBy(item=>item.get('bjhm')).keySeq().toSet();
        if (aCheckedDfhm) {
            let aDfhm = aCheckedDfhm.groupBy(item=>item.get('dfhm')).keySeq().toSet();
            aDfhm = aDfhm.subtract(aBjhm);

            if (aDfhm.size > 0) {
                this.getAllByPhones(aDfhm.map(item=>item + '_phoneNO').toJS(), false);
            } else {
                dialog.alert('对端号码已在列表中！');
            }
        } else {
            dialog.alert('没有选择任何数据！');
        }
    },
    gotoGraph: function () {
        //到图析
        this.context.router.push('/judged/graph');
        //当前选中的通话事件中的「对端号码」作为实体
        let aCheckedDfhm = this.getDfhmByData(this.props.originalList);
        console.log(aCheckedDfhm);
    },
    gotoMapping: function () {
        //到轨迹
        this.context.router.push('/judged/map');
        //当前选中的通话事件中的「对端号码」作为实体
        let aCheckedDfhm = this.getDfhmByData(this.props.originalList);
        console.log(aCheckedDfhm);
    },
    getDfhmByData: function (list) {
        let aCheckedDfhm = list.groupBy(item=>item.get('checked')).get(true);
        if (aCheckedDfhm) {
            return aCheckedDfhm.groupBy(item=>item.get('dfhm')).keySeq().toSet().toJS();
        } else {
            return [];
        }
    },
    changeStepForward: function () {
        if (this.props.originalList.size != 0) {
            if (this.props.step != -1) {
                this.props.dispatch(actionCreator.setStepForward());
            }
        }
    },
    changeStepBack: function () {
        if (this.props.originalList.size != 0) {
            if (this.props.step != -5) {
                this.props.dispatch(actionCreator.setStepBack());
            }
        }
    },
    changeDfhmByPolyline: function (aData) {
        //根据平行坐标上线选中对端号码并高亮
        this.props.dispatch(actionCreator.setDfhmByPolyLine(aData));
    },
    resetStep: function () {
        let step = this.props.step;
        if (step != -1) {
            //清除aHistoryState数组中 当前step到-1之间的数据
            aHistoryState = aHistoryState.clear();
            this.props.dispatch(actionCreator.resetStep());
        }
    },
    render: function () {
        return (
            <div>
                <div className="navcontent ticket">
                    <TicketView
                        originalList={this.props.originalList}
                        list={this.props.judgedList}
                        step={this.props.step}
                        merge={this.props.merge}
                        changeMerge={this.changeMerge}
                        changeShowListCount={this.changeShowListCount}
                        listShowCount={this.props.listShowCount}
                        kssjSort={this.props.kssjSort}
                        thscSort={this.props.thscSort}
                        thschSort={this.props.thschSort}
                        thcshSort={this.props.thcshSort}
                        thcshSensitiveSort={this.props.thcshSensitiveSort}
                        changeKssjSort={this.changeKssjSort}
                        changeThscSort={this.changeThscSort}
                        changeThschSort={this.changeThschSort}
                        changeThcshSort={this.changeThcshSort}
                        changeThcshSensitiveSort={this.changeThcshSensitiveSort}
                        setSingleChecked={this.setSingleChecked}
                        setCheckedAll={this.setCheckedAll}
                        titleActive={this.props.rightTActive}
                        changeRightTitleActive={this.changeRightTitleActive}
                        periodActive={this.props.periodActive}
                        changePeriod={this.changePeriod}
                        changePeriodDateFilter={this.changePeriodDateFilter}
                        periodDateFilter={this.props.periodDateFilter}
                        bjhmStatisticFilter={this.props.bjhmStatisticFilter}
                        dfhmStatisticFilter={this.props.dfhmStatisticFilter}
                        xqhStatisticFilter={this.props.xqhStatisticFilter}
                        jzhStatisticFilter={this.props.jzhStatisticFilter}
                        changeStatisticBjhmFilter={this.changeStatisticBjhmFilter}
                        changeStatisticDfhmFilter={this.changeStatisticDfhmFilter}
                        changeStatisticXqhFilter={this.changeStatisticXqhFilter}
                        changeStatisticJzhFilter={this.changeStatisticJzhFilter}
                        bjhmAnalyseFilter={this.props.bjhmAnalyseFilter}
                        bjhmMergeAnalyseFilter={this.props.bjhmMergeAnalyseFilter}
                        changeBjhmMergeAnalyseFilter={this.changeBjhmMergeAnalyseFilter}
                        chooseTypeAnalyseFilter={this.props.chooseTypeAnalyseFilter}
                        chooseTimeAnalyseFilter={this.props.chooseTimeAnalyseFilter}
                        chooseTimeSpanAnalyseFilter={this.props.chooseTimeSpanAnalyseFilter}
                        dfhmAnalyseFilter={this.props.dfhmAnalyseFilter}
                        changeBjhmAnalyseFilter={this.changeBjhmAnalyseFilter}
                        changeChooseTypeAnalyseFilter={this.changeChooseTypeAnalyseFilter}
                        changeChooseTimeAnalyseFilter={this.changeChooseTimeAnalyseFilter}
                        changeChooseTimeSpanAnalyseFilter={this.changeChooseTimeSpanAnalyseFilter}
                        changeAnalyseDfhmFilter={this.changeAnalyseDfhmFilter}
                        dfhmByPolyline={this.props.dfhmByPolyline}
                        changeDfhmByPolyline={this.changeDfhmByPolyline}
                        changeStepForward={this.changeStepForward}
                        changeStepBack={this.changeStepBack}
                        setHighlightChecked={this.setHighlightChecked}
                        checkedToTop={this.props.checkedToTop}
                        setCheckedToTop={this.setCheckedToTop}
                        delChecked={this.delChecked}
                        delReverseChecked={this.delReverseChecked}
                        getMachinePhoneData={this.getMachinePhoneData}
                        gotoGraph={this.gotoGraph}
                        gotoMapping={this.gotoMapping}
                    />
                </div>
            </div>
        );
    }
});

//获得所有的状态处理函数
const getAllStateFns = state=> {
    return Methods.getStateArg(state);
};

//记录历史记录的步数
//保存历史数据
let aHistoryState = Immutable.fromJS([]);

const getJudgedState = createSelector(
    getAllStateFns(),
    (judgedList,
     rightTitleActive,
     periodActive,
     periodDateFilter,
     bjhmStatisticFilter,
     dfhmStatisticFilter,
     xqhStatisticFilter,
     jzhStatisticFilter,
     bjhmAnalyseFilter,
     chooseTypeAnalyseFilter,
     chooseTimeAnalyseFilter,
     chooseTimeSpanAnalyseFilter,
     dfhmAnalyseFilter,
     checkedToTop,
     kssjSort,
     thscSort,
     merge,
     step,
     thschSort,
     thcshSort,
     thcshSensitiveSort,
     bjhmMergeAnalyseFilter,
     dfhmByPolyline,
     listShowCount)=> {
        let oCur = {
            judgedList: Methods.getListByFilter(
                judgedList,
                rightTitleActive,
                periodActive,
                periodDateFilter,
                bjhmStatisticFilter,
                dfhmStatisticFilter,
                xqhStatisticFilter,
                jzhStatisticFilter,
                bjhmAnalyseFilter,
                chooseTypeAnalyseFilter,
                chooseTimeAnalyseFilter,
                chooseTimeSpanAnalyseFilter,
                dfhmAnalyseFilter,
                checkedToTop,
                kssjSort,
                thscSort,
                merge,
                thschSort,
                thcshSort,
                thcshSensitiveSort
            ),
            originalList: judgedList,
            kssjSort,
            thscSort,
            checkedToTop,
            rightTActive: rightTitleActive,
            periodActive,
            periodDateFilter,
            bjhmStatisticFilter,
            dfhmStatisticFilter,
            xqhStatisticFilter,
            jzhStatisticFilter,
            bjhmAnalyseFilter,
            chooseTypeAnalyseFilter,
            chooseTimeAnalyseFilter,
            chooseTimeSpanAnalyseFilter,
            dfhmAnalyseFilter,
            merge,
            step,
            thschSort,
            thcshSort,
            thcshSensitiveSort,
            bjhmMergeAnalyseFilter,
            dfhmByPolyline,
            listShowCount
        };

        let data = aHistoryState.get(step);
        data = data === undefined ? {} : data;
        if (step == -1 && !Immutable.fromJS(oCur).equals(Immutable.fromJS(data))) {
            aHistoryState = aHistoryState.push(oCur);
            if (aHistoryState.size > 5) {
                aHistoryState = aHistoryState.shift();
            }
        }

        data.step = step;
        return aHistoryState.get(step);
    });

export default connect(getJudgedState)(MainJudgedView);


