import { React,dialog,PureRenderMixin,$} from '../../common/Util';

const SnapshotComponentSelect = React.createClass({
    mixins:[PureRenderMixin],
    componentDidMount: function () {

    },
    render: function () {
        let giveOnclick = this.props.giveOnclick;
        return (
            <div className="select cooperation_main_select_theme hand">
                <select style={{cursor:"pointer"}} onChange={this.props.themeOnChange} data-logid="112" data-logcontent="快照类型筛选" name="select_theme" id="cooperation_main_select_theme">
                    <option value="1">全部快照</option>
                    <option value="2">我的快照</option>
                    <option value="3">推送的快照</option>
                    <option value="4">公共的快照</option>
                </select>
            </div>
        );
    }
});

export default SnapshotComponentSelect;



