import {React, Link} from '../../common/Util';

const HeaderView = React.createClass({
    getInitialState: function () {
        return {
            searchCls: ''
        }
    },
    searchAll: function () {
        let sValue = this.refs.searchsec.value;
        alert(sValue);
    },
    importHandler: function () {
        alert('导入开始~');
    },
    addInputActiveCls: function () {
        this.setState({
            searchCls: 'active'
        });
        this.refs.searchsec.focus();
    },
    removeInputActiveCls: function () {
        this.setState({
            searchCls: ''
        });
    },
    render: function () {
        return (
            <div id="header" className="clearfix">
                <div className="pull-left" id="logo"></div>
                <div className="pull-left navigation">
                    <ul role="nav" className="list-unstyled list-inline">
                        <li><Link to="/home" activeClassName="active">首页</Link></li>
                        <li><Link to="/application" activeClassName="active">应用</Link></li>
                        <li><Link to="/retrieval" activeClassName="active">检索</Link></li>
                        <li><Link to="/judged" activeClassName="active">研判</Link></li>
                        <li><Link to="/archives" activeClassName="active">档案</Link></li>
                        <li><Link to="/cooperation" activeClassName="active">协作</Link></li>
                    </ul>
                </div>
                <div className="pull-right headrright">
                    <div className="pull-right">
                        <span className="glyphicon glyphicon-off" onClick={this.props.logout}></span>
                    </div>
                    <div className="pull-right">
                        {this.props.username}
                    </div>
                    <div className="pull-right">
                        <div className="icon icon-importdata" onClick={this.importHandler}></div>
                    </div>
                    <div className="pull-right" onMouseEnter={this.addInputActiveCls}>
                        <input className={this.state.searchCls} ref="searchsec" onBlur={this.removeInputActiveCls}/>
                        <div className="icon icon-search2" onClick={this.searchAll}></div>
                    </div>
                </div>
            </div>
        );
    }
});

export default HeaderView;


