import {React,PureRenderMixin} from '../../common/Util';
const InstructionsSingleView = React.createClass({
    mixins:[PureRenderMixin],

    render: function () {
        let click = this.props.click;
        let infor = this.props.infor;
        return (
            <tr id={infor.get('id')} className="hand" onClick={click} data-logid="101" data-logcontent={infor.get('content')} data-content={infor.get('content')}>
                <td style={{width:"50%",paddingLeft:"20px"}} data-logid="101" data-logcontent={infor.get('content')} id={infor.get('id')} title={infor.get('content')}>{infor.get('content')}</td>
                <td style={{width:"50%"}} id={infor.get('id')} data-logid="101" data-logcontent={infor.get('content')} title={infor.get('time')}>{infor.get('time')}</td>
            </tr>
        );
    }
});

export default InstructionsSingleView;


