import {$,React, Tools,connect, PureRenderMixin,createSelector,dialog} from '../../common/Util';
import actionCreator from '../actions/actionCreator';
import FileInfoComponentDaterange from './FileInfoComponentDaterange';
import FileInfoComponentKeywordSearch from './FileInfoComponentKeywordSearch';
import Paginator from './Paginator';

const FileInforView = React.createClass({
    mixins:[PureRenderMixin],
    componentDidMount: function () {
    },
    handleLocation:function(e,bjhm){
        e.preventDefault();
        let str = bjhm.split(",");
        var arry=[];
        for(let i=0;i < str.length;i++){
            arry.push(str[i]+"_phoneNO");
        }
        dialog({
            title: '跳转设置',
            content: '是否清空原有话单分析？',
            okValue: '是Y',
            ok: function () {
                //若是，则清空研判，新添加到研判，且跳转至【研判-话单模式】
                scopaConfig.pages.judged.getJudgedDataByPhones(arry,true)
                location.hash = "#!scopa/ticket";
            },
            cancelValue: '否N',
            cancel: function () {
                //若否，则追加数据，且跳转至【研判-话单模式】
                scopaConfig.pages.judged.getJudgedDataByPhones(arry,false)
                location.hash = "#!scopa/ticket";
            }
        }).showModal();
    },
    handleRemove:function(e,id,jtid,index){
        e.preventDefault();
        this.props.onPhoneRemove(id,jtid,index);
    },
    pageClick(page){
        this.props.onUserPage(page)
    },
    render: function () {
        let table = [],
            result = this.props.fileInfoAjaxResult,
            pageNo= 0,
            pageTotal=0,
            pageSize = 0;

        let self = this;
        const pagePernum = 20;//每页显示多少条
        const total = result.get('total');//总共多少条

        pageNo = result.get('pageNo');//当前请求的第几页
        pageTotal = Math.ceil(total / pagePernum);//一共有几页
        //分页参数
        let pages={
            current: pageNo,
            pageTotal:pageTotal
        }

        let listObj = result.get('listObj');

        if(listObj != undefined){
            listObj.forEach((item, index)=> {
                table.push(
                    <tr role="row" key={index}>
                        <td className="column-ellipsis column"><span>{ pageNo==1 ? index+1 : ( ((pageNo - 1) * pagePernum)+index+1 )}</span></td>
                        <td className="column-ellipsis column"><span>{item.get('fileName')}</span></td>
                        <td className="column-ellipsis column"><span>{item.get('recordNum')}</span></td>
                        <td className="column-ellipsis column"><span>{ Tools.convertDate2FormatStr(new Date(  item.get('importTime') ), 'yyyy-MM-dd hh:mm:ss') }</span></td>
                        <td className="column-ellipsis column"><span>{item.get('author')}</span></td>
                        <td className="column-ellipsis column"><span>{item.get('tag')}</span></td>
                        <td className="column-ellipsis column"><div><a href="javascript:;" className="sprite-i_operation" title="分析" onClick={e=>this.handleLocation(e,item.get('bjhm'))} data-logid="130" data-logcontent="协同作战--相关资料-话单分析的点击操作"></a><a href="javascript:;" className="sprite-i_delete" title="删除" onClick={e=>self.handleRemove( e, item.get('id'),item.get('jtid'),index)} data-logid="131" data-logcontent="协作-协同作战-相关资料删除操作"></a></div></td>
                    </tr>
                )
            })
        }

        //当前显示至几条
        if(pageNo == 1){
            pageSize = table.length
        }else{
            pageSize = (pageNo - 1) * pagePernum + table.length
        }

        return (
            <div className="relatedInfor">
                <div className="clearfix">
                    <h1 className="theme">研判主题：<span>{this.props.judgeThemeName}</span></h1>
                    <FileInfoComponentKeywordSearch
                        onhandleSearch={this.props.onhandleSearch}
                    />
                    <FileInfoComponentDaterange
                        onDateApply={this.props.onDateApply}
                    />
                </div>
                <div className="themeTable">
                    <table className="table table-striped table-bordered table-hover dataTable no-footer" id="themeTable">
                        <thead>
                            <tr role="row">
                                <th className="column-ellipsis column"  style={{width:'10%'}} aria-label="序列" >序列</th>
                                <th className="column-ellipsis column"  style={{width:'24%'}} aria-label="文件名称" >文件名称</th>
                                <th className="column-ellipsis column"  style={{width:'8%'}} aria-label="条目" >条目</th>
                                <th className="column-ellipsis column"  style={{width:'15%'}} aria-label="时间" >时间</th>
                                <th className="column-ellipsis column"  style={{width:'11%'}} aria-label="作者" >作者</th>
                                <th className="column-ellipsis column"  style={{width:'23%'}} aria-label="标签" >标签</th>
                                <th className="column-ellipsis column"  aria-label="操作" >操作</th>
                            </tr>
                        </thead>
                        <tbody className="text-center">
                            {table}
                        </tbody>
                    </table>
                    <div className="currentPage">当前显示至<em>{pageSize}</em>条，共<em>{total}</em>条</div>
                    <Paginator current={pages.current} total={pages.pageTotal} max={this.max} onPage={this.pageClick}></Paginator>
                </div>
            </div>
        );
    }
});


export default FileInforView;


