import {React, connect, PureRenderMixin,createSelector} from '../../common/Util';
import SelectView from './SelectView';
const SearchConditionView = React.createClass({

    render: function () {

    	let checkNodes = this.props.checkNodes;
        return (
			<div className="search_type mb">
				<a className="hd" href="javascript:;" onClick = {this.props.searchTypeShowClick}>
					<span>高级搜索设置</span>
					<i></i>
				</a>
				<SelectView
					tgIndex = {this.props.tgIndex}
					resultOnclick = {this.props.resultOnclick}
					attrChange = {this.props.attrChange}
					selectHtml = {this.props.selectHtml}
					selectNodes = {this.props.selectNodes}
					personObject = {this.props.personObject}
					vehicleObject = {this.props.vehicleObject}
					caseObject = {this.props.caseObject}
					attrData = {this.props.attrData}
					cbChange = {this.props.cbChange}
					selectData = {this.props.selectData}
				/>
			</div>
        );
    }
});

export default SearchConditionView;


