import {Tools} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const chooseTimeAnalyseReducer = (state = actionTypes.JUDGED_ANALYSE_CHOOSETIME, action)=> {
    switch (action.type) {
        case actionTypes.SET_JUDGED_ANALYSE_CHOOSETIME:
            return action.filter;
        default:
            return state;
    }
};


