import {React, connect, PureRenderMixin, createSelector} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';
import actionCreator from '../actions/actionCreator';

import Information from './InformationHomeView';
import Instructions from './InstructionsHomeView';
import Case from './CaseHomeView';
import People from './PeopleHomeView';
import Map from './MapHomeView';
import Links from './LinkHomeView';

const MainHomeView = React.createClass({
    mixins: [PureRenderMixin],
    componentDidMount: function () {
        this.mp = new BMap.Map("allmap",{minZoom:11,maxZoom:14,enableMapClick:false});
        let point=new BMap.Point(114.520828,38.048684);// 初始化地图,用城市名设置地图中心点
        this.mp.centerAndZoom(point,13);
        this.mp.enableScrollWheelZoom();

        let {dispatch} = this.props;
        //浮层
        dispatch(actionCreator.getMap());
        //热力图
        dispatch(actionCreator.getMapNew());
        dispatch(actionCreator.getHomeAuthority());

        scopaConfig.pages.home.drawHotMap = this.drawHotMap;
    },
    drawFloatDiv:function(bodyDatassssssssss){
        let that = this;
        function getNewData(datas, x, y,names) {
            function ComplexCustomOverlay(point) {
                this._point = point;
            }

            ComplexCustomOverlay.prototype = new BMap.Overlay();
            ComplexCustomOverlay.prototype.initialize = function (map) {
                this._map = map;
                var div = this._div = document.createElement("div");
                div.style.position = "absolute";
                div.style.height = "38px";
                div.style.zIndex = 10;

                var div_1 = this._div_1 = document.createElement("div");
                div_1.style.width = "41px";
                div_1.style.height = "18px"
                div_1.style.position = "absolute";
                div_1.style.left = "-7px";
                div_1.style.top = "38px";
                div_1.style.borderRadius = "4px";

                //var img = this.img = document.createElement("img");
                //img.src = "./../../images/cac.png";
                //div_1.appendChild(img);
                div.appendChild(div_1);

                var div_3 = this._div_3 = document.createElement("div");
                div_3.style.background = "rgba(255,255,255,0.8)";
                div_3.style.textAlign = "center";
                div_3.style.borderRadius = "3px";
                div_3.style.color = "#070707"
                div_3.style.width = "176px";
                //div_3.style.height = "190px"
                div_3.style.position = "absolute";
                div_3.style.right = "-97px";
                div_3.style.top = "57px";
                div_3.style.display = "none";
                div_3.style.border = "2px solid #529fc7"
                var h2 = this.h2 = document.createElement("h2");
                h2.textContent = names;
                h2.style.marginTop = "10px";
                h2.style.marginBottom = "5px";
                div_3.appendChild(h2);

                datas.forEach((item,index)=>{
                    let small = document.createElement("div");
                    small.style.display = "inline-block";
                    small.style.fontSize = "10px";
                    small.style.marginRight = "7px";
                    small.style.marginLeft = "8px";
                    small.textContent = item.get("ajlbmc")+" : "+item.get("cs");
                    div_3.appendChild(small);
                })
                div.appendChild(div_3);
                div_1.onmouseover = function(){
                    div_3.style.display = "block";
                    div.style.zIndex = 20;
                }

                div_1.onmouseout = function(){
                    div_3.style.display = "none";
                    div.style.zIndex = 10;
                }

                that.mp.getPanes().labelPane.appendChild(div);
                return div;
            }
            ComplexCustomOverlay.prototype.draw = function () {
                var map = this._map;
                var pixel = map.pointToOverlayPixel(this._point);
                this._div.style.left = pixel.x - 13 + "px";
                this._div.style.bottom = -pixel.y - 12 + "px";
            }
            let myCompOverlay = new ComplexCustomOverlay(new BMap.Point(x, y));
            that.mp.addOverlay(myCompOverlay);
        }

        let data = bodyDatassssssssss.get("data");
        //画浮层数据
        data.forEach((item, index)=> {
            let area = item.get("area");
            if(scopaConfig.mapArea.get(area)){
                let position =  scopaConfig.mapArea.get(area).get('position');
                let lng = position.get(0);
                let lat = position.get(1);
                let citys = scopaConfig.mapArea.get(area).get('citys').slice(7);
                if(citys == "鹿泉市"){
                    citys = "鹿泉区";
                }
                if(citys == "栾城县"){
                    citys = "栾城区";
                }
                if(citys == "藁城市"){
                    citys = "藁城区";
                }
                getNewData(item.get("data"), lng, lat,citys);
            }
        });
    },
    drawHotMap:function(){
        if(!this.props.home_maps.get("data")){
            return;
        }

        //热力图
        let hotNum = 0;
        function hotMap(datas,x,y){
            let count = 0;
            datas.forEach((item2,index2)=>{
                count += item2.get("cs");
            })
            hot.push({"lng":x,"lat":y,"count":count});
            if(count > hotNum){
                hotNum = count;
            }
        }

        let mapData = this.props.home_maps.get("data");
        let hot = [];

        //画热力图
        mapData.forEach((item, index)=> {
            let area = item.get("area");
            let keys = scopaConfig.newMapArea.keySeq();
            for(var i=0;i<keys.size;i++){
                let policeAddress = keys.get(i);
                let aPo = policeAddress.split(',');
                if(area.includes(aPo[0]) && area.includes(aPo[1])){
                    if(aPo[0]==aPo[1]){
                        if(area.endsWith(aPo[0])){
                            hotMap(item.get("data"), scopaConfig.newMapArea.get(policeAddress).get('postion').get(0), scopaConfig.newMapArea.get(policeAddress).get('postion').get(1));
                        }
                    }else{
                        hotMap(item.get("data"), scopaConfig.newMapArea.get(policeAddress).get('postion').get(0), scopaConfig.newMapArea.get(policeAddress).get('postion').get(1));
                    }

                    break;
                }
            }
        })

        let heatmapOverlay = new BMapLib.HeatmapOverlay({"radius":40});
        this.mp.addOverlay(heatmapOverlay);
        heatmapOverlay.setDataSet({data:hot,max:hotNum});
    },
    render: function () {
        let {bodyData, bodyDatass,bodyDatassss,bodyDatassssssss,bodyDatassssssssss,Data_person,home_authority,home_maps} = this.props;
        let personArr = [];
        Data_person.forEach((item, index)=> {
            if(item.get("PERSON_zdrlx")){
                personArr.push(item.get("PERSON_zdrlx"));
            }else{
                personArr.push("");
            }
        })

        return (
            <div className="home_view">
                <div className="chunk">
                    <Information bodyData={bodyData}/>
                    <Map bodyDatassssssssss={bodyDatassssssssss} home_maps={home_maps} drawFloatDiv={this.drawFloatDiv} drawHotMap={this.drawHotMap} />
                </div>
                <div className = "chunk">
                    <People bodyDatassssssss = {bodyDatassssssss} personArr = {personArr}/>
                    <div className = "center_left">
                        <Case bodyDatassss = {bodyDatassss} home_authority={home_authority}/>
                        <Instructions bodyDatass={bodyDatass} />
                    </div>
                </div>
                <div className="chunk1">
                    <Links/>
                </div>
            </div>
        );
    }
});

const home = state => {return state.home_bodyData};
const home1 = state => {return state.home_bodyDatass};
const home2 = state => {return state.home_bodyDatassss};
const home4 = state => {return state.home_bodyDatassssssss};
const home5 = state => {return state.home_bodyDatassssssssss};
const home6 = state => {return state.home_Data_person};
const home_authority = state => {return state.home_authority};
const home_maps = state => {return state.home_maps};

const getHome = createSelector([home,home1,home2,home4,home5,home6,home_authority,home_maps], (home,home1,home2,home4,home5,home6,home_authority,home_maps)=> {
    return {
        bodyData: home,
        bodyDatass:home1,
        bodyDatassss:home2,
        bodyDatassssssss:home4,
        bodyDatassssssssss:home5,
        Data_person:home6,
        home_authority:home_authority,
        home_maps:home_maps
    }
});

export default connect(getHome)(MainHomeView);



