import {React, connect, PureRenderMixin,createSelector} from '../../common/Util';
import actionCreator from '../actions/actionCreator';
import SelectView from './SelectView';

const ResultContent = React.createClass({
    mixins: [PureRenderMixin],
	getInitialState:function(){
    	return {

		}
	},

    render: function () {

        function createMarkup(data) { return {__html: data}; };

	    let {dispatch,resultContent} = this.props;
        let attrNodes = [];
        if(resultContent){
            for(var index in resultContent){
                let attrJson = {};
                attrJson = (
                    <i key = {index} dangerouslySetInnerHTML = {createMarkup(resultContent[index].value)} />
                );
                attrNodes.push(attrJson);
            }
        }
        return (
            <p className="cont" >
                {attrNodes}
            </p>
        );
		
    }
});

export default ResultContent;


