import {storeCreateByReducer, combineReducers} from './common/UtilOld';
//模块js
import Main from './main/MainOld';
import Home from './home/Home';
import Judged from './judged/Judged';
import Cooperation from './cooperation/Cooperation';
import {reducers} from './main/reducers'
import {config} from './common/config';
import {Immutable} from './common/UtilOld';

let store = storeCreateByReducer(combineReducers(reducers));
let oHome = new Home(store);
let oJudged = new Judged(store);
let oCooperation = new Cooperation(store);

window.scopaConfig = {
    "url": "/",
    "pages": {
        "index": oHome,
        "home": oHome,
        "judged": oJudged,
        "cooperation": oCooperation
    },
    "errors": {},
    "mapArea": Immutable.fromJS(config.mapArea),
    "newMapArea": Immutable.fromJS(config.newMapArea),
    "linkArr": Immutable.fromJS(config.linkArr)
};

new Main().init();


