import * as actionTypes from '../actions/actionTypes';

export const listShowCountReducer = (state = actionTypes.JUDGED_TABLE_SHOWCOUNT, action)=> {
    switch (action.type) {
        case actionTypes.SET_JUDGED_LIST_SHOWCOUNT:
            return action.count;
        default:
            return state;
    }
};


