import {React, PureRenderMixin} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';
import StatisticsView from './StatisticsView';
import AnalyseStripView from './AnalyseStripView';
import AnalyseParallelView from './AnalyseParallelView';
import ToolbarView from './ToolbarView';
import TicketNormalTableView from './TicketNormalTableView';
import TicketMergeTableView from './TicketMergeTableView';
import DateView from './DateView';

const TicketView = React.createClass({
    mixins: [PureRenderMixin],
    changeRightTitleActive: function (tabName) {
        //切换右侧标题选中状态
        this.props.changeRightTitleActive(tabName);
    },
    render: function () {
        //右侧标题栏激活状态
        let titleRightActive = this.props.titleActive;
        let merge = this.props.merge;
        let statisticsCls = '';
        if (titleRightActive == actionTypes.JUDGED_RIGHT_TITLE_ACTIVE.STATISTICS) {
            statisticsCls += "active";
        }
        //禁用点击
        if (merge) {
            statisticsCls += "disabled";
        }

        return (
            <div>
                <div className="pull-right">
                    <div className="righttitle">
                        <div className="pull-left"><a href="javascript:;"
                                                      className={statisticsCls} data-logid="213" data-logcontent="话单--页面右侧统计"
                                                      onClick={()=>!merge && this.changeRightTitleActive(actionTypes.JUDGED_RIGHT_TITLE_ACTIVE.STATISTICS)}>统计</a>
                        </div>
                        <div className="pull-left"><a href="javascript:;" data-logid="214" data-logcontent="话单--页面右侧分析"
                                                      className={titleRightActive == actionTypes.JUDGED_RIGHT_TITLE_ACTIVE.ANALYSE ? "active" : ""}
                                                      onClick={()=>this.changeRightTitleActive(actionTypes.JUDGED_RIGHT_TITLE_ACTIVE.ANALYSE)}>分析</a>
                        </div>
                    </div>
                    <div className="rightcontent">
                        <div
                            className={titleRightActive == actionTypes.JUDGED_RIGHT_TITLE_ACTIVE.STATISTICS ? "statisticscontent" : "statisticscontent hide"}>
                            {
                                !this.props.merge ? <StatisticsView
                                    list={this.props.originalList}
                                    bjhmFilter={this.props.bjhmStatisticFilter}
                                    dfhmFilter={this.props.dfhmStatisticFilter}
                                    xqhFilter={this.props.xqhStatisticFilter}
                                    jzhFilter={this.props.jzhStatisticFilter}
                                    changeStatisticBjhmFilter={this.props.changeStatisticBjhmFilter}
                                    changeStatisticDfhmFilter={this.props.changeStatisticDfhmFilter}
                                    changeStatisticXqhFilter={this.props.changeStatisticXqhFilter}
                                    changeStatisticJzhFilter={this.props.changeStatisticJzhFilter}
                                /> : null
                            }
                        </div>
                        <div
                            className={titleRightActive == actionTypes.JUDGED_RIGHT_TITLE_ACTIVE.ANALYSE ? "analysecontent" : "analysecontent hide"}>
                            {!this.props.merge ? <AnalyseStripView
                                list={this.props.originalList}
                                bjhmAnalyseFilter={this.props.bjhmAnalyseFilter}
                                chooseTypeAnalyseFilter={this.props.chooseTypeAnalyseFilter}
                                chooseTimeAnalyseFilter={this.props.chooseTimeAnalyseFilter}
                                chooseTimeSpanAnalyseFilter={this.props.chooseTimeSpanAnalyseFilter}
                                dfhmAnalyseFilter={this.props.dfhmAnalyseFilter}
                                changeBjhmAnalyseFilter={this.props.changeBjhmAnalyseFilter}
                                changeChooseTypeAnalyseFilter={this.props.changeChooseTypeAnalyseFilter}
                                changeChooseTimeAnalyseFilter={this.props.changeChooseTimeAnalyseFilter}
                                changeChooseTimeSpanAnalyseFilter={this.props.changeChooseTimeSpanAnalyseFilter}
                                changeAnalyseDfhmFilter={this.props.changeAnalyseDfhmFilter}
                            /> : <AnalyseParallelView
                                list={this.props.list}
                                bjhmMergeAnalyseFilter={this.props.bjhmMergeAnalyseFilter}
                                changeBjhmMergeAnalyseFilter={this.props.changeBjhmMergeAnalyseFilter}
                                changeDfhmByPolyline={this.props.changeDfhmByPolyline}
                            />}
                        </div>
                    </div>
                </div>
                <div className="ticketmain">
                    <ToolbarView
                        list={this.props.originalList}
                        changeStepForward={this.props.changeStepForward}
                        changeStepBack={this.props.changeStepBack}
                        merge={this.props.merge}
                        changeMerge={this.props.changeMerge}
                        checkedToTop={this.props.checkedToTop}
                        setSingleChecked={this.props.setSingleChecked}
                        setHighlightChecked={this.props.setHighlightChecked}
                        setCheckedToTop={this.props.setCheckedToTop}
                        delChecked={this.props.delChecked}
                        delReverseChecked={this.props.delReverseChecked}
                        getMachinePhoneData={this.props.getMachinePhoneData}
                        gotoGraph={this.props.gotoGraph}
                        gotoMapping={this.props.gotoMapping}
                    />
                    {
                        this.props.merge ?
                            <TicketMergeTableView
                                list={this.props.list}
                                setSingleChecked={this.props.setSingleChecked}
                                setCheckedAll={this.props.setCheckedAll}
                                thschSort={this.props.thschSort}
                                thcshSort={this.props.thcshSort}
                                thcshSensitiveSort={this.props.thcshSensitiveSort}
                                changeThschSort={this.props.changeThschSort}
                                changeThcshSort={this.props.changeThcshSort}
                                changeThcshSensitiveSort={this.props.changeThcshSensitiveSort}
                                dfhmByPolyline={this.props.dfhmByPolyline}
                            /> : <TicketNormalTableView
                            list={this.props.list}
                            step={this.props.step}
                            merge={this.props.merge}
                            setSingleChecked={this.props.setSingleChecked}
                            setCheckedAll={this.props.setCheckedAll}
                            kssjSort={this.props.kssjSort}
                            thscSort={this.props.thscSort}
                            changeKssjSort={this.props.changeKssjSort}
                            changeThscSort={this.props.changeThscSort}
                            changeShowListCount={this.props.changeShowListCount}
                            listShowCount={this.props.listShowCount}
                        />
                    }
                    {!this.props.merge ? <DateView
                        periodActive={this.props.periodActive}
                        periodDateFilter={this.props.periodDateFilter}
                        changePeriod={this.props.changePeriod}
                        changePeriodDateFilter={this.props.changePeriodDateFilter}
                        list={this.props.list}/> : null}
                </div>
            </div>
        )
    }
});

export default TicketView;


