import {
    $,
    Tools,
} from '../common/Util';
import RouterConfig from './RouterConfig';

class Main {
    init() {
        //this.gotoPage();
        this.event();
    }

    closeLoading() {
        // mining.utils.modalLoading('close');
        // var loadingInstance = dialog.getCurrent();
        // if (loadingInstance && loadingInstance.id == "loadingDiv") {
        //     loadingInstance.close().remove();
        // }
    }

    event() {
        this.globalEvent();
    }

    globalEvent() {
        //全局控制
        $(document).on('click.logid', function (e) {
            let target = e.target;
            if (target.dataset && target.dataset.logid) {
                mining.utils.serverLog(target.dataset.logid, target.dataset.logcontent);
            }
        }).on('contextmenu', function () {

        });

        //窗口尺寸变化时执行reload
        $(window).on('resize', function () {

        });

        //全局loading画面及错误处理
        $(document).ajaxSend((event, XMLHttpRequest, ajaxOptions) => {
            // mining.utils.modalLoading();
            //如果没有loading画面，一个界面只有一个loading动画时
            // var loadingInstance = dialog.getCurrent();
            // if (loadingInstance == null || loadingInstance && loadingInstance.id != "loadingDiv") {
            //     dialog.loading();
            // }
        }).ajaxSuccess((event, XMLHttpRequest, ajaxOptions) => {
            var oRes = JSON.parse(XMLHttpRequest.responseText);

            //统一处理错误提示
            if (oRes.statusCode != 200) {
                //特殊处理登录页
                if (oRes.statusCode == 301) {
                    // mining.utils.loadLogin({
                    //     message: '获取用户信息失败，请重新登陆'
                    // });

                    location.replace(location.origin);
                    return;
                }

                //关闭Loading画面
                this.closeLoading();
                if (!ajaxOptions.url.endsWith('/pass/login') && !ajaxOptions.url.endsWith('pass/getUserInfo')) {
                    console.log(ajaxOptions.url + ',错误信息：' + oRes.message);
                }
            }
        }).ajaxError((event, request, settings) => {
            console.log(settings.url + '当前接口不通！');
            // 关闭Loading画面
            this.closeLoading();
            // dialog.alert(settings.url + '当前接口不通！');
        }).ajaxStop(() => {
            // console.log('stop');
            this.closeLoading();
        });
    }

    gotoPage() {
        let sPathName = Tools.getPathname().replace('/', '').replace(new RegExp(".html$"), '');
        this.sHTMLName = sPathName ? sPathName : 'index';
        scopaConfig.pages[this.sHTMLName].init();
    }
}

export default Main;



