import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const judgedListReducer = (state = Immutable.fromJS([]), action)=> {
    switch (action.type) {
        case actionTypes.SET_JUDGED_ALL:
            if (action.isClear) {
                state = Immutable.fromJS(action.list);
            } else {
                state = state.concat(Immutable.fromJS(action.list));
            }

            return state.map(item=> {
                if (item.get('checked') === undefined) {
                    item = item.set('checked', false);
                }

                //有新数据进来，清空所有高亮
                item = item.set('highlight', false);

                return item;
            });
        case actionTypes.SET_JUDGED_SINGLE_CHECKED:
            //设置单条记录的是否选中
            return state.map(oTicket=> {

                if (action.ids.includes(oTicket.get('id'))) {
                    oTicket = oTicket.set('checked', !oTicket.get('checked'));
                }
                return oTicket;
            });
        case actionTypes.SET_JUDGED_CHECKED_ALL:
            //设置全选
            return state.map(oTicket=> {
                return oTicket.set('checked', action.checked);
            });
        case actionTypes.SET_JUDGED_CHECKED_TOTOP:
            //设置选中置顶
            return state.sort(item=>!item.get('checked'));
        case actionTypes.DEL_JUDGED_CHECKED:
            //删除checked元素
            return state.filter(item=>!item.get('checked'));
        case actionTypes.DEL_JUDGED_CHECKED_REVERSE:
            //反向删除checked元素
            return state.filter(item=>item.get('checked'));
        default:
            return state;
    }
};


