import {Immutable} from '../../common/Util';
import * as actionTypes from '../actions/actionTypes';

export const dfhmByPolylineReducer = (state = Immutable.List(), action)=> {
    switch (action.type) {
        case actionTypes.SET_JUDGED_DFHM_POLYLINE:
            return action.aData;
        default:
            return state;
    }
};


