import { React,dialog,PureRenderMixin } from '../../common/Util';
import InformationSingleView  from './InformationSingleView';

const Information = React.createClass({
    mixins:[PureRenderMixin],
    Information_down_onClick:function(e){
        let wid = -(e.target.id-11)*37;
        let Information = document.getElementsByClassName("Information")[0];
        let Information_down = Information.getElementsByClassName("Information_down")[0];
        if (Information.style.overflow=="inherit") {
            Information.style.overflow="hidden";
            Information_down.style.bottom="0px";
            Information_down.style.background="url('./../../images/down.png')no-repeat left 247px top 10px rgba(255,255,255,0.8)";
        }else{
            Information.style.overflow="inherit";
            Information_down.style.bottom=wid+"px";
            Information_down.style.background="url('./../../images/up.png')no-repeat left 247px top 10px #fff";
            Information_down.style.borderBottom="1px solid #dee3eb";
        }
    },
    componentDidMount: function () {

    },
    render: function () {
        let otr = [];
        let bodyData = this.props.bodyData;
        let len = 0;
        bodyData.forEach((item, index)=> {
            len++;
            otr.push(
                <InformationSingleView id={index}
                    key = {index}
                    infor = {item}
                />
            )
        })
        if(len<=12){
            return (
                <div className = "Information" style={{overflow:"hidden"}}>
                    <div className = "top_bar"><span>内生情报</span></div>
                    <table className = "all_table table_Information">
                        <thead><tr><td style={{width:"24%",paddingLeft:"20px"}}>情报来源</td><td style={{width:"46%"}}>信息描述</td><td style={{width:"30%"}}>发现时间</td></tr></thead>
                        <tbody>
                            {otr}
                        </tbody>
                    </table>
                </div>
            );
        }else{
            return (
                <div className = "Information" style={{overflow:"hidden"}}>
                    <div className = "top_bar"><span>内生情报</span></div>
                    <table className = "all_table table_Information">
                        <thead><tr><td style={{width:"24%",paddingLeft:"20px"}}>情报来源</td><td style={{width:"46%"}}>信息描述</td><td style={{width:"30%"}}>发现时间</td></tr></thead>
                        <tbody>
                            {otr}
                        </tbody>
                    </table>
                    <div className="Information_down hand" id={len} onClick={this.Information_down_onClick}  data-logid="102" data-logcontent="内生情报-下拉" style={{background:"url('./../../images/down.png') no-repeat left 247px top 10px rgba(255,255,255,0.8)"}}></div>
                </div>
            );
        }

    }
});

export default Information;


