import {React, connect, PureRenderMixin,createSelector,$} from '../../common/Util';

import actionCreator from '../actions/actionCreator';
import SnapshotTop from './SnapshotTop';
import SnapshotTable from './SnapshotTable';


require('moment');
require('../../plugins/daterangepicker');

const SnapshotView = React.createClass({
    mixins: [PureRenderMixin],
    getInitialState: function () {//初始化数据
        return {
            type: "1",
            keyword: "",
            startTime: "",
            endTime: "",
            pageNo: 1,
            pageSize: 6,
        }
    },
    themeOnChange: function (e) {//快照——主题下拉列表选择
        let {dispatch} = this.props;
        let judgeThemeId = this.props.judgeThemeId;
        let user_id = this.props.user_id;
        let {type,keyword,startTime,endTime,pageNo,pageSize} = this.state;
        type = e.target.value;
        this.setState({type});

        pageNo = 1 ;
        this.setState({pageNo});

        dispatch(actionCreator.getSnapshotList(user_id,judgeThemeId,type,keyword,startTime,endTime,pageNo,pageSize));
    },
    keyOnChange: function (e) {//快照——关键字筛选
        let {dispatch} = this.props;
        let judgeThemeId = this.props.judgeThemeId;
        let user_id = this.props.user_id;
        let {type,keyword,startTime,endTime,pageNo,pageSize} = this.state;
        pageNo = 1 ;
        this.setState({pageNo});

        if(e.currentTarget.tagName === 'A'){
            dispatch(actionCreator.getSnapshotList(user_id,judgeThemeId,type,keyword,startTime,endTime,pageNo,pageSize));
        }
        if(e.currentTarget.tagName === 'INPUT' && e.keyCode === 13){
            dispatch(actionCreator.getSnapshotList(user_id,judgeThemeId,type,keyword,startTime,endTime,pageNo,pageSize));
        }
    },
    changes:function(e){
        let keyword = this.state.keyword;
        keyword = e.target.value
        this.setState({keyword});
    },
    timeOnChange: function (start, end) {//快照——时间筛选
        let {dispatch} = this.props;
        let judgeThemeId = this.props.judgeThemeId;
        let user_id = this.props.user_id;
        let {type,keyword,startTime,endTime,pageNo,pageSize}=this.state;
        pageNo = 1 ;
        this.setState({pageNo});
        if( $(event.target).hasClass('clearBtn')){
            if(startTime != "" && endTime != ""){
                startTime = "";
                endTime = "";
                dispatch(actionCreator.getSnapshotList(user_id,judgeThemeId,type,keyword,startTime,endTime,pageNo,pageSize));
            }
        }else{
            startTime = start.format('YYYY-MM-DD HH:MM:SS');
            endTime = end.format('YYYY-MM-DD HH:MM:SS');
            dispatch(actionCreator.getSnapshotList(user_id,judgeThemeId,type,keyword,startTime,endTime,pageNo,pageSize));
        }
        this.setState({startTime});
        this.setState({endTime});


    },
    photoPageClick:function(page){//快照——分页
        let {dispatch,judgeThemeId,user_id} = this.props;
        let {type,keyword,startTime,endTime,pageNo,pageSize}=this.state;
        pageNo = page;
        this.setState({pageNo});
        dispatch(actionCreator.getSnapshotList(user_id,judgeThemeId,type,keyword,startTime,endTime,pageNo,pageSize));
    },
    fenxiangOnclick:function(e){//快照——分享切换
        let {dispatch} = this.props;
        let index = e.target.id;
        let status = e.target.dataset.titles;
        let sid = e.target.dataset.attrcation;
        if(status==0){
            status=1
            dispatch(actionCreator.getQuxiaoFenxiangAttention(sid));
        }else{
            status=0
            dispatch(actionCreator.getFenxiangAttention(sid));
        }
        dispatch(actionCreator.updataFenxiang(status,index));
    },
    photosAbord:function(){
        let {dispatch} = this.props;
        let judgeThemeId = this.props.judgeThemeId;
        let user_id = this.props.user_id;
        let {type,keyword,startTime,endTime,pageNo,pageSize} = this.state;
        dispatch(actionCreator.getSnapshotList(user_id,judgeThemeId,type,keyword,startTime,endTime,pageNo,pageSize));
    },
    delectedPhoto:function(e){//快照——删除快照
        let {dispatch} = this.props;
        let sid = e.target.dataset.titles;
        let judgeThemeId = this.props.judgeThemeId;
        let user_id = this.props.user_id;
        let {type,keyword,startTime,endTime,pageNo,pageSize} = this.state;
        dialog.confirm({
            cancel: function(){},
            ok:function(){
                dispatch(actionCreator.getDelectAttention(sid,user_id,judgeThemeId,type,keyword,startTime,endTime,pageNo,pageSize));
            }
        }).showModal();
    },
    componentDidMount: function () {
        let {dispatch} = this.props;
        let judgeThemeId = this.props.judgeThemeId;
        let user_id = this.props.user_id;
        let {type,keyword,startTime,endTime,pageNo,pageSize}=this.state;
        dispatch(actionCreator.getSnapshotList(user_id,judgeThemeId,type,keyword,startTime,endTime,pageNo,pageSize));
        //快照对外接口
        scopaConfig.pages.cooperation.getCooperationSnapshotByPhotos = this.getCooperationSnapshotByPhotos;
        scopaConfig.pages.cooperation.photosAbord = this.photosAbord;

    },
    getCooperationSnapshotByPhotos:function(uId,jId){
        let {dispatch} = this.props;
        dispatch(actionCreator.getSnapshotList(uId,jId,"1","","","",1,6));
    },
    render: function () {
        let {cooperation_snapshotList,cooperation_snapshotNum} = this.props;
        let giveOnclick = this.props.giveOnclick;

        return (
            <div className="snapshot">
                <SnapshotTop
                    judgeThemeName={this.props.judgeThemeName}
                    themeOnChange={this.themeOnChange}
                    keyOnChange={this.keyOnChange}
                    timeOnChange={this.timeOnChange}
                    changes={this.changes}
                />
                <SnapshotTable
                    giveOnclick={giveOnclick}
                    snapshotAjaxResult={cooperation_snapshotList}
                    cooperation_snapshotNum={cooperation_snapshotNum}
                    photoPageClick={this.photoPageClick}
                    pageNo={this.state.pageNo}
                    user_id={this.props.user_id}
                    fenxiangOnclick={this.fenxiangOnclick}
                    delectedPhoto={this.delectedPhoto}
                    types={this.state.type}
                />
            </div>
        );
    }
});

const cooperation_snapshotList = state => {
    return state.cooperation_snapshotList;
};
const cooperation_snapshotNum = state => {
    return state.cooperation_snapshotNum;
};
const getCooperation = createSelector([cooperation_snapshotList,cooperation_snapshotNum], (cooperation_snapshotList,cooperation_snapshotNum)=> {
    return {
        cooperation_snapshotList: cooperation_snapshotList,
        cooperation_snapshotNum:cooperation_snapshotNum
    }
});

export default connect(getCooperation)(SnapshotView);


