import { React,dialog,PureRenderMixin } from '../../common/Util';
import InstructionsSingleView  from './InstructionsSingleView';

const Instructions = React.createClass({
    mixins:[PureRenderMixin],
    componentDidMount: function () {

    },
	typeOnclick:function(e){
		mining.utils.hashChange("event");
		window.showEventList = true;
		window.showEventDetailsId = "";
	},
	slingOnClick:function(e){
		mining.utils.hashChange("event");
		window.showEventDetailsId = e.target.id;
		window.showEventList = "";
	},
    render: function () {
		let otr = [];
		let bodyDatass = this.props.bodyDatass;

		bodyDatass.forEach((item, index)=> {
			otr.push(
				<InstructionsSingleView
					click={this.slingOnClick}
					id={index}
					key = {index}
					infor = {item}
				/>
			)
		})

        return (
            <div className = "Instructions">
            	<div className = "top_bar"><span>重大事件情报</span><a onClick={this.typeOnclick} className = "arrow hand scopaicon-gengxuoxiangyou"  data-logid="100" data-logcontent="重大事件情报-更多"></a></div>
            	<table className = "all_table table_Instructions">
            		<thead><tr><td style={{width:"50%",paddingLeft:"20px"}}>指令内容</td><td style={{width:"50%"}}>指令时间</td></tr></thead>
            		<tbody>
					 	{otr}
            		</tbody>
            	</table>
            </div>
        );
    }
});

export default Instructions;


