import * as actionTypes from './actionTypes';
import HomeService from '../service/HomeService';
import {dialog,Immutable} from '../../common/Util';

const actionCreator = {
    getInformation: function (applicantId) {
        return dispatch=> {
            HomeService.getInformation(applicantId).done(oData=> {
                if (oData.statusCode == 200) {
                    dispatch(this.setInformation(oData.listObj));
                }
            })
        }
    },
    setInformation: function (bodyData) {
        return {
            type: actionTypes.SET_HOME_INFORMATION,
            bodyData
        }
    },
    getInstruction: function (applicantId) {
        return dispatch=> {
            let oResult = null;
            let that = this;
            function _getIns(){
                HomeService.getInstruction(applicantId).done(oData=> {
                    if (oData.statusCode == 200) {
                        if(oResult==null||oResult!=oData){
                            oResult = oData;
                            dispatch(that.setInstruction(oData.listObj));
                        }
                    }
                    setTimeout(_getIns, 170000);
                })
            }
            _getIns();
        }
    },
    setInstruction: function (bodyDatass) {
        return {
            type: actionTypes.SET_HOME_INSTRUCTION,
            bodyDatass
        }
    },
    getCase: function (userId) {
        return dispatch=> {
            let oResult = null;
            let that = this;
            function _getData(){
                HomeService.getCase(userId).done(oData=> {
                    if (oData.statusCode == 200) {
                        //if(oResult==null||Immutable.fromJS(oResult).equals(Immutable.fromJS(oData))){
                        //    oResult = oData;
                        //    dispatch(this.setCase(oData.bodyData));
                        //}
                        if(oResult==null||oResult!=oData){
                            oResult = oData;
                            dispatch(that.setCase(oData.bodyData));
                        }
                    }
                    setTimeout(_getData, 180010);
                });
            }
            _getData();
        }
    },
    setCase: function (bodyDatassss) {
        return {
            type: actionTypes.SET_HOME_CASE,
            bodyDatassss
        }
    },
    getPeople: function (applicantId) {

        let oResult = null;
        let that = this;
        return dispatch=> {
            function _getpeo(){
                let sfzhStr = "";
                HomeService.getPeople(applicantId).done(oData=> {
                    if (oData.statusCode == 200) {
                        if(oData.listObj.length > 0){
                            if(oResult==null||oResult!=oData){
                                oResult = oData;
                                dispatch(that.setPeople(oData.listObj));
                                oData.listObj.forEach((item, index)=> {
                                    sfzhStr = sfzhStr+item["sfzh"]+",";
                                })
                                if(sfzhStr.length>1){
                                    dispatch(that.getPerson(sfzhStr.substr(0,sfzhStr.length-1)));
                                }
                            }
                        }
                    }
                    setTimeout(_getpeo, 180000);
                })
            }
            _getpeo();
        }
    },
    setPeople: function (bodyDatassssssss) {
        return {
            type: actionTypes.SET_HOME_PEOPLE,
            bodyDatassssssss
        }
    },
    getPerson: function (sfzh) {
        return dispatch=> {
                HomeService.getPerson(sfzh).done(oData=> {
                    if (oData.v) {
                        dispatch(this.setPerson(oData.v));
                    }
                })
        }
    },
    setPerson: function (Data_person) {
        return {
            type: actionTypes.SET_HOME_PERSON,
            Data_person
        }
    },
    getMap: function () {
        return dispatch=> {
            //浮层
            HomeService.getMap().done(oData=> {
                if (oData.statusCode == 200) {
                    if(oData.obj["data"].length != 0){
                        dispatch(this.setMap(oData.obj));
                    }
                }
            })
        }
    },
    setMap: function (bodyDatassssssssss) {
        return {
            type: actionTypes.SET_HOME_MAP,
            bodyDatassssssssss
        }
    },
    getMapNew: function () {
        //热力图
        return dispatch=> {
            HomeService.getMapNew().done(oData=> {
                if (oData.statusCode == 200) {
                    if(oData.obj["data"].length != 0){
                        dispatch(this.setMapNew(oData.obj));
                    }
                }
            })
        }
    },
    setMapNew: function (home_maps) {
        return {
            type: actionTypes.SET_HOME_NEW_MAP,
            home_maps
        }
    },
    //获取用户id
    getHomeAuthority: function () {
        return dispatch=> {
            HomeService.getHomeAuthority().done(oData=> {
                if (oData.statusCode == 200) {
                    dispatch(this.setHomeAuthority(oData.obj["user_id"]));
                    dispatch(this.getInformation(oData.obj["user_id"]));
                    dispatch(this.getInstruction(oData.obj["user_id"]));
                    dispatch(this.getCase(oData.obj["user_id"]));
                    dispatch(this.getPeople(oData.obj["user_id"]));
                }
            })
        }
    },
    setHomeAuthority: function (Authoritys) {
        return {
            type: actionTypes.GET_HOME_AUTHORITY,
            Authoritys
        }
    }

};
export default actionCreator;



