import {React, connect, PureRenderMixin,createSelector} from '../../common/Util';
import actionCreator from '../actions/actionCreator';

const TypeRetrievalView = React.createClass({
    mixins: [PureRenderMixin],
    componentDidMount: function () {

    },


    render: function () {

		let select_type_attr = [
			{"type":"1","name":"全部"},
			{"type":"2","name":"实体"},
			{"type":"3","name":"文本"},
		];

		let typeNodes = null;
		if(select_type_attr){
			typeNodes = select_type_attr.map((event,index)=>(
				<a onClick={this.props.typeCheckOnclick} key={index} name={event.name}>
					<span className="seacr_icons"></span>
					<b>{event.name}</b>
				</a>
			));
		}

        return (
            <div className="search_keyword mb">
				{typeNodes}
			</div>
        );
    }
});


export default TypeRetrievalView;


